Imgur.Flash = React.createClass({
    displayName: 'Flash',

    getDefaultProps: function() {
        return {
            delay: 1000
        };
    },

    getInitialState: function() {
        return {
            visible: false
        };
    },

    componentWillReceiveProps: function(next) {
        if(next.start !== this.props.start || next.delay !== this.props.delay) {
            // Reset the timer if the children are changed

            this._installTimer(next.start);

            this.setState({
                visible: true
            });
        }
    },

    _getDuration: function(start) {
        return Math.max(0, start + this.props.delay - (+new Date()));
    },

    componentDidMount: function() {
        if(this._getDuration(this.props.start) > 0) {
            this._installTimer(this.props.start);
            this.setState({
                visible: true
            });
        }
    },

    _installTimer: function(start) {
        if(this._timer !== null) {
            window.clearTimeout(this._timerRef);
            this._timerRef = null;
        }

        var duration = this._getDuration(start);
        this._timerRef = window.setTimeout(_.bind(this._handleTimerExpired, this), duration);
    },

    _handleTimerExpired: function() {
        this.setState({
            visible: false
        });

        this._timerRef = null;
    },

    render: function() {
        return React.createElement(React.addons.CSSTransitionGroup, {transitionName: 'flash'},
            this.state.visible ? React.DOM.div(null, this.props.children) : null
        );
    }
});
