    // cleans up the GET parms and decides if a search is simply simple (q=)
    searchAdvancedHandleSubmit : function(e) {
        e.preventDefault();
        var loc = document.location,
            $advInputs = this._.el.search.$advancedForm.find('input'),
            $tmp,
            tmpName,
            tmpVal,
            getVars = [],
            qAllOnly = true,
            qVal = this._.el.search.$advancedAllInput.val(),
            len = $advInputs.length,
            iter,
            destination = loc.protocol+'//'+loc.host
            skip = {'qs':1, 'q_all':1};

        if(loc.port.length) {
            destination += ':'+loc.port;
        }

        destination += '/search/'+this._.sort+'/'+this._.window;

        if(len) {
            for(iter = 0; iter < len; iter++) {
                $tmp       = $($advInputs[iter]);
                tmpName    = $($advInputs[iter]).attr('name');
                tmpVal     = String($($advInputs[iter]).val());

                if(tmpName in skip) {
                    continue;
                }

                switch($tmp.attr('type')) {
                    case 'hidden':
                        if(tmpVal != $($advInputs[iter]).attr('data-default')) {
                            getVars.push(tmpName+'='+encodeURIComponent(tmpVal));
                            qAllOnly = false;
                        }
                        break;
                    case 'text':
                        if(tmpVal.length) {
                            getVars.push(tmpName+'='+encodeURIComponent(tmpVal));
                            qAllOnly = false;
                        }
                        break;
                    case 'checkbox':
                        if($tmp.is(':checked')) {
                            getVars.push(tmpName+'=on');
                        }
                        break;
                }
            }
            if(qAllOnly) {
                getVars = ['q='+encodeURIComponent(qVal)];
            } else {
                getVars.push('q_all='+encodeURIComponent(qVal));
            }
            if(getVars.length) {
                destination += '?'+getVars.join('&');
            }

            // finall do it!
            document.location = destination;
        }
    },

    searchAdvancedToggleHandler : function(e) {
        var self = this;
        var $target = $(e.target);

        if(this._.el.search.$advanced.is(':visible')) {
            this._.el.search.$advanced.fadeOut(150, function() { self.syncSearchSimpleAndAdvanced(); });
            $(e.target).attr('title', 'show advanced search');
        } else {
            this._.el.search.$advanced.fadeIn(150, function() { self.syncSearchSimpleAndAdvanced(); });
            $(e.target).attr('title', 'hide advanced search');
        }

        $target.parent().toggleClass('active');
        $target.tipsy('hide');
    },

    searchAdvancedToggleSizeType : function (e) {
        if(this._.el.search.$sizeToggle.is(':checked')) {
            // mpx requested
            this._.el.search.$pxCombo.hide(0);
            this._.el.search.$mpxCombo.show(0);
        } else {
            this._.el.search.$mpxCombo.hide(0);
            this._.el.search.$pxCombo.show(0);
        }
    },

    searchHighlight : function() {
        var txt = '',
            exact = false,
            sploded = false,
            iter,
            len,
            opts = {className: 'matched-search-term'},
            $cards = $(this._.el.search.imageCard);

        if(this._.inSearch) {
            if(typeof this._.advSearch === 'object' && (typeof this._.searchQuery != 'string' || this._.searchQuery === '')) {
                if(typeof this._.advSearch.q_all === 'string') {
                    txt += ' '+this._.advSearch.q_all;
                }
                if(typeof this._.advSearch.q_exactly === 'string') {
                    exact = this._.advSearch.q_exactly;
                }
                if(typeof this._.advSearch.q_any === 'string') {
                    txt += ' '+this._.advSearch.q_any;
                }
                if(txt.length) {
                    sploded = txt.match(/("[^"]+"|[^\s]+)/g);
                    len = sploded.length;
                    if(len > 0) {
                        for(iter = 0; iter < len; iter++) {
                            $cards.strEle(sploded[iter], opts);
                        }
                    }
                }
                if(exact) { // exact is highlighted wholesale
                    $cards.strEle(exact, opts);
                }
            } else if(this._.searchQuery) {
                $cards.strEle(this._.searchQuery, opts);
            }
            $cards.find('.bottom-box span').removeClass(opts.className);

        }
    },

    syncSearchSimpleAndAdvanced : function() {
        if(this._.el.search.$advanced.length < 1) {
            return;
        }
        // in the future this will need to parse search syntax and act
        // accordingly with the various advanced inputs
        if(this._.el.search.$advanced.is(':visible')) {
            this._syncSearchSimpleAndAdvancedElements(this._.el.search.$headerSearchInput, this._.el.search.$advancedAllInput);
            this._.el.search.$headerSearch.removeClass('search-focused').addClass('search-forced-closed');
            this._.el.search.$advancedAllInput.focus().select();
        } else {
            this._syncSearchSimpleAndAdvancedElements(this._.el.search.$advancedAllInput, this._.el.search.$headerSearchInput);
            this._.el.search.$headerSearch.removeClass('search-forced-closed').addClass('search-focused')
            this._.el.search.$headerSearchInput.focus().select();
        }
    },

    _syncSearchSimpleAndAdvancedElements : function($from, $to) {
        var tmpVal    = String($to.val()),
            tmpNewVal = $from.val(),
            place     = $from.attr('placeholder');

        if(tmpVal.length < 1 && tmpNewVal != place) {
            $to.val(tmpNewVal).trigger('change');
        }
        $from.val('')
    },

