Namespace('Imgur.Tag');

Imgur.Tag.OutsideTaggingWidget = Class.extend({
    init: function($outsideTagging, $outsideTaggingParent, $tagShowHide, emitter) {
        this._ = {
            emitter                : emitter,
            showBestCommentsCookie : 'showComments',
            showTagsCookie         : 'showTags',
            el: {
                $outsideTaggingParent: $outsideTaggingParent,
                $outsideTagging: $outsideTagging,
                $tagShowHide: $tagShowHide
            }
        };
    },

    isOpen: function() {
        return $.cookie(this._.showTagsCookie);
    },

    toggle: function(comments) {
        var hidden = this._.el.$outsideTaggingParent.hasClass('hidden');
        
        $('.tipsy').remove();

        if(hidden) {
            this._.el.$outsideTaggingParent.removeClass('hidden');
            this._.el.$tagShowHide.attr('title', 'Hide Sidebar');
            this._.el.$outsideTagging.stop(true, true).fadeTo(0, 1);
        } else {
            this._.el.$outsideTaggingParent.addClass('hidden');
            this._.el.$tagShowHide.attr('title', 'Show Sidebar');
            this._.el.$outsideTagging.stop(true, true).delay(100).fadeTo(100, 0);
        }

        if(comments) {
            this.setBestCommentsCookie(hidden? 1: 0);
        } else {
            this.setTagBoxCookie(hidden? 1: 0);
        }
    },

    openTagBox: function() {
        if(!this.isOpen()) {
            this.toggle(false);
        }
    },
    
    setTagBoxCookie: function(value) {
        if(value) {
            $.cookie(this._.showTagsCookie, value, {'path': '/', expires: 10000});
        } else {
            $.removeCookie(this._.showTagsCookie, {'path': '/'});
        }
    },
    
    setBestCommentsCookie: function(value) {
        if(value) {
            $.cookie(this._.showBestCommentsCookie, value, {'path': '/', expires: 10000});
        } else {
            $.removeCookie(this._.showBestCommentsCookie, {'path': '/'});
        }
    }
});
