Namespace('Imgur.Tag.Store');

Imgur.Tag.Store.Tag = Class.extend({
    INTERESTING_FIELDS: ['tag', 'subscribers', 'images'],

    init: function(emitter, limit) {
        this._ = {
            emitter: emitter,
            tags: {}, // map tag name -> tag object
            limit: limit || 0 //0 = unlimited tags
        };
    },

    getTag: function(tagName) {
        return this._.tags[tagName];
    },
    
    getTags: function() {
        return this._.tags;
    },

    haveTag: function(tagName) {
        return this._.tags.hasOwnProperty(tagName);
    },

    getTagSubscribers: function(tag) {
        if(this._.tags.hasOwnProperty(tag)) {
            if(this._.tags[tag].hasOwnProperty('subscribers')) {
                return this._.tags[tag].subscribers;
            }
        }

        return 'n/a';
    },

    addTag: function(tag) {
        if(this._.limit == 0 || Object.keys(this.getTags()).length != this._.limit) {
            var mergeBasis = {};
    
            if(this._.tags.hasOwnProperty(tag.tag)) {
                mergeBasis = this._.tags[tag.tag];
            }
    
            tag                  = _.pick(tag, this.INTERESTING_FIELDS);
            this._.tags[tag.tag] = _.merge({}, mergeBasis, tag);
            this._.emitter.trigger('tag updated');
        } else {
            this._.emitter.trigger('tag limit reached');
        }
    },

    removeTag: function(tagName) {
        delete this._.tags[tagName];

        this._.emitter.trigger('tag removed');
    },
    
    setFollowing: function(tagName, following) {
        var tag = this.getTag(tagName);
        
        if(tag) {
            tag.following = following;
        }
    },

    encode: function(tag_name) {
        return encodeURIComponent(tag_name.replace(new RegExp(' ', 'g'), '_'));
    }
});
