Namespace('Imgur.Tag.Validator');

Imgur.Tag.TagValidator = Class.extend({
    TAG_REGEX: /^[a-zA-Z0-9\_ ]*$/,

    init: function(min_length, max_length) {
        this._ = {
            maxLength: max_length,
            minLength: min_length
        };
    },

    isValidTag: function(cause, query, isDuplicate) {
        return this._validateTag(cause, query, isDuplicate).success;
    },

    whyNot: function(cause, query, isDuplicate) {
        return this._validateTag(cause, query, isDuplicate).message;
    },

    _validateTag: function(cause, query, isDuplicate) {
        var validationResult = {
            success: true,
            message: null
        };

        query = query.trim().toLowerCase();

        if(query.match(Object.getPrototypeOf(this).TAG_REGEX) == null) {
            validationResult.message = 'Tags can only contain alphanumeric, space, and underscore characters.';
            validationResult.success = false;
        }

        if(isDuplicate) {
            validationResult.message = 'This tag has already been added';
            validationResult.success = false;
        }

        if(cause == 'submit' && query.length < this._.minLength) {
            validationResult.message = 'Tag is too short. Tags can be between ' + this._.minLength + ' and ' + this._.maxLength + ' characters.';
            validationResult.success = false;
        } else if(query.length > this._.maxLength) {
            validationResult.message = 'Tag is too long. Tags can be between ' + this._.minLength + ' and ' + this._.maxLength + ' characters.';
            validationResult.success = false;
        }

        return validationResult;
    }
});
