Namespace('Imgur.Share');

Imgur.Share.Publish = React.createClass({
    displayName: 'Share-Publish',

    handleRules: function() {
        Imgur.Util.jafoLog({
            event: 'reportRules',
            meta: {
                type: 'galleryPost',
                hash: this.props.hash,
                isAlbum: this.props.is_album
            }
        });
    },
    
    render: function() {
        var publishButton,
            missingFieldsText,
            missingFields = this.props.cortex.missingFields.getValue();
        
        if (missingFields) {
            publishButton     = React.DOM.button({id: 'share', className: 'btn btn-grey', disabled: 'disabled'}, 'Finish & Share');
            missingFieldsText = React.DOM.p({id: 'missing-fields'}, 'missing ' + missingFields + ' required field' + (missingFields != 0 ? 's' : ''));
        } else {
            publishButton     = React.DOM.button({id: 'share', className: 'btn btn-main', onClick: this.props.handleSubmit}, 'Finish & Share');
            missingFieldsText = React.DOM.p({id: 'missing-fields', className: 'success'}, 'Remember our ',
                React.DOM.a({href: '/rules', target: '_blank', onClick: this.handleRules}, 'community rules')
            );
        }
        
        return React.DOM.div({},
            publishButton,
            React.DOM.span({id: 'share-loader', className: this.props.cortex.submitting.getValue() ? '' : 'nodisplay'}),
            this.props.cortex.promoted.getValue() ? '' : React.DOM.a({id: 'preview', href: '', onClick: this.props.handlePreview}, 'preview post'),
            missingFieldsText
        );
    }
});
