Namespace('Imgur.Favorite');
  
Imgur.Favorite = Imgur.extend({
    init : function(params) {
        this._ = $.extend({
            'method'       : null,
            'hash'         : null,
            '$favButton'   : $('.favorite-image'),
            'confirm'  : {
                'target'      : '#colorbox-confirm',
                '$message'    : $('#colorbox-confirm-message'),
                '$title'      : $('#colorbox-confirm-title')
            },
            'messages' : {
                'favorited'    : '<div class="favorited"></div>',
                'unfavorited'  : '<div class="favorited unfavorited"></div>',
                'confirmMsg'   : function (fav) { return 'Are you sure you want to '+ (fav? 'unfavorite':'favorite')+ ' this image?';},
                'confirmTitle' : function (fav) { return (fav? 'Unfavorite':'Favorite')+ ' this image?';}
            }
        }, params);

        this._.method = $.trim(this._.method);
        this._super(this._);
        if(!this._.hash) {
            this._.hash = Imgur.InsideNav.getInstance().getHash();
        }
    },
    
    _init : function() {
        this.showMessage();

        // can be run multiple times        
        this._init_click_handler();
    },
    
    _init_click_handler : function() {
        this._.$favButton.unbind('click').on('click', _.bind(function (e) {
            var displayMsg = false;
            var isAlbum;
            var insideImage;
            var callBack;

            if(typeof e.fav_display_msg != 'undefined' && e.fav_display_msg === true) {
                displayMsg = true;
            }

            // find if this was the gallery item and read the album state from that
            // try/catch just in case the class exists, but is not instantiated
            try {
                if(Imgur.InsideNav) {
                    insideImage = Imgur.InsideNav.getInstance().getImage();
                }

                if(insideImage && this._.hash === insideImage.hash) {
                    isAlbum = insideImage.is_album;

                    callBack = function(method) {
                        Imgur.InsideNav.getInstance().updateFavorite(method == 'favorited');
                    };
                }
            } catch(ex) {
                // http://i.imgur.com/q7vY1YX.gif
            }

            this.favorite(this._.hash, isAlbum, callBack, displayMsg);
        }, this));

        Imgur.Util.triggerPerformanceCheckpoint('favoritebtn');
    },

    handleKey: function(e) {
        if(!e.ctrlKey && !e.altKey && !e.metaKey && (e.keyCode == 48 || e.keyCode == 45 || e.keyCode == 96)) {
            if(!$(e.target).is('input, textarea') && $(e.target).attr('contenteditable') != 'true') {
                // display heart popup only when key is pressed
                var event = jQuery.Event('click', {'fav_display_msg' : true});
                this._.$favButton.trigger(event);
            }
        }
    },

    showMessage : function (method) {
        if(typeof(method) == 'undefined') {
            method = this._.method;
        }
        
        if(method == 'favorited') {
           this.showFavorited();
        } else if(method == 'unfavorited') {
            this.showUnfavorited();
        } else if(method == 'confirm') {
            this.showConfirm();
        }
    },
    
    showFavorited : function() {
        humanMsg.displayMsg(this._.messages.favorited, 'action-msg');
    },
    
    showUnfavorited: function() {
        humanMsg.displayMsg(this._.messages.unfavorited, 'action-msg');
    },

    showConfirm : function () {        
        this._.confirm.$message.html(this._.messages.confirmMsg(this.isFavorited()));
        this._.confirm.$title.html(this._.messages.confirmTitle(this.isFavorited()));
        
        Imgur.Utilities.ColorboxConfirm.getInstance().show(this._.confirm.target,  _.bind(this.favorite, this));
    },
    
    favorite : function(hash, is_album, callback, display_msg) {
        if(!Imgur.getInstance()._.signed) {
            $('.tipsy,#favorite-bar').remove(); // Needed for when clicked from imgur image via linkifier
            Imgur.getInstance().signin('favoriteAttempt', true);
        }

        if(typeof(hash) == 'undefined') {
            hash = this._.hash;
        } else if(typeof(hash) == 'function') {
            hash = hash();
        }

        if(Imgur.getInstance()._.signed) {
            // Do not update UI state if user is not logged in
            if(this._.$favButton.hasClass('favorited')) {
                this._.$favButton.removeClass('favorited');
                this._.method = 'unfavorited';
            } else {
                this._.$favButton.addClass('favorited');
                this._.method =  'favorited';

                if($('#mainUpArrow').is(':visible')) {
                    this.upvote();
                }
            }

            if(display_msg) {
                this.showMessage();
            }
        }
        
        var url = '/'+hash+'/fav.json';
        if(window.location.href.indexOf("/a/") !== -1 || is_album) {
            url = '/a'+url;
        }
        
        var data = {
            ajax: true,
            msid: Imgur.Environment.msid,
            location: Imgur.InsideNav? 'inside': 'outside'
        }
        
        //Force upvote on login
        if(!Imgur.getInstance()._.signed) {
            _.extend(data, {
                sid:  Imgur.Environment.sid,
                vote: 'up',
                type: 'image'
            });
        } else {
            Imgur.Util.jafoLog({
                event: 'favorite',
                meta: {
                    isAlbum: is_album? is_album: window.location.href.indexOf("/a/") !== -1,
                    hash: hash,
                    location: Imgur.InsideNav? 'inside': 'outside',
                    state: this._.method === 'unfavorited'? 'unfavorite': 'favorite'
                }
            });
        }
        
        $.ajax({
            'url' : url,
            'type': 'POST',
            'data': data,
            'success' : _.bind(this.favoriteSuccess, this, callback),
            'error': _.bind(this.favoriteError, this)
        });
    },
    
    favoriteSuccess : function(callback, response) {
        if(response && 'success' in response && response.success) {
            this._.method = response.data.fav_method;
        
            if(callback) {
                callback(response.data.fav_method);
            }
        }
    },
    
    favoriteError : function() {
        if(Imgur.getInstance()._.signed) {
            // Since we are only updating UI when user is logged in
            // no need to revert if a failure occurs if user is not logged in
            if(this._.method == 'favorited') {
                this._.$favButton.removeClass('favorited');
                this._.method = 'unfavorited';
            } else {
                this._.$favButton.addClass('favorited');
                this._.method = 'favorited';
            }
        }
    },
    
    isFavorited : function() {
        return this._.$favButton.hasClass('favorited');
    },
    
    upvote : function() {
        if(!Imgur.getInstance()._.signed) { return; }
        
        if(!$('#mainUpArrow').hasClass('pushed') && !$('#mainDownArrow').hasClass('pushed') ) {
            $('#mainUpArrow').click();
        }
    }

});
