Namespace('Imgur.InfiniteScroll');

Imgur.InfiniteScroll = Class.extend({
    init : function(params) {
        this._ = $.extend(true, {
            'speed'    : 200,
            'height'   : 300,
            'cont'     : true,
            'calcRight': true
        }, params);
    },

    levelReached : function() {
        var pageHeight = Math.max(document.body.scrollHeight ||
            document.body.offsetHeight);
        var viewportHeight = window.innerHeight  ||
            document.documentElement.clientHeight  ||
            document.body.clientHeight || 0;
        var scrollHeight = window.pageYOffset ||
            document.documentElement.scrollTop  ||
            document.body.scrollTop || 0;

        if(!this._.calcRight) {
            pageHeight -= $('#content').children('.right').height();
        }
        
        if(pageHeight <= 0) {
            return false;
        } else {
            return pageHeight - viewportHeight - scrollHeight < this._.height;
        }
    },

    stopInfiniteScroll : function() {
        this._.cont = false;
    },

    continueInfiniteScroll : function() {
        this._.cont = true;
    },

    infiniteScroll : function(cb) {
        if(this.levelReached()) {
            if(this._.cont) { cb.call(); }
        } else {
            if(this._.cont) { this.pollLevel(cb); }
        }
    },

    pollLevel : function(cb) {
        var self = this;
        setTimeout(function() { self.infiniteScroll(cb); }, this._.speed);
    }

});

Class.addSingleton(Imgur.InfiniteScroll);
