Namespace('Imgur.Tag');

Imgur.Tag.UserGroup = Imgur.Tag.Group.extend({
    init: function($container, list) {
        this._super($container, list);
        this._.el.$remove = $container.find('.remove');
    },

    setName: function(name) {
        this._.el.$name.text(name);
    },

    get$RemoveButton: function() {
        return this._.el.$remove;
    },

    remove: function() {
        this._.el.$container.remove();
    },
    
    equals: function(other) {
        return other === this.getId();
    }
});
