Namespace('Imgur.Tag');

Imgur.Tag.RemovableTag = Imgur.Tag.Tag.extend({
    init: function(emitter, $container) {
        this._super($container);
        this._.emitter = emitter;
        this._.el.$remove = $container.find('.remove');
    },

    getEmitter: function() {
        return this._.emitter;
    },

    get$RemoveButton: function() {
        return this._.el.$remove;
    },

    markConfirmed: function() {
        this._.el.$container.removeClass('pending');
    },

    remove: function() {
        this._.emitter.trigger('tag.remove', this.getName());
        this._super();
    }
});
