Namespace('Imgur.Factory');

Imgur.Factory.OutsideGallery = Imgur.Factory.Gallery.extend({
    init: function(global_env, gallery_env, groups_env, postProvider) {
        this._ = {
            env: {
                global: global_env,
                gallery: gallery_env,
                groups: groups_env
            },
            gallery_env: gallery_env,
            postProvider: postProvider
        };
    },

    produceOutsideGallery: function(emitter) {
        var outsideActiveTagStore = new Imgur.Tag.Store.OutsideActiveTag(emitter);

        var history     = new Imgur.History();
        var gallery_env = this._.env.gallery;
        var gallery     = new Imgur.Gallery.MainOutside(outsideActiveTagStore, emitter, gallery_env, history);

        emitter.on('show tagset loader', gallery, gallery.fadeInLoader);
        emitter.on('hide tagset loader', gallery, gallery.fadeOutLoader);
        emitter.on('new tagset', gallery, gallery.refetchPage);
        emitter.on('new outside active tag', gallery, gallery.handleTagChanged);

        $('#imagelist').on('click', '.remove-outside-image', function(e) {
            var hash = $(e.target).attr('data-hash');
            gallery.remove(hash);
            $('#'+hash).fadeOut();
        });

        $('#imagelist').on('click', '.scroll-top-text', function(e) {
            $("html, body").animate({scrollTop:0}, '500', 'easeInOutCirc');
        });

        $('#sponsored-upload').click(_.bind(function() {
            if(!this._.env.gallery.signed) {
                var imgur = new Imgur();
                imgur.signin('sponsoredUploadAttempt', true);
            } else {
                $('#topbar li.upload-button').click();
            }
         }, this));

        history.addPopState('initial', _.bind(gallery.initialPopState, gallery));

        Imgur.Gallery._instance = gallery;

        return {
            gallery: gallery,
            history: history,
            outsideActiveTagStore: outsideActiveTagStore
        };
    },

    produceOutsideUser: function(emitter) {
        var gallery_env = this._.env.gallery;
        var gallery = new Imgur.Gallery.Outside(emitter, gallery_env);

        Imgur.Gallery._instance = gallery;
        return gallery;
    },

    produceOutsideMemes: function(emitter) {
        var gallery_env = this._.env.gallery;
        var gallery = new Imgur.Gallery.Outside(emitter, gallery_env);

        Imgur.Gallery._instance = gallery;
        return gallery;
    },

    produceOutsideNonMainSectionChooser: function(emitter) {
        $('#section').combobox({
            maybe: function(element, value, $choice) {
                return false;
            }
        });

        $('#sort').combobox();

        var activeGroup = null;
        var NonMainGroupSelector = new Imgur.Tag.GroupSelector(emitter, this._.env.gallery.section, this._.env.gallery.sort, this._.env.gallery.searchQuery, $('.sentence-sorting'), activeGroup);
    },

    produceOutsideTaggingWidget: function(emitter, galleryGraph) {
        var sid    = this._.env.gallery.sid;
        var signed = this._.env.gallery.signed;
        var current_group = this._.env.gallery.group;
        var current_group_type = this._.env.gallery.group_type;

        var imgur = new Imgur(),
            signin = function(e) { e.preventDefault(); e.stopPropagation(); imgur.signin('tagAttempt', true); };


        var $outsideTagging = $('#outside-tagging'),
            $outsideTaggingParent = $('#content .panel'),
            $tagShowHide = $('#outside-tagging-showhide');

        $('#section').combobox({
            maybe: function(element, value, $choice) {
                return false;
            }
        });

        $('#sort').combobox({
            maybe: function(element, value, $choice) {
                return false;
            },
            
            onChange: function(element, value, $choice) {
                emitter.trigger('chosen.sort', value, $choice);
            }
        });

        var tagStore = new Imgur.Tag.Store.Tag(emitter),
            tagGroupStore = new Imgur.Tag.Store.TagGroup(emitter),
            groupStore = new Imgur.Tag.Store.Group(emitter, this._.env.groups.groups),
            groupModel = new Imgur.Tag.GroupModel(this._getStandardPostAuthorized(), tagGroupStore, emitter),
            tagValidator = new Imgur.Tag.TagValidator(Imgur.Constants.MIN_TAG_LENGTH, Imgur.Constants.MAX_TAG_LENGTH),
            suggestionStore = new Imgur.Tag.Store.Suggestion(emitter, tagValidator);

        Imgur.Dev.DebugVar('suggestionstore', suggestionStore);

        var currentGroupId = this._.env.gallery.group.id;
        _.each(this._.env.gallery.group.tags, function(tag) {
            tagGroupStore.addTagToGroup(tag, currentGroupId);
        });

        Imgur.Dev.DebugVar('tagGroupStore', tagGroupStore);
        Imgur.Dev.DebugVar('outsideActiveTagStore', galleryGraph.outsideActiveTagStore);
        Imgur.Dev.DebugVar('tagstore', tagStore);
        Imgur.Dev.DebugVar('groupstore', groupStore);
        Imgur.Dev.DebugVar('taggroupstore', tagGroupStore);

        var extractTagGroupData = (function() {
            var $existingTagGroupData = $("script[type='text/tag-group-data']");

            $existingTagGroupData.each(function() {
                var tagGroupData = {};
                try {
                    tagGroupData = $.parseJSON($(this).html());
                } catch(e) {}

                _.each(tagGroupData, function(groups, tag) {
                    _.each(groups, function(group_hash) {
                        if(!tagGroupStore.isTagInGroup(tag, group_hash)) {
                            tagGroupStore.addTagToGroup(tag, group_hash);
                        }
                    });
                });

                $(this).remove();
            });
        });

        extractTagGroupData();
        
        var extractTagData = (function() {
            var $existingTagData = $("script[type='text/tag-data']");

            $existingTagData.each(function() {
                var tag_obj = null;

                try {
                    tag_obj = $.parseJSON($(this).html());
                } catch(e) {}

                if(tag_obj) {
                    tagStore.addTag(tag_obj);
                }

                $(this).remove();
            });
        });

        extractTagData();

        emitter.on('new gallery page', null, extractTagData);

        var groupModel = this._produceGroupModel(emitter, tagGroupStore);

        var renderTagList = _.bind(function() {
            if(tagGroupStore.getTagsForGroup(currentGroupId).length == 0  && current_group_type != 'base') {
                this._produceEmptyGalleryTagList(emitter, tagGroupStore, galleryGraph.outsideActiveTagStore, suggestionStore, groupModel);
            } else {
                this._produceUserTagList(emitter, tagGroupStore, galleryGraph.outsideActiveTagStore, suggestionStore, groupModel);
            }
        }, this);

        if ((this._.env.gallery.section == 'group' || this._.env.gallery.section == 'custom') && current_group_type != 'base') {
            renderTagList();

            emitter.on('gallery page refetched', null, renderTagList);
        }
        
        var outsideWidget = new Imgur.Tag.OutsideTaggingWidget($outsideTagging, $outsideTaggingParent, $tagShowHide, emitter);

        $tagShowHide.on('click', _.bind(outsideWidget.toggle, outsideWidget, this._.env.gallery.bestCaps));
        var $sentence = $('.sentence-sorting');

        var groupValidator = new Imgur.Tag.GroupValidator(2, 25);

        var sentence = new Imgur.Tag.Sentence($sentence.find('.before-text'), $sentence.find('.middle-text'), groupStore);

        var group_selector = new Imgur.Tag.MainGroupSelector(
            emitter, 
            this._.env.gallery.section, 
            this._.env.gallery.sort, 
            this._.env.gallery.inSearch,
            $sentence,
            sentence,
            galleryGraph.outsideActiveTagStore
        );

        galleryGraph.history.addPopState('tag', function TagPopStateHandler(e) {
            var tagName = window.location.href.replace(/.*\/t\//i, '').replace(/_/g,' ');
            galleryGraph.outsideActiveTagStore.setActiveTag(tagName, true);
        });

        if(this._.env.gallery.bestCaps) {
            $('#top-comments .best-captions img').tipsy({
                opacity: 0.95,
                offset: -2,
                gravity: 'n',
                html: true
            });
        }

        if(!signed) {
            $('#custom-group a').on('click', signin);
            return;
        }

        var groupLimitsController = new Imgur.Tag.Controller.GroupSelectorLimits(emitter, group_selector, groupStore);
        emitter.on('group.remove', groupLimitsController, groupLimitsController.handleGroupRemove);
        emitter.on('group.add', groupLimitsController, groupLimitsController.handleGroupAdd);

        return groupModel;
    },
    
    produceSharePage: function(emitter, type, hash, tags, tagLimit, gallery_type, title, topic, default_tags) {
        var tagStore = new Imgur.Tag.Store.Tag(emitter, tagLimit);
        
        _.each(tags, function(tag){
            tagStore.addTag({
                tag: tag
            });
        });
        
        var tagValidator    = new Imgur.Tag.TagValidator(2, 36);
        var suggestionStore = new Imgur.Tag.Store.Suggestion(emitter, tagValidator);
        var autoSuggestUrl  = '//es.' + this._.env.global.host;
        var suggestModel    = new Imgur.Tag.SuggestModelNoProvider(this._.postProvider(), autoSuggestUrl);
        var $tagInput       = $("#tags input");
        var placeholder     = $tagInput.attr('placeholder');
        var tabIndex        = $tagInput.attr('tabindex');
        
        var renderTitle = (function() {
            ReactDOM.render(React.createElement(Imgur.Share.Title,{
                title: title,
                maxLength: 255,
                inputClasses: 'required input-bb',
                limitClasses: 'limit'
            }), $('#submission-title').get(0));
        });
        
        renderTitle();
        
        var renderInsideList = (function() {
            ReactDOM.render(React.createElement(Imgur.Tag.ShareTagComponent,{
                tagStore        : tagStore,
                suggestionStore : suggestionStore,
                placeholder     : placeholder,
                tabIndex        : tabIndex,
                id              : 'tagbox',
                gallery_type    : gallery_type,
                defaultTags     : default_tags,
                tagLimit        : tagLimit
            }), $('#tags').get(0));
        });

        renderInsideList();
        
        var shareOnImgur = Imgur.ShareOnImgur.getInstance({
            type     : type,
            hash     : hash,
            tagStore : tagStore,
            emitter  : emitter,
            hasTitle : !!title,
            hasTopic : !!topic
        });
        
        shareOnImgur.generalInit();
        
        var outsideController = new Imgur.Tag.Controller.OutsideComponent(suggestModel, suggestionStore);
        emitter.on('new suggest query', null, _.bind(outsideController.handleChangedQuery, outsideController));
        
        emitter.on('select', null, renderInsideList);
        emitter.on('new suggest query', null, renderInsideList);
        emitter.on('new finished query', null, renderInsideList);
        emitter.on('new suggestions', null, renderInsideList);
        emitter.on('tag removed', null, renderInsideList);
        
        emitter.on('tag limit reached', null, function(){
            humanMsg.displayMsg('You have already added the maximum number of tags', 'error-msg');
        });
        
        //Handle blurring of tagging widget
        $(document).on('click', function(e) { 
            //Clicking on a suggested tag or the tag input
            if($(e.target).closest('.tag-filter, .tag-list-content').length){
                $('#tags').addClass('focus');
                return;
            } else {
                $('#tags').removeClass('focus');
            }
        });
    },

    _produceUserTagList: function(emitter, tagGroupStore, outsideActiveTagStore, suggestionStore, groupModel) {
        var suggestedPost = this._.postProvider();
        var autoSuggestUrl = '//es.' + this._.env.global.host;
        var suggestModel = new Imgur.Tag.SuggestModelNoProvider(suggestedPost, autoSuggestUrl);

        var userTagListProps = {
            tagGroupStore: tagGroupStore,
            groupId: this._.env.gallery.group.id,
            outsideActiveTagStore: outsideActiveTagStore,
            groupModel: groupModel,
            suggestionStore: suggestionStore,
            placeholder: 'add tags to this gallery'
        };

        $('#tags-current-list, #outside-tagging-showhide').show();

        if($.cookie('showTags')) {
            $('#content > .panel').removeClass('hidden');
        }

        var renderUserTagList = function() {
            ReactDOM.render(React.createElement(Imgur.Tag.TagListComponent,userTagListProps), $("#tags-current-list").get(0));
        };

        renderUserTagList();

        emitter.on('tag added to group', null, renderUserTagList);
        emitter.on('tag removed from group', null, renderUserTagList);
        emitter.on('new tag-group event', null, renderUserTagList);
        emitter.on('pending tag removed from group', null, renderUserTagList);
        emitter.on('new outside active tag', null, renderUserTagList);
        emitter.on('new suggestions', null, renderUserTagList);
        emitter.on('new suggest query', null, renderUserTagList);
        emitter.on('new finished query', null, renderUserTagList);
        emitter.on('new suggest validation mode', null, renderUserTagList);

        var outsideController = new Imgur.Tag.Controller.OutsideComponent(suggestModel, suggestionStore);
        emitter.on('new suggest query', null, _.bind(outsideController.handleChangedQuery, outsideController));
    },

    _produceGroupModel: function(emitter, tagGroupStore) {
        var self = this;

        var groupModel = new Imgur.Tag.GroupModel(this._getStandardPostAuthorized(), tagGroupStore, emitter);

        return groupModel;
    },

    _produceSuggestedTagFromData: function(cb_choose, hash, name, subscribers, is_first) {
        var $dom = $(_.templates['tags.tag-suggested']({
            is_first: !!is_first,
            tag: {
                name: name,
                subscribers: subscribers,
                urlencoded: ''
            }
        }));

        var tag = new Imgur.Tag.SuggestedTag($dom, hash);

        tag.get$Add().on('click', _.partial(cb_choose, tag));

        return tag;
    },

    _produceRemovableTagFromData: function(hash, name, subscriptions) {
        var $dom = $(_.templates['tags.tag-removable']({
            tag: name
        }));

        return $dom;
    },

    produceRemovableTagFromDom: function(emitter, groupController, nanoScroller, $dom) {
        var tag = new Imgur.Tag.RemovableTag(emitter, $dom);

        tag.get$RemoveButton().on('click', _.bind(groupController.handleRemovedTagEvent, groupController, tag));

        var covers = [{
            spot: 'markConfirmed',
            cover: 'recomputeNanoScroller',
            type: 'passAfter'
        }];

        tag = Imgur.Util.decorateWithDetailed(tag, nanoScroller, covers);
        return tag;
    },

    _produceComboboxItemFromSomething: function($something, value) {
        var $item = $(_.templates['tags.combobox-item']({
            value: value
        }));

        $item.append($something);

        return $item;
    },
    
    _produceEmptyGalleryTagList: function(emitter, tagGroupStore, outsideActiveTagStore, suggestionStore, groupModel) {
        var suggestedPost = this._.postProvider();
        var autoSuggestUrl = '//es.' + this._.env.global.host;
        var suggestModel = new Imgur.Tag.SuggestModelNoProvider(suggestedPost, autoSuggestUrl);

        var userTagListProps = {
            tagGroupStore: tagGroupStore,
            groupId: this._.env.gallery.group.id,
            outsideActiveTagStore: outsideActiveTagStore,
            groupModel: groupModel,
            suggestionStore: suggestionStore,
            id: 'add-tag',
            placeholder: 'Start following tags and stay up to date with the most viral images on the Internet'
        };
        
        //Sidebar always hidden on new tag page
        $('#tags-current-list, #outside-tagging-showhide').hide();
        $('#content > .panel').addClass('hidden');

        var renderUserTagList = function() {
            var addTagContainer = $('#add-tag-container').get(0);
            
            if(addTagContainer) {
                ReactDOM.render(React.createElement(Imgur.Tag.TagListComponent,userTagListProps), addTagContainer);
            }
        };

        renderUserTagList();
        
        emitter.on('select', null, renderUserTagList);
        emitter.on('new suggest query', null, renderUserTagList);
        emitter.on('new finished query', null, renderUserTagList);
        emitter.on('new suggest validation mode', null, renderUserTagList);
        emitter.on('new suggestions', null, renderUserTagList);
        
        var outsideController = new Imgur.Tag.Controller.OutsideComponent(suggestModel, suggestionStore);
        emitter.on('new suggest query', null, _.bind(outsideController.handleChangedQuery, outsideController));
        
        $('.popular-tag .add-tag').on('click', function(){
            //Get the popular tag name the user wants to add
            var tag = $(this).parent().find('.tag-name').text();
            groupModel.subscribe(userTagListProps.groupId, tag);
        });
    }
});
