(function($) {
    var combobox = function(element) {
        var cb        = this,
            selection = $('<div class="selection"></div>'),
            input     = $('<input type="hidden" name="" value="" />'),
            triangle  = '<div class="triangle {{vector-icon-class}}"></div>',
            options   = null,
            pos       = null,
            
            settings = {
                vectorIcons: false,
                onChange: function() { return null }
            },
            
            toggleOptions = function() {
                var opening = false;
                if(element.hasClass('opened')) {
                    element.removeClass('opened');
                    options.css({
                        'height': ''
                    });
                } else {
                    opening = true;
                    closeAll();
                    element.addClass('opened');
                    
                    var height    = calculateHeight(options);
                    var $nano     = element.children('.nano');
                    var maxHeight = parseInt(options.css('max-height'), 10);
                    
                    options.css({
                        'height': height,
                    });
                    
                    if($nano.length && maxHeight) {
                        setTimeout(function() {
                            $nano.css('position','relative');
                            $nano.nanoScroller({
                                preventPageScrolling: true,
                                alwaysVisible: true
                            });
                            $nano.css('position','');
                            element.hide().show(0); //Removing the position doesn't cause a redraw in chrome so force it here
                        }, 125); //Height animation is 100ms
                    }
                    
                    element.trigger('open');
                }
                
                if(opening) {
                    var filter;
                    if(filter = options.find('.filter')) {
                        filter.focus();
                    }
                }
            },
            closeAll = function() {
                var combobox = $('.combobox, .opened');
                combobox.find('.options').css({
                    'height': ''
                });
                combobox.removeClass('opened');
            },
            stopEvents = function(e) {
                if(!e) {
                   e = window.event;
                }
                
                if(e.stopPropagation) {
                    e.stopPropagation();
                }
                
                e.cancelBubble = true;
                
                if(e.preventDefault) {
                    e.preventDefault();
                }
                
                e.returnValue = false;
                
                return false;
             },
            defaultFilter = function(text, options) {
                // return only the options that have text contained in its textContent
                return options.filter(function() {
                    return (this.textContent.toLowerCase().indexOf(text) !== -1);
                });
            },
            emptyFilter = function(text, options) {
                return options;
            },
            calculateHeight = function(options) {
                var height = 0;
                
                options.children(':not(.pane)').each(function() {
                    var $this = $(this);
                    
                    if ($this.css('display') != 'none') {                            
                        height += $(this).outerHeight(true);
                    }
                });
                
                return height;
            };
            
        this.run = function() {
            options = element.children('.options');            
            options.removeClass('opened');

            triangle = $(triangle.replace('{{vector-icon-class}}', settings.vectorIcons ? 'icon-arrow-down' : ''));

            if(element.find('.selection').length == 0) {
                element.prepend(triangle)
                       .prepend(selection)
                       .append(input);
            } else {
                selection = element.find('.selection');
                input     = element.find('input[type="hidden"]');
            }
            
            element.find('.triangle').unbind('click');
            element.find('.triangle').click(function(e) {
                toggleOptions();
                this.lazyload();
                stopEvents(e);
            }.bind(this));
            
            selection.unbind('click');
            selection.click(function(e) {
                toggleOptions();
                this.lazyload();
                stopEvents(e);
            }.bind(this));
            
            $('body').click(function(e) {
                if(element.hasClass('opened')) {
                    toggleOptions();
                    element.trigger('close');
                }
            });
            
            options.off('click', '.item');
            options.on('click', '.item', function() {
                if(typeof settings.maybe != 'function' || settings.maybe($(this), $(this).attr('data-value'), $(this))) {
                    $('div[name="'+element.attr('name')+'"]').find('.selection').html($(this).html());
                    input.attr('value', $(this).attr('data-value'));
                    
                    options.find('.item').removeClass('selected');
                    $(this).addClass('selected');

                    toggleOptions();
                    settings.onChange(element.attr('name'), $(this).attr('data-value'), $(this));
                }
            });
            
            selection.html(element.find('.selected').html());
            
            input.attr('name', element.attr('name'))
                 .attr('value', element.find('.selected').attr('value'));
        };
        
        this.lazyload = function() {
            options.find('img.lazyload').each(function() {
                $(this).attr('src', $(this).attr('data-src'));
                $(this).removeClass('lazyload');
            });
        };

        this.init = function(options) {
            if(options) {
                $.extend(settings, options);
            }

            this.run();
        };

        /**
         * Add the filter element, bind its keyup, etc.
         */
        this.setupFilter = function(placeholder_text) {
            var _options = options.children(".item[data-value]").filter(function() {
                return ($(this).attr('data-value').length);
            });
            this._original_options = _options.clone(true);
            var filter_input = $(document.createElement('input')).attr({
                'type': 'text',
                'class': 'filter placeholder-onkeydown',
                'title': placeholder_text
            }).keyup(this.filterKeyUp.bind(this)).click(function(e) {
                e.preventDefault();
                e.stopPropagation();
            });
            Imgur.getInstance().runPlaceholder(filter_input);
            options.prepend(filter_input);
        };

        /**
         * Add a type-as-you-go filter to the combobox. The filter selects and orders the options.
         *
         * @param filter : A function that takes the current search text as its first argument, and the
         *                 list of HTMLOption options as the second argument, and returns an ordered array of
         *                 the options.
         */
        this.setFilter = function(filter, placeholder_text) {
            if(!filter) {
                filter = defaultFilter;
            }
            if(!this.filter) {
                this.filter = filter;
                this.setupFilter(placeholder_text);   
            } else {
                this.filter = filter;
            }
        };

        this.filterKeyUp = function(e) {
            var filter_text = $(e.target).val();
            if(!filter_text.length) {
                // use the empty filter, don't do more work than necessary
                var _options = this.applyFilter(filter_text, emptyFilter);
            } else {
                var _options = this.applyFilter(filter_text);
            }
            options.find('.item').remove();
            options.append(_options);
            options.css('height', Math.max(calculateHeight(_options), $(e.target).outerHeight()));
            this.lazyload();
        };

        this.applyFilter = function(text, filter) {
            filter = filter || this.filter;
            return filter(text, this._original_options);
        }
    };
    
    methods = {
        init: function (options) {
            return this.each(function () {
                var inst = new combobox($(this));
                inst.init(options);
                var $that = $(this);
                $that.data('combobox', inst);
            });
        },
        filter: function(options) {
            if(!options) {
                options = {};
            }
            return this.each(function() {
                var $this = $(this);
                if($this.data('combobox')) {
                    var inst = $this.data('combobox');
                    inst.setFilter(options.filter, options.placeholder);
                }
            });
        }
    };
    
    $.fn.combobox = function (method) {
        if (methods[method]) {
            return methods[method].apply(this, Array.prototype.slice.call(arguments, 1));
        } else if (typeof method === 'object' || !method) {
            return methods.init.apply(this, arguments);
        } else {
            return $.error('Method ' + method + ' does not exist');
        }
    };
}(jQuery));
