Namespace('Imgur');

/**
 * Lazy container that renders comments only when they're $(':in-viewport')
 */
Imgur.CommentList = React.createClass({
    RENDERED_AT_A_TIME: 20,

    displayName: 'CommentList',

    mixins: [React.addons.PureRenderMixin],

    propTypes: {
        image: React.PropTypes.object.isRequired,
        contextids: React.PropTypes.object.isRequired,
        childmap: React.PropTypes.object.isRequired,
        votemap: React.PropTypes.object.isRequired,
        level: React.PropTypes.number.isRequired,
        expandMode: React.PropTypes.number.isRequired,
        expandedFromPermalink: React.PropTypes.bool.isRequired,
        groupingBadReplies: React.PropTypes.bool.isRequired,
        parent_id: React.PropTypes.number
    },

    getInitialState: function() {
        return {
            rendered: this.RENDERED_AT_A_TIME,
            bad_comments: false,
            // for recommendations experiment (expT4377):
            truncatingForRecommendations: true,
        };
    },

    _getChildrenIds: function() {
        if(this.props.parent_id in this.props.childmap) {
            return this.props.childmap[this.props.parent_id];
        } else {
            return [];
        }
    },

    componentDidUpdate: function() {
        this._checkNeedToRenderMore();
    },

    componentDidMount: function() {
        // Scroll events could get fired after the component unmounts if the browser event queue happens to be slow.
        // We shouldn't use this.isMounted() because it's being deprecated.
        // https://github.com/facebook/react/issues/3417#issuecomment-121649937
        this._mounted = true;

        this._listener = _.throttle(this._checkNeedToRenderMore, 50);
        $(window).on('scroll', this._listener);
        this._checkNeedToRenderMore();
    },

    componentWillUnmount: function() {
        $(window).off('scroll', this._listener);
        this._mounted = false;
    },

    toggleShowingBadComments: function(e) {
        // Prevent propagation up to the CommentTree to prevent this auto-expanding the comment
        e.stopPropagation();

        this.setState({
            bad_comments: !this.state.bad_comments
        });
    },

    _checkNeedToRenderMore: function() {
        if(this._mounted) {
            var children_ids = this._getChildrenIds();

            if(children_ids.length > 0 && children_ids.length > this.state.rendered) {
                var $last_rendered = $(ReactDOM.findDOMNode(this.refs.last));

                if($last_rendered.is(':in-viewport')) {
                    this.setState({
                        rendered: this.state.rendered + this.RENDERED_AT_A_TIME
                    });
                }
            }
        }
    },

    _handleBadCaptionsContainerClick: function(e) {
        // Prevent propagation up to the CommentTree to prevent this auto-expanding the comment
        e.stopPropagation();
    },

    render: function() {
        var hash = this.props.image.hash;

        var good_comments = [], bad_comments = [], just_inserted = [];
        var children_ids = this._getChildrenIds();
        var has_bad_comments = false;

        if (!this.props.removing) {
            for(var i = 0; i < Math.min(children_ids.length, this.state.rendered); i++) {
                var comment = Imgur.Caption.getInstance().getCaptionById(hash, children_ids[i]);

                var props = {
                    comment: comment,
                    image: this.props.image,
                    key: comment.id,
                    level: this.props.level,
                    expandMode: this.props.expandMode,
                    expandedFromPermalink: this.props.expandedFromPermalink,
                    contextids: this.props.contextids,
                    childmap: this.props.childmap,
                    votemap: this.props.votemap,
                    votable: true,
                    groupingBadReplies: this.props.groupingBadReplies,
                    touchEnabled: this.props.touchEnabled,
                };

                // last comment
                if(i == Math.min(children_ids.length, this.state.rendered) - 1) {
                    props.ref = 'last';
                }

                if(comment.points >= 0 || !this.props.groupingBadReplies) {
                    if(comment.just_inserted) {
                        just_inserted.push(React.createElement(Imgur.CommentTree,props));
                    } else {
                        good_comments.push(React.createElement(Imgur.CommentTree,props));
                    }
                } else {
                    bad_comments.push(React.createElement(Imgur.CommentTree,props));
                    has_bad_comments = true;
                }
            }
        }

        var showMoreMarkup = null;
        // truncate to 5 comments for recommendations experiment (expT4377)
        if (this.state.truncatingForRecommendations && this.props.inRecommendationsExperiment && this.props.parent_id === null) {
            showMoreMarkup = good_comments.length > 5 ? React.DOM.button({
                className: 'btn btn-grey',
                style: {
                    margin: '0 auto',
                    display: 'block'
                },
                onClick: function () {
                    this.setState({truncatingForRecommendations: false})
                }.bind(this),
            }, 'Show More Comments') : null;
            good_comments = good_comments.slice(0, 5);
        }

        return React.DOM.div({className: 'children'},
            React.createElement(React.addons.CSSTransitionGroup,{
                key: 'just-inserted-' + this.props.image.hash,
                transitionName: "newcomment",
                transitionEnterTimeout: 500,
                transitionLeave: false
            },
                just_inserted
            ),
            React.createElement(React.addons.CSSTransitionGroup,{
                key: 'just-expanded-' + this.props.image.hash,
                transitionName: "expandcomment",
                transitionAppear: true,
                transitionEnter: false,
                transitionAppearTimeout: 250,
                transitionLeaveTimeout: 250
            },
                good_comments,
                React.DOM.div({onClick: this._handleBadCaptionsContainerClick, className: 'bad-captions ' + (has_bad_comments ? '' : 'nodisplay')},
                    React.DOM.a({onClick: this.toggleShowingBadComments, href: 'javascript:void(0)', className: 'link'}, (this.state.bad_comments ? 'hide' : 'show') + ' bad replies'),
                    React.DOM.div({className: (this.state.bad_comments ? '' : 'nodisplay')},
                        bad_comments
                    )
                ),
                showMoreMarkup
            )
        );
    }
});
