Namespace('Imgur.Fullscreen');

Imgur.Fullscreen = Class.extend({
    init : function(params) {
        this._ = $.extend({
            signed  : Imgur.getInstance()._.signed,
            el      :
                {
                    $fullscreenBox					: $('.fullscreenBox'),
                    $fullscreenLink					: $('.album-fullscreen-link'),
                    $userActionLinks                : $('.userActionLinks'),
                    $embed                          : $('#fullscreenEmbed')
                }
        }, params);

        if(window.fullScreenApi && fullScreenApi.supportsFullScreen && this._.el.$embed.length) {

            if(this._.signed) {
                this._.el.$fullscreenBox.hide();

                this._.el.$fullscreenBox    = $('.fullscreenAlbumOptions');
                this._.el.$fullscreenLink   = $('.fullscreenAlbumOptions .album-fullscreen-link');
            }

            if(this._.el.$fullscreenLink.length) {
                this._.el.$fullscreenLink.unbind('click');
                this._.el.$fullscreenLink[0].removeEventListener('click', this.viewFullscreen);
                this._.el.$fullscreenLink[0].addEventListener   ('click', this.viewFullscreen, true );
            }

            // Fit the fullscreen link in the box.
            if(this._.el.$userActionLinks.hasClass('vertical')) {
                this._.el.$userActionLinks.css({
                    'line-height'   : '22px',
                    'padding'       : '8px',
                    'font-size'     : '13px'
                });
            }

            this._.el.$embed.attr('src','');

            this._.el.$fullscreenBox.show();
        }
    },

	viewFullscreen : function() {
		var self 		 = Imgur.Fullscreen.getInstance();
		var embed        = self._.el.$embed;
        var embedElement = embed[0];

        // Try again if DOM not ready..
        if(!self._.el.$embed.length) {
            setTimeout(self.viewFullscreen, 80);

        } else if(!self._.el.$embed.attr('src') || !self._.el.$embed.attr('src').length) {
            // Initial load, make a request for the embed album.
            self._.el.$embed.attr('src','/a/'+Imgur.Album.getInstance()._.id+'/embed');
            $(self._.el.$embed[0].contentDocument).ready(self.viewFullscreen);
            humanMsg.displayMsg('Starting fullscreen mode...');
            return;
        }

		self._.el.$embed.show();
		window.fullScreenApi.requestFullScreen(embedElement);

		setTimeout( self.focusOverlayAfterOpen, 99);

        var checkIfFullscreen = setInterval( function() {
            self.showOverlayIfOpen(checkIfFullscreen);
        }, 100);

        var checkIfExitedFullscreen = setInterval( function() {
            self.closeOverlayIfExited(checkIfExitedFullscreen);
        }, 100);
	},

    focusOverlayAfterOpen : function() {
        var self = Imgur.Fullscreen.getInstance();

        if (fullScreenApi.isFullScreen()) {

            self._.el.$embed[0].contentWindow.focus();
            $(self._.el.$embed[0].contentWindow).keydown(function(e){
                if(e.which==27) {
                    self._.el.$embed.hide();
                }
                e.stopPropagation();
            });
        } else {
            window.fullScreenApi.requestFullScreen(self._.el.$embed[0]);
            setTimeout(self.focusOverlayAfterOpen, 200);
        }
    },

    showOverlayIfOpen : function(intervalID) {
        var self = Imgur.Fullscreen.getInstance();

        // Show if it's still hidden for some reason
        if(!self._.el.$embed.is(':visible')) {
            self._.el.$embed.css({
                display     : 'block',
                opacity     : '100%'
            });
        } else {
            clearInterval(intervalID);
        }
    },

    closeOverlayIfExited : function(intervalID) {
        var self = Imgur.Fullscreen.getInstance();
        // Close if it's still open for some reason

        if (!fullScreenApi.isFullScreen()) {
            if(self._.el.$embed.is(':visible')) {
                self._.el.$embed.css({
                    display     : 'none'
                });
            } else {
                clearInterval(intervalID);
            }
        }
    }

});

Class.addSingleton(Imgur.Fullscreen);