Namespace('Imgur.Tag.Store');

Imgur.Tag.Store.CurrentImage = Class.extend({
    init: function(emitter) {
        this._ = {
            emitter: emitter,
            currentImage: null
        };
    },

    setCurrentImage: function(image) {
        this._.currentImage = image;
        this._.emitter.trigger('current image updated');
    },

    getCurrentImage: function() {
        return this._.currentImage;
    },

    getCurrentImageId: function() {
        return this._.currentImage ? this._.currentImage.hash : null;
    }
});
