Imgur.Elements.Copy = React.createClass({
    displayName: 'Copy',

    getDefaultProps: function() {
        return {
            inputClass: ''
        };
    },

    copy: function() {
        var value = this.refs['value'];
        value.select();
        
        try {
            document.execCommand('copy');
            window.getSelection().removeAllRanges();
        } catch(e) {}

        flashElement($(value), 100, null);
    },

    selectAll: function() {
        ReactDOM.findDOMNode(this.refs.value).select();
    },

    render: function() {
        var copyButton;
        var inputClasses = this.props.inputClass;

        if (document.queryCommandSupported('copy')) {
            copyButton = React.DOM.button({onClick: this.copy, className: this.props.copyClass}, 'Copy');
        } else {
            copyButton = React.DOM.button({className: this.props.copyClass + ' copy-button-unsupported'});
            inputClasses += ' copy-input-unsupported';
        }

        inputClasses += ' copyflash';
        
        return React.DOM.div({className: 'copywrapper'},
            React.DOM.input({type: 'text', value: this.props.value, ref: 'value', className: inputClasses, onClick: this.selectAll, readOnly: true}),
            copyButton
        );
    }
});