Namespace('Imgur.Elements');

Imgur.Elements.Combobox = React.createClass({
    displayName: 'Combobox',

    propTypes: {
        name:     React.PropTypes.string.isRequired,
        value:    React.PropTypes.string.isRequired,
        onChange: React.PropTypes.func.isRequired
    },

    getInitialState: function() {
        return {
            value: this.props.value,
        };
    },

    componentDidMount: function() {
        $(this.refs['base']).combobox({
            onChange: _.bind(function(element, value) {
                switch(element) {
                    case this.props.name:
                        var text = $(this.refs['base']).find('.selection').text();
                        this.props.onChange(text, value);
                        this.setState({
                            value: text
                        });
                        break;
                }
            }, this)
        });
    },

    render: function() {
        return React.DOM.div({id: this.props.name, className: 'combobox ' + this.props.className, ref: 'base', name: this.props.name},
            React.DOM.div({className: 'selection' + ((this.state.value == this.props.defaultValue) ? ' default' : ' green')}, this.state.value),
            React.DOM.div({className: 'triangle'}, ''),
            React.DOM.div({className: 'options'},
                (this.props.defaultValue ? React.DOM.div({className: 'combobox-header'}, this.props.defaultValue) : ''),
                _.map(this.props.values, _.bind(function(value) {
                    return React.DOM.div({className: 'item ' + ((this.state.value == value.label) ? 'selected' : ''), 'data-value': value.value, 'key': value.label}, value.label);
                }, this))
            )
        );
    }
});
