Namespace('Imgur.Tag');

Imgur.Tag.Group = Class.extend({
    init: function($container, list) {
        this._ = {
            list: list,
            el: {
                $container: $container,
                $name: $container.find('.name')
            }
        };
    },

    getId: function() {
        return this.getIdFrom$Container(this._.el.$container);
    },

    getList: function() {
        return this._.list;
    },

    setList: function(list) {
        this._.list = list;
    },

    getName: function() {
        return this._.el.$name.text();
    },

    get$Container: function() {
        return this._.el.$container;
    },

    get$Name: function() {
        return this._.el.$name;
    },

    setName: function(name) {
        this._.el.$name.text(name);
    },

    add: function(tag) {
        this._.list.add(tag);
    },

    contains: function(tag) {
        return this._.list.contains(tag);
    },

    containsBy: function(predicate) {
        return this._.list.containsBy(predicate);
    },

    findFirstTagBy: function(predicate) {
        return this._.list.findFirstBy(predicate);
    },

    getIdFrom$Container: function($container) {
        return $container.children('div').attr('value');
    },
    
    getTypeFrom$Container: function($container) {
        return $container.children('div').attr('data-type');
    },
    
    equals: function(other) {
        return other === this.getId();
    },
    
    insertBefore: function(el) {
        this.get$Container().before(el.get$Container());
    }
});
