Namespace('Imgur.Tag');

Imgur.Tag.AutocompleteComponent = React.createClass({
    displayName: 'Tag-AutocompleteComponent',

    getInitialState: function() {
        return {
            hidden: '',
        };
    },
    
    getDefaultProps: function() {
        return {
            KEYCODES: {
                KEY_T           : 84,
                KEY_ESC         : 27,
                KEY_DOWN_ARROW  : 40,
                KEY_RIGHT_ARROW : 39,
                KEY_UP_ARROW    : 38,
                KEY_ENTER       : 13,
                KEY_TAB         : 9
            }
        };
    },

    focus: function() {
        this.refs.query.focus();
    },
    
    moveCursorToEnd: function(el) {
        el.selectionStart = el.selectionEnd = el.value.length;
        el.style.display  = 'none';
        el.offsetHeight;
        el.style.display  = 'block';
        el.focus();
    },

    handleKeyDown: function(e) {
        //remove error tipsies
        $('.tipsy').remove();

        //If tag input is empty and user presses tab then they want to get out of box
        if (e.keyCode == 9 && this._getAcInfo().queryDisplayed != '') {
            e.preventDefault();
        }

        var key = {
            down_arrow  : (e.keyCode === this.props.KEYCODES.KEY_DOWN_ARROW),
            up_arrow    : (e.keyCode === this.props.KEYCODES.KEY_UP_ARROW),
            right_arrow : (e.keyCode === this.props.KEYCODES.KEY_RIGHT_ARROW),
            enter       : (e.keyCode === this.props.KEYCODES.KEY_ENTER),
            tab         : (e.keyCode === this.props.KEYCODES.KEY_TAB),
            modifier    : !!e.metaKey || !!e.ctrlKey || !!e.altKey
        };

        if (key.modifier) {
            return;
        }
        
        if(key.enter) {
            this.handleSubmit(e);
        }
        
        var suggestions = this.props.suggestionStore.getSuggestions();

        if (suggestions.length == 0) {
            suggestions = this.props.defaultTags || [];
        }

        if(suggestions.length) {
            var curIdx = this.props.getAutocompleteIdx();

            if(key.down_arrow || key.up_arrow) {
                var step = 0;

                if(key.down_arrow) {
                    step = 1;
                } else {
                    step = -1;
                }

                if((curIdx === 0 && key.up_arrow) || (curIdx === suggestions.length - 1 && key.down_arrow)) {
                    this.props.setComponentState({
                        selectionActive: false,
                        autocompleteIdx: null
                    });
                    return;
                }
                    
                if(curIdx == null) {
                    if(key.up_arrow) {
                        curIdx = suggestions.length - 1;
                    } else {
                        curIdx = 0;
                    }
                } else {
                    curIdx = (((curIdx + step) % suggestions.length) + suggestions.length) % suggestions.length;
                }

                this.props.setComponentState({
                    selectionActive: true,
                    autocompleteIdx: curIdx
                });
            } else if(key.right_arrow || key.tab) {
                var acText = suggestions[0].tag,
                    query  = this.refs.query;

                if(curIdx != null) {
                    acText = suggestions[curIdx].tag;
                } else if(typeof query.selectionEnd == "number" && query.selectionEnd == query.value.length) {
                    this.moveCursorToEnd(query);
                }

                this.props.suggestionStore.setQuery(acText);
                this.handleKeyPress(e);

                if(key.tab) {
                    this.props.setComponentState({
                        focusDue: true
                    });
                }
            }
        }
    },
    
    handleKeyPress: function(e) {
        //32 == space key which doesn't count toward width at the end of a span so fill it with a dummy character to get the actual width
        var temptext = this._getAcInfo().queryDisplayed + (e.which == 32 ? 'm' : String.fromCharCode(e.which));
        var temp     = $('<span />').text(temptext);
        $('body').append(temp);
        
        if(temp.width() > $(this.refs.query).width()) {
            this.setState({
                hidden: 'hidevis'
            });
        } else {
            this.setState({
                hidden: ''
            })
        }
        
        temp.remove();
    },
    
    handleChangedQuery: function(e) {
        this.props.suggestionStore.setValidationMode('query');
        this.props.suggestionStore.setQuery(e.target.value);

        this.props.setComponentState({
            selectionActive: false,
            autocompleteIdx: null
        });
    },
    
    handleSubmit: function(e) {
        e.preventDefault();

        this.props.suggestionStore.setValidationMode('submit');

        var acInfo = this._getAcInfo();
        var query = acInfo.queryDisplayed; // use the autosuggested query rather than the user's (possibly overridden) query

        var isValid = this.props.suggestionStore.getIsValidQuery(query);

        if(isValid) {
            this.props.onSubmit(query, e);
        }
    },
    
    focused: function() {
        this.props.setComponentState({
            focusDue: false
        });
    },
    
    _getAcInfo: function() {
        var query = this.props.suggestionStore.getQuery(),
            acText = '',
            suggestions = this.props.suggestionStore.getSuggestions();

        if (suggestions.length == 0) {
            suggestions = this.props.defaultTags || [];
        }

        if(suggestions.length && this.props.getAutocompleteIdx() != null) {
            acText = suggestions[this.props.getAutocompleteIdx()].tag;
        } else if(suggestions.length) {
            acText = suggestions[0].tag;
        }

        var prefix = this._longestCommonPrefix(acText, query);

        if(!prefix || (prefix != query && !this.props.getSelectionActive())) {
            acText = '';
        }

        return {
            acText: acText,
            queryDisplayed: (this.props.getAutocompleteIdx() != null && this.props.getSelectionActive()) ? acText : query
        };
    },
    
    /**
     * A bit naive
     */
    _longestCommonPrefix: function(alpha, beta) {
        //default tag has no beta
        if(!beta) {
            return alpha;
        }

        var i = Math.min(alpha.length, beta.length);

        while(i >= 0) {
            if(alpha.substr(0, i) == beta.substr(0, i)) {
                return alpha.substr(0, i);
            }

            i--;
        }

        return '';
    },
    
    componentDidUpdate: function() {        
        if(this.props.maybeErrorLabel || this.props.maybeAjaxErrorLabel) {
            $('.tipsy-error').tipsy({
                cls:     'error-tipsy',
                gravity: 'w',
                trigger: 'manual',
                title:   'data-title'
            }).tipsy('show');
        }
    },
    
    render: function() {
        return React.DOM.div({className: 'acContainer'},
            React.DOM.input({
                ref: 'query',
                onChange: this.handleChangedQuery,
                type: 'text',
                id: this.props.id ? this.props.id : 'tagbox',
                placeholder: this.props.placeholder,
                tabIndex: this.props.tabIndex,
                value: this._getAcInfo().queryDisplayed,
                onKeyDown: this.handleKeyDown,
                onKeyPress: this.handleKeyPress,
                autoComplete: 'off',
                'data-title': this.props.maybeErrorLabel ? this.props.maybeErrorLabel : (this.props.maybeAjaxErrorLabel ? this.props.maybeAjaxErrorLabel : ''),
                className: this.props.className + ((this.props.maybeErrorLabel || this.props.maybeAjaxErrorLabel) ? ' tipsy-error' : '')
            }),
            React.DOM.input({id: 'acText', type: 'text', autoComplete: 'off', readOnly: true, ref: 'hint', className: this.state.hidden, value: this._getAcInfo().acText})
        );
    }
});
