Namespace('Imgur.Tag.Controller');

Imgur.Tag.Controller.GroupSelectorLimits = Class.extend({
    init: function(emitter, group_selector, groupStore) {
        this._ = {
            emitter: emitter,
            groupSelector: group_selector,
            groupStore: groupStore
        };
    },

    _disableNew: function() {
        this._.groupSelector.disableNewGroupButton();
    },

    _enableNew: function() {
        this._.groupSelector.enableNewGroupButton();
    },

    handleGroupRemove: function(hash) {
        this._.groupSelector.removeUserGroup(hash);

        this._.groupStore.markDeleted(hash);

        if(this._.groupSelector.getRemainingUserGroups() > 0) {
            this._enableNew();
        }
    },

    handleGroupAdd: function(group) {
        this._.groupSelector.addUserGroup(group);

        this._.groupStore.addGroup({
            hash: group.getId(),
            name: group.getName(),
            account_id: Imgur.Environment.auth.id,
            account_url: Imgur.Environment.auth.url
        });

        if(this._.groupSelector.getRemainingUserGroups() <= 0) {
            this._disableNew();
        }
    }
});
