Namespace('Imgur.Share');

Imgur.Share.Title = React.createClass({
    displayName: 'Share-Title',

    getInitialState: function() {
        return {
            title       : this.props.title,
            titleLength : this.props.title.length,
        };
    },
    
    handleChange: function(e) {
        var targetLength = e.target.value.length;

        this.setState({
            title       : e.target.value,
            titleLength : targetLength
        });

        if (typeof this.props.handleChange === 'function') {
            var valid = false;

            if (targetLength > 0 && targetLength <= this.props.maxLength) {
                valid = true;
            }

            this.props.handleChange(valid);
        }
    },
    
    render: function() {
        var countClass = '';
        
        if(this.state.titleLength >= this.props.maxLength) {
            countClass = ' filled';
        } else if (this.state.titleLength > 0) {
            countClass = ' filling';
        }
        
        return React.DOM.div({},
            React.DOM.input({
                type: 'text',
                className: this.props.inputClasses,
                ref: 'title',
                name: 'title',
                placeholder: 'Give your post a title',
                value: this.state.title,
                autoComplete: 'off',
                maxLength: this.props.maxLength,
                tabIndex: 1,
                onChange: this.handleChange
            }),
            React.DOM.p({
                className: this.props.limitClasses + countClass
            }, '(' + this.state.titleLength + '/' + this.props.maxLength + ')')
        );
    }
});