Namespace('Imgur.History');
Imgur.History = Class.extend({
    init: function(params) {
        this._ = $.extend({
            popStates: {}
        }, params);

        this.onPopState();
    },

    onPopState: function() {
        var self = this;

        window.onpopstate = function(e) {
            try {
                self._.popStates[e.state].call(this, e);
            } catch(exception) { }
        };
    },

    addPopState: function(state, cb) {
        this._.popStates[state] = cb;
    },

    removePopState: function(state) {
        delete this._.popStates[state];
    },

    addWindowState: function(state, title, url) {
        if(window.history.replaceState) {
            if(!window.history.state) {
                window.history.replaceState(state, 'initial', window.location.href);
            }

            var previousState = {
                state: window.history.state,
                title: document.title,
                url: window.location.href
            };

            var newState = {
                state: state,
                title: title,
                url: url
            };

            window.history.pushState(state, title, url);

            var $event = $.Event('pushstate');
            $event.previousState = previousState;
            $event.currentState = newState;
            $(window).trigger($event);
        }
    }
});

Class.addSingleton(Imgur.History);
