Namespace('Imgur.Utilities.ColorboxConfirm');

Imgur.Utilities.ColorboxConfirm = Class.extend({
    init : function(params) {
        this._ = $.extend(true, {
            'el' : {
                '$confirmation' : $('.colorbox-confirm'),
                '$noConfirm'    : $('.colorbox-no-confirm'),
                'confirmed'     : 'colorbox-confirmed'
            }
        }, params);

        this.setupHandlers();
    },

    setupHandlers : function() {
        this._.el.$confirmation.unbind('click');
        this._.el.$noConfirm.unbind('click');

        var self = this;
        this._.el.$confirmation.click(function() {
            $('.' + self._.el.confirmed).val(true);
            $.colorbox.close();
        });

        this._.el.$noConfirm.click(function() {
            $.colorbox.close();
        });
    },

    show : function(target, cb) {
        var self = this;
        $.colorbox({
            href       : $(target),
            open       : true,
            inline     : true,
            top        : '15%',
            transition : 'none',
            scrolling  : false,
            onComplete : function() { self.onComplete(target); },
            onClosed   : function() { self.onClose(target, cb); }
        });
    },

    onComplete: function(target) {
        if(window.getSelection) {
            window.getSelection().removeAllRanges();
        } else if (document.selection) {
            document.selection.empty();
        }

        var $confirmed = $(target).children('.' + this._.el.confirmed);
        if(!$confirmed.length) {
            $confirmed = $('<input/>', {
                'type'  : 'hidden',
                'class' : this._.el.confirmed
            });

            $(target).append($confirmed);
        }

        $confirmed.val(false);
    },

    onClose : function(target, cb) {
        var $confirmed = $(target).children('.' + this._.el.confirmed);
        if($confirmed.val() !== 'false') { cb.call(); }
    }

});

Class.addSingleton(Imgur.Utilities.ColorboxConfirm);
