Namespace('Imgur.Promoted.Source.Adzerk');

Imgur.Promoted.Source.Adzerk = Imgur.Promoted.Source.extend({
    init: function(config, emitter) {
        this._ = {
            'blockedCreatives': [],
            'emitter': emitter,
            'espoused': false,
            'config': config,
            'user': {},
            'cookieOpts': {
                domain: '.'+Imgur.Environment.host,
                path: '/',
                expires: 365
            },
            'rgxDomain': /\/\/engine\.[^.]+\.net/,
            'pgxProto': /^[^:]+:\/\//,
            // our pixel IDs correllated to adzerk event IDs
            // these are in order of functionality, not id
            'evtToPx': {
                10 : 'ACTION_IMAGE_UPVOTE',
                11:  'ACTION_IMAGE_DOWNVOTE',
                22:  'ACTION_COMMENT',
                101: 'ACTION_COMMENT_REPLY',
                102: 'ACTION_COMMENT_UPVOTE',
                103: 'ACTION_COMMENT_DOWNVOTE',
                20:  'ACTION_FAVORITE',
                21:  'ACTION_SHARE',
                50:  'ACTION_SHARE_FACEBOOK',
                51:  'ACTION_SHARE_TWITTER',
                52:  'ACTION_SHARE_PINTEREST',
                53:  'ACTION_SHARE_REDDIT',
                54:  'ACTION_SHARE_EMAIL',
                104: 'VIEW_UNDERIMAGE',
                400: 'VIEW_TIME_0',
                401: 'VIEW_TIME_1',
                402: 'VIEW_TIME_2',
                403: 'VIEW_TIME_3',
                404: 'VIEW_TIME_4',
                405: 'VIEW_TIME_5',
                406: 'VIEW_TIME_6',
                407: 'VIEW_TIME_7',
                408: 'VIEW_TIME_8',
                409: 'VIEW_TIME_9',
                410: 'VIEW_TIME_10',
                415: 'VIEW_TIME_15',
                420: 'VIEW_TIME_20',
                425: 'VIEW_TIME_25',
                430: 'VIEW_TIME_30_PLUS'
            }
        };

        this._idFromCookie();

        return this._;
    },

    getPlacement: function(payload) {
        var reqData = {
            user: this._getUser(),
            placements: this._.config.adzerk.placementParms,
            blockedCreatives: this._.blockedCreatives,
            keywords: ['platform-desktop']
        };

        return $.ajax({
            url  : this._.config.adzerk.apiLocation,
            type : 'POST',
            contentType: 'application/json',
            data: JSON.stringify(reqData),
            dataType: 'json',
            success: _.bind(this._handleApiResponse, this),
            error: _.bind(this._handleApiError, this)
        });
    },

    _handleApiError: function(xhr, status, error) {
        console.log('adzerk get fail', arguments);
    },

    // This expects raw response data from the Adzerk API call.
    // will alter the passed object directly, but still passes it back
    _decodeEncodedResponse: function(dataset) {
        var dataRoot = 'contents.0.data.customData',
            decisions = Imgur.Util.dive(dataset, 'decisions'),
            iter, encs, encIter, encTmp;

        if('object' === typeof decisions) {

            for(iter in decisions) {

                if(decisions.hasOwnProperty(iter)) {
                    encs = Imgur.Util.dive(decisions[iter], dataRoot+'.encoded');

                    if(_.isArray(encs)) {

                        for(encIter = 0; encIter < encs.length; encIter++) {
                            encTmp = Imgur.Util.dive(decisions[iter], dataRoot+'.'+encs[encIter]);

                            if('string' === typeof encTmp) {
                                dataset.decisions[iter].contents[0].data.customData[encs[encIter]] = decodeURIComponent(encTmp);
                                dataset.decisions[iter].contents[0].data.customData[encs[encIter]+'_raw'] = encTmp;
                            }
                        }
                    }

                    encs = Imgur.Util.dive(decisions[iter], dataRoot+'.encodedObj');

                    if(_.isArray(encs)) {

                        for(encIter = 0; encIter < encs.length; encIter++) {
                            encTmp = Imgur.Util.dive(decisions[iter], dataRoot+'.'+encs[encIter]);

                            if('string' === typeof encTmp) {
                                dataset.decisions[iter].contents[0].data.customData[encs[encIter]] = JSON.parse(decodeURIComponent(encTmp));
                                dataset.decisions[iter].contents[0].data.customData[encs[encIter]+'_raw'] = encTmp;
                            }
                        }
                    }
                }
            }
        }

        return dataset;
    },

    // used to affirm the adzerk userdb id and other things leveraged via cookies for promoted posts
    // can just be blindly called
    _espouse: function() {
        if(this._.espoused) {
            return;
        }

        if(Imgur.Environment.auth) {
            $.ajax({
                type: 'GET',
                url: '/account/espouse',
                complete: _.bind(function () {
                    this._.espoused = true;
                }, this)
            });
        }
    },

    // get the adzerk userdb id from cookie freshly
    _idFromCookie: function() {
        var cookied = $.cookie(this._.config.userCookie);

        if(cookied) {
            this._.user.key = cookied;
        }
    },

    _handleApiResponse: function(data, status, xhr) {
        var conf = this._.config.adzerk,
            placeObj = null,
            thumb = Imgur.Util.dive(data, 'decisions.'+conf.placementIds.thumb),
            page = Imgur.Util.dive(data, 'decisions.'+conf.placementIds.page),
            companion = Imgur.Util.dive(data, 'decisions.'+conf.placementIds.companion),
            customPx;

        if(typeof data === 'object' && typeof data.user === 'object' && data.user) {
            this._setUserFromResponse(data);
        }

        if(typeof data === 'object' && typeof data.decisions === 'object' && data.decisions) {
            data = this._decodeEncodedResponse(data);
            placeObj = new Imgur.Promoted.Post(this._.emitter);

            if(companion) {
                // detects "native" vs "adzerk hosted" companions and formats a standard object
                compFormatted = this._formatCompanionData(companion);

                placeObj.addCompanionPlacement(compFormatted);
                placeObj.addPixel('CLICK_COMPANION', this._cleanPx(companion.clickUrl));
                placeObj.addPixel('IMPRESSION_COMPANION', this._cleanPx(companion.impressionUrl));
            }

            if(page) {
                placeObj.addPagePlacement(page);
                placeObj.addPixel('IMPRESSION_POST', this._cleanPx(page.impressionUrl));
            }

            if(thumb) {
                adData = Imgur.Util.dive(thumb, 'contents.0.data.customData');
                customPx = Imgur.Util.dive(thumb, 'contents.0.data.customData.customPx');

                if(adData) {
                    placeObj.addThumbPlacement(thumb);
                    placeObj.addPixel('CLICK_THUMB', this._cleanPx(thumb.clickUrl));
                    placeObj.addPixel('IMPRESSION_THUMB', this._cleanPx(thumb.impressionUrl));
                    placeObj.setData(adData);
                    this._.blockedCreatives.push(thumb.creativeId);

                    if('object' === typeof(thumb.events)) {
                        this._addEventPixels(placeObj, thumb.events);
                    }

                    if('object' === typeof(customPx)) {
                        this._addCustomPixels(placeObj, customPx);
                    }
                }
            }

            this._.emitter.trigger('gotPromoted', placeObj);
        } else {
            console.warn('Empty or invalid promoted response.', arguments);
        }
    },

    _cleanPx: function(pxUrl) {
        if('string' === typeof pxUrl) {
            if(this._.rgxDomain.test(pxUrl)) {
                pxUrl = pxUrl.replace(this._.rgxDomain, 'https://zrk.'+Imgur.Environment.host);
            }

            if(this._.pgxProto.test(pxUrl)) {
                pxUrl = pxUrl.replace(this._.pgxProto, '//');
            }
        }

        return pxUrl;
    },

    _addCustomPixels: function(placeObj, events) {
        var iter, len;

        if('object' === typeof placeObj && _.isArray(events)) {
            len = events.length;

            for(iter = 0; iter < len; iter++) {
                // get the right stuff back?
                if('undefined' !== typeof events[iter].id && 'undefined' !== typeof events[iter].url) {
                    // is this an event we care about?
                    if('string' === typeof events[iter].id) {
                       placeObj.addPixel(events[iter].id, this._cleanPx(events[iter].url));
                    }
                }
            }
        }
    },

    _addEventPixels: function(placeObj, events) {
        var iter, len;

        if('object' === typeof placeObj && _.isArray(events)) {
            len = events.length;

            for(iter = 0; iter < len; iter++) {
                // get the right stuff back?
                if('undefined' !== typeof events[iter].id && 'undefined' !== typeof events[iter].url) {
                    // is this an event we care about?
                    if('string' === typeof this._.evtToPx[events[iter].id]) {
                       placeObj.addPixel(this._.evtToPx[events[iter].id], this._cleanPx(events[iter].url));
                    }
                }
            }
        }
    },

    _setUserFromResponse: function(responseData) {

        if(typeof responseData === 'object' && typeof responseData.user === 'object' && responseData.user) {
            this._.user = _.extend({}, responseData.user);

            // let the possibly cookied ID get applied to the user object
            this._idFromCookie();

            // reaffirm or set the possibly new ID
            $.cookie(this._.config.userCookie, this._.user.key, this._.cookieOpts);

            // make a call to our back-end so the userdb id can persist for logged in users
            // note that this may actually change the userdb id for someone who recently logged in
            this._espouse();
        }
    },

    _getUser: function() {
        // re-affirm cookie just in case it's changed
        this._idFromCookie();

        return this._.user;
    },

    _formatCompanionData: function(compData) {
        var compType = Imgur.Util.dive(compData, 'contents.0.data.customData.adType'),
            ret = {
                'type': compType,
                'raw': compData,
                'html': '',
            },
            imgSrc, link, title, html;

        ret.raw = compData;

        switch(compType) {
            case 'hash':
            case 'native':
                imgSrc = Imgur.Environment.cdnUrl+'/'+Imgur.Util.dive(compData, 'contents.0.data.customData.hash')+'.jpg';
                link = Imgur.Util.dive(compData, 'contents.0.data.customData.link');
                title = Imgur.Util.dive(compData, 'contents.0.data.customData.title');

                ret.html = '<a href="'+link+'" rel="nofollow" target="_blank" title="'+title+'"><img src="'+imgSrc+'" title="'+title+'" alt="'+title+'" border="0" width="300" height="250"></a>';
                break;

            case 'html':
                ret.html = Imgur.Util.dive(compData, 'contents.0.data.customData.html');
                break;

            default:
                html = Imgur.Util.dive(compData, 'contents.0.body');

                if(html) {
                    ret.html = html;
                }
                break;
        }

        return ret;
    }

});
