Namespace('Imgur.Tag');

Imgur.Tag.Model = Class.extend({
    /**
     * Primary tags: list of string; tag names to automatically exclude from success cb
     */
    init: function(ajax, tagProvider) {
        this._ = {
            ajax: ajax,
            tagProvider: tagProvider,
            queryNonce: 0,
            pendingRequest: null
        };
    },

    cancel: function() {
        this._.queryNonce++;

        if(null != this._.pendingRequest) {
            this._.pendingRequest.abort();
        }
    },

    fetch: function(queryContext, url, cb, data) {
        this.cancel();
        this._.pendingRequest = this._.ajax(url, data, _.bind(this._handleResponse, this, this._.queryNonce, queryContext, cb || _.noop), _.bind(this._handleError, this, queryContext));
    },

    responseWasSuccessful: function(response) {
        return !!response.success;
    },

    _handleResponse: function(nonce, queryContext, success_callback, response) {
        this._.pendingRequest = null;

        if(nonce < this._.queryNonce) {
            // nonce was either cancelled or another fetch is happening. 
            // only proceed with the latest request.
            return;
        }

        this._.queryNonce++;
        if(response && this.responseWasSuccessful(response)) {
            success_callback(queryContext, this._handleResponseSuccess(queryContext, response));
        }
    },

    _handleError: function(queryContext) {
        throw 'abstract';
    },

    _handleResponseSuccess: function(queryContext, response) {
        throw 'abstract';
    }
});
