Namespace('Imgur.Caption');

Imgur.Caption = Imgur.extend({
    sid : $('#sid').val(),

    init : function(params) {
        this._ = $.extend({
            sid                 : Imgur.getInstance()._.sid,
            auth_id             : Imgur.Environment.auth.id,
            signed              : Imgur.Environment.signed,
            baseURL             : '/gallery',
            hash                : null,
            popEventState       : 'comment',

            el                      : {
                $captions        : $('#captions')
            },
            $captions           : $('#captions'),

            children            : 'children',
            counterClass        : '.counter',
            captionBarClass     : 'caption-bar',

            commentName         : 'comment',

            userCommentsClass   : '.comment-item',

            mobile              : false,
            showRepliesCount    : false,
            captions            : { } // map hash -> { 'set': map id -> caption, 'parent': map id -> parent_id, 'votes': map caption id -> vote }
        }, params);

        this._super(this._);
    },

    setupHandlers : function() {
        Imgur.ShareButtons.getInstance().bindButtons();
    },

    addDownvoteLimit : function() {
        clearTimeout(this._.timeout);

        $('.caption .arrow.down').unbind('click');
        $('.caption .arrow.down').attr('title', 'Downvoting on this image has been disabled for one minute because you made a comment.');

        $('.caption .arrow.down').tipsy({
            'gravity' : 'nw',
            'opacity' : 1
        });
    },

    removeDownvoteLimit : function() {
        var self = this;

        $('.caption .arrow').unbind('click');
        $('.caption .arrow.down').unbind('hover');
        $('.tipsy').remove();

        $('.caption .arrow.down').attr('title', 'dislike');
        $('.caption .arrow').click(function(e) { self.vote(e); });
    },

    haveCaptionVotes : function(hash) {
        return (hash in this._.captions && 'votes' in this._.captions[hash]);
    },

    haveCaptions : function(hash) {
        return (hash in this._.captions && 'set' in this._.captions[hash]);
    },

    deleteCaption: function(hash, caption_id) {
        if(!this.haveCaptions(hash)) {
            return;
        }

        if(!(caption_id in this._.captions[hash].set)) {
            return;
        }

        this._.captions[hash].set[caption_id].deleted = true;
        this._.captions[hash].set[caption_id].just_deleted = true;
    },

    undeleteCaption: function(hash, caption_id) {
        this._.captions[hash].set[caption_id].deleted = false;
        this._.captions[hash].set[caption_id].just_deleted = false;
    },

    getCaptionDepthById : function(hash, caption_id) {
        // walk the parent relation upwards
        caption_id = Number(caption_id);
        if(this.haveCaptions(hash) && caption_id in this._.captions[hash].set) {
            var visited = []; // detect circular relations
            var level = 0;
            var current_id = caption_id;

            while(current_id != null && !(current_id in visited)) {
                visited.push(current_id);

                var parent = this._.captions[hash].set[current_id];

                if(parent && parent.hasOwnProperty('depth')) {
                    // this can be false if e.g. the parent becomes spam
                    return level + parent.depth;
                }

                current_id = this._.captions[hash].parent[current_id];
                level++;
            }

            return level-1;
        }
    },

    countComments: function(hash) {
        if(this.haveCaptions(hash)) {
            return Object.keys(this._.captions[hash].set).length;
        }

        return null;
    },

    getCaptionRepliesById: function(hash, caption_id) {
        caption_id = Number(caption_id);

        if(this.haveCaptions(hash) && caption_id in this._.captions[hash].set) {
            return _.filter(_.values(this._.captions[hash].parent), function(parent_id) {
                return parent_id === caption_id
            }).length;
        }
    },

    getCaptionById : function(hash, caption_id) {
        caption_id = Number(caption_id);
        if(this.haveCaptions(hash) && caption_id in this._.captions[hash].set) {
            return this._.captions[hash].set[caption_id];
        }
        return null;
    },

    getCaptionPropertyById : function(hash, caption_id, property) {
        caption_id = Number(caption_id);
        if(this.haveCaptions(hash) && caption_id in this._.captions[hash].set) {
            if(property in this._.captions[hash].set[caption_id]) {
                return this._.captions[hash].set[caption_id][property];
            }
        }
        return null;
    },

    getCaptionParentById : function(hash, caption_id) {
        caption_id = Number(caption_id);
        if(this.haveCaptions(hash) && caption_id in this._.captions[hash].parent) {
            return this._.captions[hash].parent[caption_id];
        }
        return null;
    },

    getParentIds : function(hash) {
        if(this.haveCaptions(hash)) {
            return _.uniq(_.values(this._.captions[hash].parent));
        } else {
            return [];
        }
    },

    getCaptionChildrenIds : function(hash, caption_id) {
        caption_id = caption_id;

        if(!this.haveCaptions(hash)) {
            return [];
        }

        if(!(caption_id in this._.captions[hash].children)) {
            return [];
        }

        return this._.captions[hash].children[caption_id];
    },

    getCaptionIdsByFilter : function(hash, filter) {
        if(!this.haveCaptions(hash)) {
            return [];
        }
        var accum = [];
        jQuery.each(this._.captions[hash].set, function(caption_id, caption) {
            if(filter(caption)) {
                accum.push(caption_id);
            }
        });
        return accum;
    },

    mergeCaptions : function(hash, captions, callback) {
        var self = this;
        if (!(hash in this._.captions)) {
            self._.captions[hash] = {'set': {}, 'parent': {}, 'children': {}, 'votes': {}};
        } else if (!('set' in this._.captions[hash])) {
            //Don't overwrite votes object
            self._.captions[hash].set = {};
            self._.captions[hash].parent = {};
            self._.captions[hash].children = {};
        }
        jQuery.each(captions, function(i, caption) {
            caption.parent_id = Number(caption.parent_id);
            caption.id = Number(caption.id);
            caption.points = Number(caption.points);
            // points determines whether it is in bad replies, but visible points is what the user sees
            caption.visible_points = Number(caption.points);

            if(caption.parent_id === 0) {
                caption.parent_id = null;
            }
            
            self._.captions[hash].set[caption.id] = caption;
            self._.captions[hash].parent[caption.id] = caption.parent_id;

            if(!(caption.parent_id in self._.captions[hash].children)) {
                self._.captions[hash].children[caption.parent_id] = [];
            }

            if(caption.just_inserted) {
                self._.captions[hash].children[caption.parent_id].unshift(caption.id);
            } else {
                self._.captions[hash].children[caption.parent_id].push(caption.id);
            }
        });

        if(callback) {
            callback();
        }
    },

    mergeCaption : function(hash, caption) {
        this.mergeCaptions(hash, [caption]);
    },

    /*
     * Store the initial vote information. Use setVote to update the vote in tandem with the points
     */
    mergeVotes : function(hash, votes, callback) {
        var self = this;
        if(votes) {
            if(!this.haveCaptionVotes(hash)) {
                if(hash in this._.captions) {
                    this._.captions[hash].votes = {};
                } else {
                    this._.captions[hash] = {'parent': {}, 'votes': {}};
                }
            }
            jQuery.each(votes, function(i, vote) {
                self._.captions[hash].votes[vote.caption_id] = vote.vote;
            });
            if(callback) {
                callback();
            }
        }
    },

    getVotes: function(hash) {
        if(this.haveCaptionVotes(hash)) {
            return this._.captions[hash].votes;
        }

        return {};
    },

    /**
     * Store the vote information, and update visible points with respect to the vote that may have been on the image before
     */
    setVote: function(hash, comment_id, vote) {
        var old_vote = this._.captions[hash].votes[comment_id];

        if(old_vote === undefined || old_vote === null) {
            old_vote = 'veto';
        }

        this._.captions[hash].votes[comment_id] = vote;

        var points = this._.captions[hash].set[comment_id].visible_points;

        var vote_points = {
            'up': 1,
            'veto': 0,
            'down': -1
        };

        var points_before_original_vote = points - vote_points[old_vote];
        var new_points = points_before_original_vote + vote_points[vote];

        this._.captions[hash].set[comment_id].visible_points = new_points;
    },

    getVoteByCaptionId : function(hash, caption_id) {
        if(this.haveCaptionVotes(hash) && caption_id in this._.captions[hash].votes) {
            return this._.captions[hash].votes[caption_id];
        }

        return null;
    },

    isCaptionOrItsSubtreeNSFW: function(id) {
        var captions = this._.captions[this._.hash].set;

        if(captions.hasOwnProperty(id)) {
            var caption = captions[id];

            var childrenNsfw = _.filter(Object.keys(this._.captions[this._.hash].parent), _.bind(function(caption_id) {
                // do not trust any comment with a negative score
                if (this._.captions[this._.hash].set[caption_id].points < 0) {
                    return false;
                }

                // do not warn if in mature topic
                if (Imgur.InsideNav.getInstance()._.topic === 'Mature') {
                    return false;
                }

                return this._.captions[this._.hash].parent[caption_id] === id && this._.captions[this._.hash].set[caption_id].caption.match(/nsfw/gi);
            }, this));

            return (caption.caption.match(/nsfw/gi) || childrenNsfw.length);
        }

        return false;
    },

    setCaptionVoteState : function(vote) {
        var directions = ['up', 'down'];
        var direction   = vote.vote;
        var $vote_btns  = $(document.getElementById('caption-' + vote.caption_id)).find(".arrows *");
        if(direction == 'veto') {
            $vote_btns.removeClass('pushed');
        } else {
            var other_direction = directions[1 - directions.indexOf(direction)];
            var $up  = $vote_btns.filter('.up');
            var $down = $vote_btns.filter('.down');
            var $vote_btn = $vote_btns.filter('.' + direction);
            var $other_vote_btn = $vote_btns.filter('.' + other_direction);

            if(!$vote_btn.hasClass('pushed')) {
                $vote_btn.addClass('pushed');
            }
            $other_vote_btn.removeClass('pushed');
        }
        return true;
    },

    /**
     * Delete all the caption data not given
     */
    purgeCaptionData : function(retain_hashes) {
        var self = this;
        var delete_keys = Object.keys(this._.captions).filter(function(key) { 
            return Object.hasOwnProperty.call(self._.captions, key) && retain_hashes.indexOf(key) === -1; 
        });
        jQuery.each(delete_keys, function(i, key) {
            delete self._.captions[key];
        });
    }

});

Class.addSingleton(Imgur.Caption);
