Namespace('Imgur.Tag');

Imgur.Tag.SuggestModel = Imgur.Tag.Model.extend({
    init: function(ajax, tagProvider, autoSuggestUrl) {
        this._super(ajax, tagProvider);
        this._.autoSuggestUrl = autoSuggestUrl;
    },
    
    fetch: function(group_id, query, cb, chosen_cb) {
        this._super([group_id, query, chosen_cb], this._.autoSuggestUrl + '/tags/_suggest', cb, JSON.stringify({
            'tags' : {
                'text' : query,
                'completion' : {
                    'field' : 'name_suggest',
                    'size': 10,
                    "fuzzy" : {
                        "edit_distance" : 1
                    }
                }
            }
        }));
    },

    responseWasSuccessful: function(response) {
        return response._shards.successful > 0;
    },

    _handleResponseSuccess: function(queryContext, response) {
        var tags = [];

        var group_id = queryContext[0],
            query = queryContext[1],
            clicked_cb = queryContext[2];

        _.each(response.tags[0].options, _.bind(function(tag_obj, key) {
            tags.push(this._.tagProvider(clicked_cb, group_id, tag_obj.text, tag_obj.payload.subscribed));
        }, this));

        return tags;
    },

    toString: function() {
        return '[tag suggestion model]';
    }
});
