Namespace('Imgur.Tag.Store');

Imgur.Tag.Store.Group = Class.extend({
    init: function(emitter, groups) {
        this._ = {
            emitter: emitter,
            groups: groups || {}
        };
    },

    getGroups: function() {
        return _.filter(_.values(this._.groups), function(group) {
            return !group.deleted;
        });
    },

    getGroup: function(hash) {
        return this._.groups[hash];
    },

    haveGroup: function(hash) {
        return this._.groups.hasOwnProperty(hash) && !this._.groups[hash].deleted;
    },

    addGroup: function(group) {
        this._.groups[group.hash] = group;

        this._.emitter.trigger('new group added');
    },

    removeGroup: function(hash) {
        if(this._.groups.hasOwnProperty(hash)) {
            delete this._.groups[hash];
        }

        this._.emitter.trigger('group deleted');
    },

    markDeleted: function(hash) {
        if(this._.groups.hasOwnProperty(hash)) {
            this._.groups[hash].deleted = true;
        }

        this._.emitter.trigger('group deleted');
    }
});
