Namespace('Imgur.Promoted.Events');

Imgur.Promoted.Events = Class.extend({
    init: function(config, emitter, insideNav) {
        this._ = {
            'ele': {
                'clicks': {
                    'ACTION_IMAGE_UPVOTE':     '.post-action-upvote:not(.green)',
                    'ACTION_IMAGE_DOWNVOTE':   '.post-action-downvote:not(.red)',
                    'ACTION_FAVORITE':         '.post-action-favorite:not(.green)', // add unfavorite variant? ask AZ for this?
                    'ACTION_SHARE':            '.social-icon',
                    'ACTION_SHARE_FACEBOOK':   '.social-icon.icon-f',
                    'ACTION_SHARE_TWITTER':    '.social-icon.icon-twttr',
                    'ACTION_SHARE_PINTEREST':  '.social-icon.icon-pin',
                    'ACTION_SHARE_REDDIT':     '.social-icon.icon-reddit',
                    'CLICK_COMPANION':         '#right-content .advertisement .promoCompanion a img'
                }
            },
            'config': config,
            'emitter': emitter,
            'inside': insideNav
        };
    },

    // pass boolean true to unbind events
    bindAll: function(off) {
        // Trying to variableize this._.emitter.(on|off)
        // freaked the emitter object the fuck out.
        // Long conditional it is!
        if (off) {
            this._.emitter.off('captionVoted', this, this._handleCommentVote);
            this._.emitter.off('captionSubmit', this, this._handleCommentSubmit);
            this._.emitter.off('vwTime', this, this._handleVwTime);
            this._.emitter.off('vwUnderImage', this, this._handleVwUnderImage);
        } else {
            this._.emitter.on('captionVoted', this, this._handleCommentVote);
            this._.emitter.on('captionSubmit', this, this._handleCommentSubmit);
            this._.emitter.on('vwTime', this, this._handleVwTime);
            this._.emitter.on('vwUnderImage', this, this._handleVwUnderImage);
        }

        this.bindClicks(off);
    },

    // pass boolean true to unbind events
    bindClicks: function(off) {
        var type = off? 'off': 'on',
            func, iter;

        for(iter in this._.ele.clicks) {
            if (this._.ele.clicks.hasOwnProperty(iter)) {
               $(this._.ele.clicks[iter])[type]('click', null, {promoEvent: iter}, _.bind(this._fireEvent, this));
            }
        }
    },

    _handleVwTime: function(hash, ms) {
        // subEv is our faked "event"
        var subEv = {
            hash: this.getSideImage(hash),
            data: {
                sec: Math.floor(ms / 1000),
                promoEvent: 'VIEW_TIME_0'
            }
        };

        if (subEv.data.sec <= 10) {
            subEv.data.promoEvent = 'VIEW_TIME_'+subEv.data.sec;
        } else if (subEv.data.sec <= 14) {
            subEv.data.promoEvent = 'VIEW_TIME_10';
        } else if (subEv.data.sec <= 19) {
            subEv.data.promoEvent = 'VIEW_TIME_15';
        } else if (subEv.data.sec <= 24) {
            subEv.data.promoEvent = 'VIEW_TIME_20';
        } else if (subEv.data.sec <= 29) {
            subEv.data.promoEvent = 'VIEW_TIME_25';
        } else {
            subEv.data.promoEvent = 'VIEW_TIME_30_PLUS';
        }

        this._fireEvent(subEv);
    },

    _handleVwUnderImage: function(hash, pos, tPos) {
        // subEv is our faked "event"
        var subEv = {
            hash: this.getSideImage(hash),
            data: {
                promoEvent: 'VIEW_UNDERIMAGE'
            }
        };

        this._fireEvent(subEv);
    },

    _handleCommentSubmit: function(commentObj) {
        // subEv is our faked "event"
        var subEv = {
            comment: commentObj,
            data: {
                promoEvent: 'ACTION_COMMENT'
            }
        };

        if (commentObj.parent_id) {
            subEv.data.promoEvent = 'ACTION_COMMENT_REPLY';
        }

        this._fireEvent(subEv);
    },

    _handleCommentVote: function(voteObj, direction) {
        // voteEv is our faked "event"
        var voteEv = {
            comment: voteObj,
            data: {
                promoEvent: null
            }
        };

        if (direction === 'veto') {
            // don't record vetos
            return;
        }

        if (direction === 'up') {
            voteEv.data.promoEvent = 'ACTION_COMMENT_UPVOTE';
        } else {
            voteEv.data.promoEvent = 'ACTION_COMMENT_DOWNVOTE';
        }

        this._fireEvent(voteEv);
    },

    getSideImage: function(hash) {
        return this._.inside._.sideGallery.getImage(hash);
    },

    _fireEvent: function(ev) {
        this._.emitter.trigger('promoEvent', ev);
    }

});

