Namespace('Imgur.Tag');

Imgur.Tag.GroupModel = Class.extend({
    init: function(post, tagGroupStore, emitter) {
        this._ = {
            post          : post,
            tagGroupStore : tagGroupStore,
            emitter       : emitter
        };
    },

    unsubscribe: function(group_id, tag_id) {
        this._.tagGroupStore.removeTagFromGroup(tag_id, group_id);

        var success = _.bind(function() {
            this._.tagGroupStore.setFailedTag('');
            this._.tagGroupStore.pushHistory(tag_id, group_id, this._.tagGroupStore.ACTIONS.REMOVED);
            this._.emitter.trigger('new tagset');
        }, this);

        var fail = _.bind(function() {
            this._.tagGroupStore.setFailedTag('Failed to unsubscribe from ' + tag_id);
            this._.tagGroupStore.addTagToGroup(tag_id, group_id);
            this._.tagGroupStore.pushHistory(tag_id, group_id, this._.tagGroupStore.ACTIONS.ERROR);
            humanMsg.displayMsg('Failed to unfollow tag', 'error-msg');
        }, this);

        this._.post('/account/groups/unsubscribe', {
            group: group_id,
            tag: tag_id
        }, success, fail);
    },

    subscribe: function(group_id, tag_id) {
        this._.tagGroupStore.addPendingTag(tag_id, group_id);

        var success = _.bind(function() {
            this._.tagGroupStore.addTagToGroup(tag_id, group_id);
            this._.tagGroupStore.removePendingTag(tag_id, group_id);
            this._.tagGroupStore.setFailedTag('');
            this._.tagGroupStore.pushHistory(tag_id, group_id, this._.tagGroupStore.ACTIONS.ADDED);
            this._.emitter.trigger('new tagset');
        }, this);

        var fail = _.bind(function() {
            this._.tagGroupStore.setFailedTag('Failed to subscribe to ' + tag_id);
            this._.tagGroupStore.removePendingTag(tag_id, group_id);
            this._.tagGroupStore.pushHistory(tag_id, group_id, this._.tagGroupStore.ACTIONS.ERROR);
            humanMsg.displayMsg('Failed to follow tag', 'error-msg');
        }, this);

        this._.post('/account/groups/subscribe', {
            group: group_id,
            tag: tag_id
        }, success, fail);
    },

    block: function(group_id, tag_id) {
        this._.tagGroupStore.addPendingTag(tag_id, group_id);
        
        var success = _.bind(function() {
            this._.tagGroupStore.addTagToGroup(tag_id, group_id);
            this._.tagGroupStore.removePendingTag(tag_id, group_id);
            this._.tagGroupStore.setFailedTag('');
            this._.tagGroupStore.pushHistory(tag_id, group_id, this._.tagGroupStore.ACTIONS.BLOCKED);
            this._.emitter.trigger('new tagset');
        }, this);

        var fail = _.bind(function() {
            this._.tagGroupStore.setFailedTag('Failed to block ' + tag_id);
            this._.tagGroupStore.removePendingTag(tag_id, group_id);
            this._.tagGroupStore.pushHistory(tag_id, group_id, this._.tagGroupStore.ACTIONS.ERROR);
        }, this);

        this._.post('/account/groups/blocktag', {
            tag: tag_id
        }, success, fail);

        this._.tagGroupStore.block(tag_id);
    },

    unblock: function(group_id, tag_id) {
        this._.tagGroupStore.removeTagFromGroup(tag_id, group_id);

        var success = _.bind(function() {
            this._.tagGroupStore.setFailedTag('');
            this._.tagGroupStore.pushHistory(tag_id, group_id, this._.tagGroupStore.ACTIONS.UNBLOCKED);
            this._.emitter.trigger('new tagset');
        }, this);

        var fail = _.bind(function() {
            this._.tagGroupStore.pushHistory(tag_id, group_id, this._.tagGroupStore.ACTIONS.ERROR);
            this._.tagGroupStore.setFailedTag('Failed to unblock ' + tag_id);
            this._.tagGroupStore.addTagToGroup(tag_id, group_id);
        }, this);

        this._.post('/account/groups/unblocktag', {
            tag: tag_id
        }, success, fail);

        this._.tagGroupStore.unblock(tag_id);
    }
});
