Namespace('Imgur.Gallery');

Imgur.Gallery.SourceBar = React.createClass({
    displayName: 'Gallery-SourceBar',

    viaAppIconClick: function(){
        Imgur.Util.jafoLog({
            event: 'viaAppIcon',
            meta: {
                type: 'upload',
                hash: this.props.image.hash,
                isAlbum: this.props.image.is_album,
                nsfw: this.props.image.nsfw,
                inGallery: true,
                platform: (this.props.image.platform === 'ios') ? 'iphone' : this.props.image.platform
            }
        });
    },

    _redditSource: function(redditLink, sourceText) {
        return React.DOM.a({
                href: 'https://www.reddit.com' + redditLink,
                target: '_blank',
                key: 'reddit'
            },
            sourceText
        );
    },

    handleUserProfileClick: function() {
        Imgur.Util.jafoLog({
            event: 'userProfileNav',
            meta: {
                accountId: this.props.image.account_id,
                method: 'usernamePost'
            }
        });
    },

    render: function() {
        var children = [],
            anon     = false;

        if(this.props.image.isAd || this.props.image.is_ad) {
            children.push(
                React.DOM.a({
                    href: 'http://imgur.com/blog/2015/05/31/about-promoted-posts/',
                    className: 'title promoted-tag',
                    title: 'learn more about promoted posts',
                    key: 'promoted'
                }, 'Promoted')
            );
        }

        if(this.props.image.account_url) {
            children.push(
                React.DOM.span({key: 'by'}, ' by '),
                React.DOM.a({
                        href: '/user/' + this.props.image.account_url,
                        onClick: this.handleUserProfileClick,
                        title: 'view account of ' + this.props.image.account_url,
                        className: 'post-account',
                        key: 'account'
                    },
                    this.props.image.account_url
                )
            );
        } else if(this.props.image.reddit) {
            var sourceText = (this.props.image.video_source && !this.props.image.is_album) ? ' (source) ' : '(source) ';

            children.push(this._redditSource(this.props.image.reddit, sourceText));
        } else if(this.props.image.subreddit) {
            children.push(this._redditSource(this.props.image.permalink, '(source) '));
        } else {
            anon = true;
            children.push(React.DOM.span({key: 'anon'}, 'Uploaded '));
        }

        var opts = {className: 'post-title-meta font-opensans-semibold'};

        if(!this.props.image.title && !Imgur.Environment.hasAccess && !this.props.slim) {
            opts.className += ' post-title-meta--notitle'
        }

        if(this.props.image.isAd || this.props.image.is_ad) {
            // we skip reporting the time for promoted posts
            return React.DOM.div(opts, children);
        }

        // via ios or android
        if (['ios', 'android'].indexOf(this.props.image.platform) >= 0) {
            children.push(React.DOM.span({key: 'platform'}, ' via ', React.DOM.a({
                href: '//' + Imgur.Environment.host + '/apps',
                onClick: this.viaAppIconClick
            },(this.props.image.platform == 'ios') ? 'iPhone' : 'Android', React.DOM.span({
                className: 'icon-mobile'
            }, null))));
        }

        if (this.props.image.timestamp || this.props.image.datetime) {
            children.push(React.createElement(Imgur.Elements.Timestamp, {key: 'humantime' + (this.props.image.timestamp || this.props.image.datetime), shouldUpdate: false, timestamp: this.props.image.timestamp || this.props.image.datetime}));
        }

        if(this.props.image.video_source && !this.props.image.is_album) {
            children.push(
                React.DOM.span({
                        className: 'vidgif-info',
                        key: 'vidgif'
                    },
                    'Made with ',
                    React.DOM.a({
                            href: '//imgur.com/vidgif',
                            target: '_blank'
                        },
                        'Video to GIF'
                    )
                )
            );
        }

        return React.DOM.div(
            opts,
            children
        );
    }
});
