Namespace('Imgur.Tag.Controller');

Imgur.Tag.Controller.UserGroup = Class.extend({
    init: function(emitter, groupModel, activeGroupId, group, groupList, cboxConfirm, $confirmMessage, $confirmTitle) {
        this._ = {
            groupModel: groupModel,
            activeGroupId: activeGroupId,
            group: group,
            groupList: groupList,
            emitter: emitter,
            cboxConfirm: cboxConfirm,
            confirmTarget: '#colorbox-confirm',

            el: {
                $confirmMessage: $confirmMessage,
                $confirmTitle: $confirmTitle,
            }
        };
    },

    handleRemoveConfirmedEvent: function(e) {
        this._.group.remove();
        this._.emitter.trigger('group.remove', this._.group.getId());

        if(this._.group.getId() == this._.activeGroupId) {
            this._.emitter.trigger('command.location', '/');
        }
    },

    handleRemoveEvent: function(e) {
        e.stopPropagation();
        
        this._.el.$confirmTitle.html('Delete ' + this._.group.getName() + '?');
        this._.el.$confirmMessage.html('Are you sure you want to delete the gallery <span class="green">' + this._.group.getName() + '</span>?');
        this._.cboxConfirm.show(this._.confirmTarget, _.bind(this.handleRemoveConfirmedEvent, this, e));
    },

    handleRemovedTagEvent: function(tag, e) {
        e.stopPropagation();

        this._.groupList.remove(tag);
        this._.groupList.removeCoreSet(tag);

        this._.groupModel.unsubscribe(this._.group.getId(), tag.getName(), _.bind(function() {
            this._.emitter.trigger('new tagset');
        }, this));
        this._.emitter.trigger('show tagset loader');
    },

    handleRemoveEventOther: function(id) {
        if(id == this._.group.getId()) {
            this._.group.remove();
        }
    },

    setList: function(sortedList) {
        this._.groupList = sortedList;
        this._.group.setList(sortedList);
    }
});
