Namespace('Imgur.Factory');

Imgur.Factory.Gallery = Class.extend({
    _getDefaultPredicates: function(kindOfTag) {
        var isCloseEnough = (function(a, b) {
            var dist = Imgur.Util.editDistance(a, b);
            return (dist / Math.max(a.length, b.length)) < 0.2 || ((a.length+b.length/2)>=5 && dist <= 3) || b.indexOf(a) != -1;
        });

        var base = (function(tag) {
            return (tag instanceof kindOfTag);
        });

        var providerFilterByLevDistancePredicate = (function(query) {
            return (function(tag) {
                return (tag instanceof Imgur.Tag.SuggestedTag);
            });
        });

        return {
            base: base,
            providerByLevDistance: providerFilterByLevDistancePredicate 
        };
    },

    _getAjaxGetWithLoader: function(loader) {
        return (function(url, data, success, fail) {
            loader.show();
            fail = fail || _.noop;
            success = success || _.noop;

            return $.get(url, data, success)
                    .fail(fail)
                    .always(_.bind(loader.hide, loader));
        });
    },

    /**
     * Make the post callback aware of our standard data response, so that fail() is triggered 
     * when a server error happens
     */
    _standardDataResponsePost: function(post) {
        return (function(url, data, success, fail) {
            fail = fail || _.noop;
            success = success || _.noop;

            var success_aware = function(response, status, jqXHR) {
                if((response && response.success) || (!response && status === 'success')) {
                    // Assume that an empty response (no errors) implies success
                    success(response, status, jqXHR);
                } else {
                    fail((response && response.data && response.data.error && response.data.error.message) || (response && response.data && response.data.error) || status, jqXHR, status);
                }
            };

            var fail_aware = function(jqXHR, status, error) {
                var msg = error;

                try {
                    var json = jqXHR.responseJSON || $.parseJSON(jqXHR.responseText);
                    fail(json.data.error.message, jqXHR, status);
                    return;
                } catch(e) {}

                fail(msg, jqXHR, status);
            };

            return post(url, data, success_aware, fail_aware);
        });
    },

    _getStandardPostAuthorized: function() {
        return this._standardDataResponsePost(this._.postProvider());
    },

    _getStandardPostAuthorizedWithLoader: function(loader) {
        var postAuthorized = this._standardDataResponsePost(this._.postProvider());

        return (function(url, data, success, fail) {
            loader.show();
            return postAuthorized(url, data, success).always(_.bind(loader.hide, loader));
        });
    },

    _getPostAuthorizedWithLoader: function(loader) {
        var postAuthorized = this._.postProvider();

        return (function(url, data, success, fail) {
            loader.show();
            return postAuthorized(url, data, success).always(_.bind(loader.hide, loader));
        });
    },

    _produceEmptyMessageProviderForQuery: function(query) {
        return function() {
            return _.templates['tags.empty-message-makeone']({query: query});
        };
    },

    _produceUserGroupProvider: function(emitter, groupModel, currentGroupId, cboxConfirm, $confirmMessage, $confirmTitle) {
        var comparator = new Imgur.Tag.Comparator(),
            comparer = _.bind(comparator.compare, comparator);

        var userGroupControllerProvider = _.bind(this._produceUserGroupController, this, 
            emitter, 
            groupModel, 
            currentGroupId,
            cboxConfirm, 
            $confirmMessage, 
            $confirmTitle
        );
        
        var userGroupToDomFromData = _.bind(this._produceUserGroupFromData, this, comparator);
        var newUserGroupFromDom = _.bind(this._produceUserGroupFromDom, this, userGroupControllerProvider, comparator, emitter);
        var newUserGroupProvider = _.compose(newUserGroupFromDom, userGroupToDomFromData);

        return newUserGroupProvider;
    },

    _produceUserGroupController: function(emitter, groupModel, current_group_id, cboxConfirm, $confirmMessage, $confirmTitle, group, tag_list_structure) {
        return new Imgur.Tag.Controller.UserGroup(
            emitter, 
            groupModel, 
            current_group_id, 
            group, 
            tag_list_structure, 
            cboxConfirm, 
            $confirmMessage, 
            $confirmTitle
        );
    },

    _produceUserGroupFromData: function(comparer, name, hash, tags) {
        return $(_.templates['tags.user-group']({
            group: {
                name: name,
                hash: hash
            }
        }));
    },

    _produceUserGroupFromDom: function(groupControllerProvider, comparer, emitter, $dom, tags) {
        var tag_list_structure = new Imgur.Element.SortedListEmptyable($(), comparer, tags || []);
        var group = new Imgur.Tag.UserGroup($dom, tag_list_structure);

        var groupController = groupControllerProvider(group, tag_list_structure);
        group.get$RemoveButton().on('click', _.bind(groupController.handleRemoveEvent, groupController));

        emitter.on('group.remove', groupController, groupController.handleRemoveEventOther);

        return {
            group: group,
            adapter: groupController
        };
    }
});
