Namespace('Imgur.Promoted');

var youtube_ready = false;
var youtube_iframes = {};

//Needs to live outside class structure for youtube script to access https://developers.google.com/youtube/iframe_api_reference#Requirements
function onYouTubeIframeAPIReady() {
    for(var iframe in youtube_iframes) {
        if (youtube_iframes[iframe].dom && !youtube_iframes[iframe].player) {
            youtube_iframes[iframe].player = new YT.Player(youtube_iframes[iframe].dom, {
                events: {
                    onReady: _.partial(onPlayerReady, iframe),
                    onStateChange: _.partial(onPlayerStateChange, iframe)
                }
            });
        }
    }
}

function onPlayerReady(iframe) {
    if (youtube_iframes[iframe].manuallyPaused) {
        return;
    }

    if(!youtube_iframes[iframe].soundOn) {
        youtube_iframes[iframe].player.mute();
    }

    youtube_iframes[iframe].player.seekTo(youtube_iframes[iframe].currentTime, true);
    youtube_iframes[iframe].player.playVideo();

    youtube_iframes[iframe].hash = youtube_iframes[iframe].player.getVideoData().video_id;
    youtube_iframes[iframe].videoLength = youtube_iframes[iframe].player.getDuration();
}

function onPlayerStateChange(iframe, event) {
    switch (event.data) {
        case YT.PlayerState.PAUSED:
            youtube_iframes[iframe].manuallyPaused = true;
            break;
        case YT.PlayerState.PLAYING:
            youtube_iframes[iframe].manuallyPaused = false;
            break;
    }
}

Imgur.Promoted = Class.extend({
    init: function(config, emitter, insideNav, sideGallery, events, source) {
        this._ = {
            'ele': {},
            'config': config,
            'emitter': emitter,
            'events': events,
            'eventsBound': false,
            'navs': 0,
            'navHashes': [],
            // whether the last attempt to fill failed
            'failMode': false,
            'reservations': [],
            'ads': {},
            'order':[],
            'pxQueue': [],
            'source': source,
            'rgxSubreddit': /\/r\/([^\/]+)/,
            'rgxPxMacroTimestamp': /[\{\[\%]+(timestamp|cachebust(er)?)[\}\]\%]+/ig,
            'rgxPxMacroRandom': /[\{\[\%]+random[\}\]\%]+/ig,
            'pxMacroRandomLength': 10,
            'allowedRandomChars': 'abcdefghijklmnopqrstuvwxyz0123456789',
            'inside': insideNav,
            'sectSortWindow': '',
            'sideGallery': sideGallery,
            'thumbnailSizes': {
                wide: '288x141',
                normal: '141x141'
            },
            'enabled': Imgur.Gallery.getInstance()._.safe_tags && true, // quickie sloppy sfw vs nsfw logic
            youtubeViewabilityTimer: 1000, //ms (1000 should be divisible by whatever value this is set to, to make seconds calculation properly)
            youtubeMinViewTime: 3 //seconds
        };

        // triggered on ajax return of new promoted post (duh)
        emitter.on('gotPromoted', this, this.handleReservation);

        // Triggered on each page pulled into sideGallery
        // We check sectSortWindow in there and if new, call handleNewGallery()
        emitter.on('sidegalleryPageLoad', this, this.handleNewGalleryPage);

        // triggered for each navigation
        emitter.on('postRendered', this, this.handleNav);

        // triggered when displaying a promoted post
        emitter.on('displayPromoted', this, this.handleDisplay);

        // fired by events object
        emitter.on('promoEvent', this, this.handleDomEvent);

        // this binds direct nav
        if (Imgur.Environment.image.isAd) {
            this.bindGameLink(Imgur.Environment.image.hash); // one-off for old spice FB game campaign
            this.initYouTubeAutoPlayMute();
        }
    },

    // decide whether or not we should enable events
    eventsEval: function(hash) {
        if(hash in this._.ads) {
            this._.events.bindAll();
            this._.eventsBound = true;
        } else if(this._.eventsBound) {
            this._.events.bindAll(true);
            this._.eventsBound = false;
        }
    },

    handleDomEvent: function(ev) {
        var evName = Imgur.Util.dive(ev, 'data.promoEvent'),
            iter;

        if(evName) {
            if(!_.isArray(evName)) {
                evName = [evName];
            }

            for(iter = 0; iter < evName.length; iter++) {
                this.pixel(this._.inside.getHash(), evName[iter]);
            }
        }
    },

    handleNav: function(data, force, distance) {
        var hash = data.hash;

        if(!this._.enabled) {
            return;
        }

        if (this._.youtubeViewabilityInterval) {
            this.resetYouTubeAutoPlayMute();
        }

        if (data.isAd) {
            this.initYouTubeAutoPlayMute();
        }

        this.eventsEval(hash);

        if(isNaN(this._.navs)) {
            this._.navs = 0;
        }

        if(this._.config.skipPromoNav && hash in this._.ads) {
            return;
        }

        if(this._.config.onlyUniqueNavs && _.indexOf(this._.navHashes, hash) > -1) {
            return;
        }

        var thresh = this._.config.navThreshold;

        if(2 > this._.order.length && this._.config.populateFirst) {
            // config setting can be set to non-positive into to disable and fallback to normal threshold
            thresh = !isNaN(this._.config.populateFirstThreshold) ? this._.config.populateFirstThreshold : thresh;
        }

        this._.navHashes.push(hash);
        this._.navs++;

        if(this._.failMode) {
            if(this._.navs > this._.config.failThreshold) {
                this._.failMode = false;
            }
        }

        if(this._.failMode) {
            // still in fail mode? bail.
            return;
        }

        // force nav or Nth
        if(force || (this._.navs > 0 && this._.navs >= thresh)) {
            this._.navs = 0;

            var after = this._.sideGallery.nthFromHash(!isNaN(distance) ? distance : this._.config.distance, hash);
            this._.reservations.push(after);
            // getPlacement() should return the XHR
            this._.emitter.trigger('wantPromoted', this._.source.getPlacement());
        }
    },

    handleDisplay: function(hash) {
        var ppData;

        this.bindGameLink(hash); // one-off for old spice FB game campaign

        if(hash in this._.ads) {
            ppData = this._.ads[hash];

            this.pixel(hash, 'IMPRESSION_POST');
            this.pixel(hash, 'CLICK_THUMB');

            this.placeCompanion(hash);

            Imgur.Util.jafoLog({
                event: 'ppConv',
                meta: {
                  'hash': hash,
                  'flightId': Imgur.Util.dive(ppData, 'parts.thumb.flightId'),
                  'campaignId': Imgur.Util.dive(ppData, 'parts.thumb.campaignId'),
                  'thumbnailSize': Imgur.Util.dive(ppData, 'parts.data.thumbnail_json.size') || this._.thumbnailSizes.normal
                }
            });
        }
    },

    // one-off for old spice FB game campaign
    bindGameLink: function(hash) {
        var subSelector = '.play-the-game';
        var games = [
            '0OcVZlb', // dev hash
            'CZf9o',
            'sAEyl',
            'Dj5dm'
        ];
        // 0OcVZlb is the dev hash
        // CZf9o is the prod hash
        if(games.indexOf(hash) > -1) {
            // subSelector has to be used because react won't let us directly bind...
            $('.post-container').off('click', subSelector, this.handleGameLink).on('click', subSelector, this.handleGameLink);
        }
    },

    // one-off for old spice FB game campaign
    handleGameLink: function(ev) {
        $.colorbox({
            href: '//www.youroldspicegame.com/',
            iframe: true,
            transition: 'fade',
            width: '1000px',
            height: '640px',
            scrolling: false,
            trapFocus: true,
            top: '50px',
            onClose: function() {
                // fire pixels here
            },
            onComplete: function() {
                // after content loads
                // fire pixels here
            },
            onOpen: function() {
                // fire pixels here
            },
        });
    },

    setYouTubeAutoPlayMuteIframes: function() {
        for (var src in youtube_iframes) {
            if(youtube_iframes[src].dom && !document.body.contains(youtube_iframes[src].dom)) {
                //Video has been scrolled out of dom
                youtube_iframes[src].isPlaying = false;
                youtube_iframes[src].player = null;
                youtube_iframes[src].dom = null;
            }
        }

        var youtube_videos = $('.post-image-description .youtube-autoplay-mute');

        for (var i = 0; i < youtube_videos.length; i++) {
            var src = youtube_videos[i].src;

            if(youtube_iframes.hasOwnProperty(src)) {
                if (youtube_iframes[src].player) {
                    youtube_iframes[src].soundOn = youtube_iframes[src].player.isMuted();
                    youtube_iframes[src].isPlaying = youtube_iframes[src].player.getPlayerState() != YT.PlayerState.PAUSED;
                    youtube_iframes[src].currentTime = youtube_iframes[src].player.getCurrentTime()

                    if (youtube_iframes[src].isPlaying) {
                        youtube_iframes[src].timeViewed += this._.youtubeViewabilityTimer/1000; //Converting ms to s

                        //Min time required to view a video
                        if (youtube_iframes[src].timeViewed == this._.youtubeMinViewTime) {
                            Imgur.Util.jafoLog({
                                event: 'videoView',
                                meta: {
                                  hash: youtube_iframes[src].hash
                                }
                            });
                        }
                    }
                }

                youtube_iframes[src].dom = youtube_videos[i];
            } else if (!youtube_iframes.hasOwnProperty(src)) {
                youtube_iframes[src] = {
                    soundOn: false,
                    isPlaying: false,
                    manuallyPaused: false,
                    currentTime: 0,
                    timeViewed: 0,
                    videoLength: 0,
                    player: null,
                    hash: null,
                    dom: youtube_videos[i]
                }
            }
        }
    },

    initYouTubeAutoPlayMute: function() {
        this.setYouTubeAutoPlayMuteIframes();

        if (!youtube_ready && Object.keys(youtube_iframes).length) {
            $.getScript('//www.youtube.com/iframe_api', _.bind(function() {
                window.addEventListener('beforeunload', _.bind(function() {
                    this.resetYouTubeAutoPlayMute();
                }, this));
            }, this));

            youtube_ready = true;
        }

        this._.youtubeViewabilityInterval = clearInterval(this._.youtubeViewabilityInterval);
        this._.youtubeViewabilityInterval = setInterval(_.bind(this.handleYouTubeAutoPlayMuteUpdate, this), this._.youtubeViewabilityTimer);
    },

    handleYouTubeAutoPlayMuteUpdate: function() {
        if (youtube_ready) {
            this.setYouTubeAutoPlayMuteIframes();
            onYouTubeIframeAPIReady();
        } else {
            this.initYouTubeAutoPlayMute();
        }
    },

    resetYouTubeAutoPlayMute: function() {
        for (var src in youtube_iframes) {
            Imgur.Util.jafoLog.direct({
                event: 'videoFinished',
                meta: {
                  hash: youtube_iframes[src].hash,
                  soundOn: youtube_iframes[src].soundOn,
                  timeViewed: youtube_iframes[src].timeViewed,
                  videoLength: youtube_iframes[src].videoLength,
                  percViewed: Math.min(1, youtube_iframes[src].timeViewed/youtube_iframes[src].videoLength) //Cap at 100%
                }
            });
        }

        this._.youtubeViewabilityInterval = clearInterval(this._.youtubeViewabilityInterval);
        youtube_iframes = {};
    },

    topicIsFiltered: function() {
        var currTopic = this._.inside._.section === 'topic'? this._.inside._.topic: null;

        return (currTopic && _.indexOf(this._.config.filteredTopics, currTopic) > -1);
    },

    sectionIsFiltered: function() {
        var keyIsObject = this._.config.filteredSections && this._.sideGallery.state.section in this._.config.filteredSections;
        var keyIsIndexed = _.indexOf(this._.config.filteredSections, this._.sideGallery.state.section) > -1;

        if(this._.sideGallery.state.section === 'subreddit') {
            return !this.subredditIsAllowed();
        }

        if(keyIsObject || keyIsIndexed) {
            if(typeof this._.config.filteredSections[this._.sideGallery.state.section] === 'object') {
                // array of filtered gallery sorts
                return _.indexOf(this._.config.filteredSections[this._.sideGallery.state.section], this._.sideGallery.state.sort) > -1;
            }

            // plain string - whole section filtered
            return true;
        }

        return false;
    },

    subredditIsAllowed: function() {
        var src = this._.sideGallery.state.source;
        var subMatched = src ? src.match(this._.rgxSubreddit) : null;

        if(subMatched && subMatched.length === 2) {
            return _.indexOf(this._.config.allowedSubreddits, subMatched[1].toLowerCase()) > -1;
        }

        return false;
    },

    handleNewGallery: function() {
        // can be set to non-positive int to disable and fall back to normal distance
        var firstDist = !isNaN(this._.config.populateFirstDistance) ? this._.config.populateFirstDistance: this._.config.distance;

        // Filter out bad topics
        if(this.topicIsFiltered()) {
            this._.enabled = false;
            return;
        }

        // Filter out bad sections
        if(this.sectionIsFiltered()) {
            this._.enabled = false;
            return;
        }

        this._.navs = 0;

        if (this._.inside._.image.isAd) {
            this._.order.push(this._.inside._.image.hash);
            return;
        }

        // populateFirstDistance
        if(this._.config.populateFirst) {
            var hash = this._.inside.getHash();
            this.handleNav(this._.inside.getSideGallery().getImage(hash), true, firstDist);
        }
    },

    handleNewGalleryPage: function(method) {
        if (method !== 'initial'){
            return;
        }
        this.handleNewGallery();
    },

    nextLocIsFlushedLeft: function(hash) {
        return $('#item-' + hash).position().left === 0;
    },

    handleReservation: function(placeObj) {
        var hash = placeObj.getHash(),
            after = this._.reservations.shift(),
            addToIndex = 0;
            thumbnailSize = Imgur.Util.dive(placeObj, 'parts.data.thumbnail_json.size');

        if (thumbnailSize === this._.thumbnailSizes.wide && this.nextLocIsFlushedLeft(after)) {
            addToIndex++;
        }

        this.handlePlacement(placeObj, after, addToIndex);
    },

    handlePlacement: function(placeObj, after, addToIndex) {
        var hash = placeObj.getHash(),
            thumbnailSize = Imgur.Util.dive(placeObj, 'parts.data.thumbnail_json.size');

        if(!hash) {
            this._.failMode = true;
            return;
        }

        if(!after) {
            after = this._.sideGallery.state.items[this._.sideGallery.state.items.length-1];
        }

        if(hash != this._.inside._.hash && !(hash in this._.ads) && this._.sideGallery.hashPosition(hash) < 0) {
            this._.ads[hash] = placeObj;
            this._.order.push(hash);

            if(!('timestamp' in placeObj.parts.data)) {
                // default to "now"
                // moment is looking for YYYY-MM-DD HH:MM:SS in the template
                placeObj.parts.data.timestamp = moment().utc().format('YYYY-MM-DD HH:mm:ss');
            }

            var insertPlacement = this._.sideGallery.hashPosition(after) + 1 + addToIndex;
            this._.sideGallery.insertSingleItem(placeObj.parts.data, insertPlacement);
            this.pixel(hash, 'IMPRESSION_THUMB');

            Imgur.Util.jafoLog({
                event: 'ppImp',
                meta: {
                  'hash': hash,
                  'flightId': Imgur.Util.dive(placeObj, 'parts.thumb.flightId'),
                  'campaignId': Imgur.Util.dive(placeObj, 'parts.thumb.campaignId'),
                  'thumbnailSize': thumbnailSize || this._.thumbnailSizes.normal
                }
            });

        }
    },

    placeCompanion: function(hash) {
        var html = Imgur.Util.dive(this._.ads[hash], 'parts.companion.html');
        var container = '<div class="promoCompanion"><iframe id="promoCompanionFrame" width="300" height="250" marginwidth="0" marginheight="0" hspace="0" vspace="0" scrolling="no" frameBorder="0" seamless="seamless" sandbox="allow-same-origin allow-scripts allow-popups allow-popups-to-escape-sandbox"></iframe></div>';

        if (hash in this._.ads && html && (''+html).length > 0) {
            this._.inside.showAds(container, function(injectedCode, injectEle) {
                var frame = document.getElementById('promoCompanionFrame');
                var fDoc;

                if (injectedCode && injectEle && frame) {
                    fDoc = frame.contentWindow.document;

                    fDoc.open();
                    fDoc.write(html);
                    fDoc.close();
                }
            });

            setTimeout(_.bind(function(){
                if($('.promoCompanion').is(':visible')) {
                    this.pixel(hash, 'IMPRESSION_COMPANION');
                }
            }, this), 800);
        }
    },

    // fire a specific type of pixel for a given hash
    pixel: function(hash, pxType, callback) {
        var urls = Imgur.Util.dive(this._.ads, hash+'.pixels.'+pxType),
            ret = [],
            iter;

        if(_.isArray(urls)) {
            for(iter = 0; iter < urls.length; iter++) {
                ret.push(this.pixelFire(urls[iter], callback, _.extend([], arguments)));
            }
        }

        return ret;
    },

    // fire an arbitrary pixel
    pixelFire: function(url, callback, pxData) {
        var promoted = this,
            idx, img, imgHandler;

        // dammit, what do we have to validate an url that isn't buried?
        if('string' === typeof url && url.length > 2) {
            img = document.createElement('img');
            img.pixelData = pxData;

            // IE<9 has an issue garbage collecting images before loading them
            // if the scope triggers cleanup. Keep a global-ish reference we
            // use as a work-around. This technically is a memory leak (at least
            // for the indexes), but the volume of sponsored pixels *should* be
            // sane compared to something like JAFO which does not support such a
            // workaround.
            idx = this._.pxQueue.push(img) - 1;

            // set up a cleanup handler with some shared scope
            imgHandler = function() {
                if('function' === typeof callback) {
                    // remember the context! "this" and "arguments" are the event's versions!
                    callback.apply(this, arguments);
                }

                promoted._.emitter.trigger('promotedPixel', arguments);

                promoted._.pxQueue[idx] = null;
            };

            img.onload = imgHandler;
            img.onerror = imgHandler;

            // handle any "macros" in the pixel URL
            if(this._.rgxPxMacroTimestamp.test(url)) {
                url = url.replace(this._.rgxPxMacroTimestamp, new Date().getTime());
            }

            if(this._.rgxPxMacroRandom.test(url)) {
                url = url.replace(this._.rgxPxMacroRandom, this.generateRandomString(this._.pxMacroRandomLength));
            }

            img.src = url;
        }

        return img;
    },

    generateRandomString: function(randLength) {
        var rLen = parseInt(randLength, 10);

        if(rLen < 1) {
            // Bail on bad requested length
            return;
        }

        var setLen = this._.allowedRandomChars.length;
        var retString = '';
        var iter;

        for(iter = 0; iter < rLen; iter++) {
            retString += this._.allowedRandomChars.charAt(Math.floor(Math.random() * setLen));
        }

        return retString;
    }
});

