Namespace('Imgur.ShareOnImgur');
Imgur.ShareOnImgur = Imgur.extend({
    init : function(params) {
        params = params || {};

        this._ = $.extend(true, {
            gallerySubmitTermsUrl     : '/lightboxes/galleryterms',
            galleryVerifyEmailFormUrl : '/lightboxes/verifyemail',
            gallerySubmitURL          : '/share/i/', //for single images
            loading                   : false,
            shareing                  : false,
            terms                     : 0,

            messages                  : {
                postingTooFast : function(sec) {
                    var time = sec > 60? (Math.round(sec/60)+Imgur.Util.pluralize(Math.round(sec/60), ' minute')): sec+Imgur.Util.pluralize(sec, ' second');
                    return 'You\'re sharing too quickly. Please wait '+time+'.';
                },
                banned              : 'You blew it! You have been banned for posting NSFW images to the gallery.'
            },

            el                        : {
                $shareonimgur      : $('#shareonimgur'),
                $shareAlbum        : $('#share-album'),
                $preview           : $('#preview'),
                $descriptions      : $('#submission-title input, .data-col textarea'),
                $topic_id          : $('input[name="topic_id"]'),
                $titleDesc         : $('input[name="imageTitle[]"], textarea[name="description[]"]'),
                $imageTitles       : $('.image-title'),
                $deleteContainers  : $('.delete-col'),
                $required          : $('.required'),
                $submit            : $('#share'),
                $terms             : $('input[name="terms"]'),
                $tagbox            : $('#tagbox'),
                $contentContainer  : $('#content-container'),
                $topicSelect       : $('.topic-select'),
                $publishButtons    : $('#publish-buttons'),
                $missingFields     : $('#missing-fields'),
                $promotedPlatBoxes : $('#platform-target-prompt input[type="checkbox"]'),
                $promotedPlatAll   : $('#platform-target-prompt input[type="checkbox"]#targeting-all'),
                sortHandler        : '.move',
                images             : '.image-container .zoom'
            }
        }, params);

        this._.galleryLink = '//'+imgur._.host+'/gallery/'+this._.hash+'/new';
        this._.publishCortex = new Cortex({
            submitting:    false,
            promoted:      false,
            missingFields: 2
        });

        this._ = this._super(this._);
        return this._;
    },

    generalInit: function() {
        var self = this;

        var renderPublishWidget = (function() {
            ReactDOM.render(React.createElement(Imgur.Share.Publish,{
                cortex:  self._.publishCortex,
                hash:    self._.hash,
                isAlbum: self._.type == 'a' ? true : false,
                handlePreview: function(e) {
                    self._.originalState = self._.el.$shareAlbum.serialize();
                    self.handleSave(e);
                },
                handleSubmit: _.bind(self.handleSubmit, self)
            }), self._.el.$publishButtons.get(0));
        });

        renderPublishWidget();

        this._.publishCortex.on('update', function() {
            renderPublishWidget();
        });

        $(this._.el.images).imageZoom();

        this._.el.$deleteContainers.on('click', function(e){
            self.handleDelete(e);
        });

        this._.el.$required.on('keyup', function(){
            if ($(this).val() != '') {
                self._.hasTitle = true;
                $(this).removeClass('error');
            } else {
                self._.hasTitle = false;
            }

            self.checkMissingFields();
        });

        this._.el.$descriptions.on('keydown', function(e){
            if(e.keyCode == 9 && !e.shiftKey) {
                var $element = $(this), $container;

                //Coming from submission title input
                if($element.hasClass('required')) {
                    $container = $('.image-container').eq(0);
                } else {
                    $container = $element.parents('.image-container').next();
                }

                $.smoothScroll({
                    scrollTarget: $container
                });
            }
        });

        this._.el.$titleDesc.on('change', function(){
            $(this).parents('.image-container').find('input[name="dirty[]"]').val(1);
        });

        //Store the original state so we can tell if anything changed
        this._.originalState = this._.el.$shareAlbum.serialize();

        this._.el.$topicSelect.combobox({
            onChange: function(element, value, $choice) {
                self._.hasTopic = true;
                self.checkMissingFields();
                self._.el.$topicSelect.removeClass('error');
            }
        });

        // bind platform boxes for PP submission
        this._.el.$promotedPlatBoxes.on('change click', _.bind(_.debounce(this.handlePromoPlatformChange, 500), this));
        // set the default state by calling
        this.handlePromoPlatformChange();

        this.placeShareButtons();

        $(window).on('resize', _.bind(self.placeShareButtons, this));

        $(window).on('beforeunload', function(){
            if(self._.el.$shareAlbum.serialize() != self._.originalState && !self._.sharing) {
                return 'You have unsaved changes';
            }
        });
    },

    placeShareButtons: function() {
        if($('#topbar').height() + $('#shareonimgur').height() + $('#cta-container-placeholder').height() > $(window).height() && $(window).width() > 960) {
            this._.el.$publishButtons.addClass('js-fixed');
        } else {
            this._.el.$publishButtons.removeClass('js-fixed');
        }
    },

    retabindex: function() {
        $('#tagbox, #submission-title input, .data-col input, .data-col textarea').each(function(index){
            $(this).attr('tabindex', index+1);
        });
    },

    handleDelete: function(e) {
        e.preventDefault();
        $(e.target).closest('.image-container').remove();

        //Hide move and delete col
        if ($(this._.el.images).length == 1) {
            this._.el.$imageTitles.remove();
        }

        this.retabindex();
    },

    //Serialize form to JSON to get around php max vars for large albums
    serialize: function() {
        var data    = {};
        var dirty   = '';
        var $inputs = this._.el.$shareAlbum.find(':input');

        for(var i = 0; i < $inputs.length; i++) {
            var namedArray = $inputs[i].name.indexOf('[]');
            var name       = $inputs[i].name.replace('[]', '');
            var value      = $inputs[i].value;

            if (name == '') {
                continue;
            } else if(name == 'dirty') {
                dirty = value;
            } else if((name == 'description' && !dirty) || (name == 'imageTitle' && !dirty)) {
                value = '';
            }

            if(namedArray != -1) {
                if(Array.isArray(data[name])) {
                    data[name].push(value);
                } else {
                    data[name] = [value];
                }
            } else {
                if($inputs[i].type == 'radio' || $inputs[i].type == 'checkbox') {
                    if($inputs[i].checked) {
                        data[name] = value;
                    }
                } else {
                    data[name] = value;
                }
            }
        }

        return JSON.stringify(data);
    },

    handleSave: function(e) {
        if(e) {
            e.preventDefault();
        }

        if(this.handleValidation(true)) {
            var self = this;

            this._.previewWindow = window.open('/share/' + this._.type + '/' + this._.hash + '/loading', '_blank');

            Imgur.Util.jafoLog({
                event: 'shareOnImgurPreview',
                meta: {
                    hash: this._.hash
                }
            });

            $.ajax({
                method: 'POST',
                url: '/share/' + this._.type + '/' + this._.hash + '/save',
                data: {
                    msid: Imgur.Environment.msid,
                    data: this.serialize()
                },
                success: function (response) {
                    if (!response || !response.success) {
                        return self.handleFail(true, response);
                    }
                },
                error: this.handleFail.bind(this, true)
            });
        }
    },

    handleSubmit: function(e) {
        if(e) {
            e.preventDefault();
        }

        if(this.handleValidation() && !this._.loading) {
            var self       = this;
            this._.loading = true;
            this._.publishCortex.submitting.set(true);

            Imgur.Util.jafoLog({
                event: 'shareOnImgurButton',
                meta: {
                    hash: this._.hash,
                    topic: $('.topic-select .selection h4').text().trim(),
                    matureToggle: $('#mature').is(':checked'),
                    location: 'uploadModal'
                }
            });

            $.ajax({
                method: 'POST',
                url: '/share/' + this._.type + '/' + this._.hash + '/share',
                data: {
                    msid: Imgur.Environment.msid,
                    data: this.serialize()
                },
                success: function(response){
                    if(!response || !response.success) {
                        return self.handleFail(false, response);
                    }

                    self._.sharing = true;

                    // open the ad/flight for promoted posts
                    if(response.data && response.data.promoPage) {
                        var newPageContent='';

                        newPageContent += '<div class="panel" id="promo-success">';
                        newPageContent += '<h1>Success!</h1>';
                        newPageContent += '<ul>';
                        newPageContent += '<li><a href="//'+imgur._.host+'/gallery/'+self._.hash+'/" target="_blank">View the gallery page</a></li>';
                        newPageContent += '<li><a href="'+response.data.promoPage+'" target="_blank">View the promo flight</a></li>';
                        newPageContent += '</ul>';
                        newPageContent += '</div>';

                        self._.el.$contentContainer.html(newPageContent);
                    } else {
                        window.location = '//'+imgur._.host+'/gallery/'+self._.hash+'/new';
                    }
                },
                error: this.handleFail.bind(this, false)
            });
        }
    },

    // juggling the platofrm targeting checkboxes
    handlePromoPlatformChange: function(ev) {
        var allSelector = '#targeting-all';
        var allNotSelector = ':not('+allSelector+')';
        var allOn = this._.el.$promotedPlatAll.attr('checked');
        var isAll = typeof ev === 'object' && $(ev.target).is(allSelector);

        if(!this._.el.$promotedPlatBoxes.length) {
            return;
        }

        if(isAll) {
            if(allOn) {
                this._.el.$promotedPlatBoxes.filter(allNotSelector).attr('checked', true);
            } else {
                this._.el.$promotedPlatAll.removeAttr('checked');
            }
        } else {
            var expected = this._.el.$promotedPlatBoxes.filter(allNotSelector).length;
            var gotten = 0;

            // yes, we need to loop through these because jQuery fucks up the checked attr
            // thus an elegant trick like this._.el.$promotedPlatBoxes.filter(':not(#targeting-all)').filter('[checked]').length
            // simply won't work (it always returns the same amount as the expected var :/)

            for(var iter = 0; iter < this._.el.$promotedPlatBoxes.length; iter++) {
                var $currBox = $(this._.el.$promotedPlatBoxes[iter]);

                if($currBox.is(allSelector)) {
                    continue;
                }

                if($currBox.attr('checked')) {
                    gotten++;
                }
            }

            if(expected == gotten) {
                this._.el.$promotedPlatAll.attr('checked', true);
            } else {
                this._.el.$promotedPlatAll.removeAttr('checked');
            }
        }

    },

    redirectToGallery: function() {
        this._.previewWindow.close();
        window.location = this._.galleryLink;
    },

    handleFail: function(savePreview, response) {
        var self = this;
        this._.loading = false;
        this._.publishCortex.submitting.set(false);

        try {
            if(!response) {
                throw 'bad response';
            }

            if(response.responseText) {
                response = $.parseJSON(response.responseText);
            }
        } catch(e) {
            humanMsg.displayMsg('An unknown error occurred. Please try again.', 'error-msg');
            $.colorbox.close();

            Imgur.Util.jafoLog({
                event: 'shareOnImgurError',
                meta: {
                    hash: this._.hash,
                    error: 'unparsable',
                    button: 'share'
                }
            });

            return;
        }

        if(response.data == null || response.data.error == null || response.data.error.message == null) {
            humanMsg.displayMsg('An unknown error occurred. Please try again.', 'error-msg');
            $.colorbox.close();

            Imgur.Util.jafoLog({
                event: 'shareOnImgurError',
                meta: {
                    hash: this._.hash,
                    error: 'unknown',
                    button: 'share'
                }
            });

            return;
        }

        var i = parseInt(response.data.error.message);
        var statMessage = 'unknown';

        if(!isNaN(i) && i > 0) {
            statMessage = 'rateLimit';
            humanMsg.displayMsg(this._.messages.postingTooFast(i), 'error-msg');
        } else if(response.data.error.message == 'Banned') {
            statMessage = 'banned';
            humanMsg.displayMsg(this._.messages.banned, 'error-msg');
            $.colorbox.close();
        } else if(response.data.error.message == 'Terms') {
            statMessage = 'terms';
            this.showTerms(savePreview);
        } else if(response.data.error.message == 'NotVerified') {
            statMessage = 'notVerified';
            this.showEmailVerification(function() {
                if(savePreview) {
                    self.handleSave();
                } else {
                    self.handleSubmit();
                }
            });
        } else if(response.data.error.message == 'OP') {
            humanMsg.displayMsg('OP must exist.', 'error-msg');
            $.colorbox.close();
        } else if(response.data.error.message == 'Suspended') {
            statMessage = 'suspended';
            humanMsg.displayMsg('Your account is currently suspended', 'error-msg');
        } else {
            humanMsg.displayMsg('An unknown error occurred. Please try again.', 'error-msg');
            $.colorbox.close();
        }

        Imgur.Util.jafoLog({
            event: 'shareOnImgurError',
            meta: {
                hash: this._.hash,
                error: statMessage,
                button: 'share'
            }
        });
    },

    handleValidation: function(fromPreviewButton) {
        var error = false;
        var statMessage = '';
        var buttonName = fromPreviewButton === true ? 'preview' : 'share';

        if(!this._.el.$required.val()) {
            this._.el.$required.addClass('error');
            this._.el.$required.focus();
            error = true;

            // this will change if we add more required fields:
            statMessage += 'noTitle';
        }

        if(this._.el.$topic_id.val() == '') {
            this._.el.$topicSelect.addClass('error');
            error = true;

            if (statMessage == 'noTitle') {
                statMessage += 'NoTags';
            } else {
                statMessage += 'noTags'
            }
        }

        if (error) {
            Imgur.Util.jafoLog({
                event: 'shareOnImgurError',
                meta: {
                    hash: this._.hash,
                    error: statMessage,
                    button: buttonName
                }
            });

            return false;
        }

        return true;
    },

    checkMissingFields: function() {
        var missing_fields = 0;

        if(!this._.hasTopic) {
            missing_fields++;
        }

        if (!this._.hasTitle) {
            missing_fields++;
        }

        this._.publishCortex.missingFields.set(missing_fields);

        return missing_fields;
    },

    showTerms: function(savePreview) {
        var self  = this;
        var share = Imgur.ShareOnImgur.getInstance();

        $.colorbox.close();

        $.colorbox({
            href: self._.gallerySubmitTermsUrl,
            open: true,
            iframe: true,
            top: '15%',
            transition: 'none',
            scrolling: false,
            innerWidth: '520px',
            innerHeight: '234px',
            onComplete: function() {
                self._.publishCortex.submitting.set(false);
            },
            onCleanup: function() {
                if(share._.terms) {
                    self._.el.$terms.val(1);
                    if(savePreview) {
                        self.handleSave();
                    } else {
                        self.handleSubmit();
                    }
                }
            }
        });
    },

    acceptTerms : function(e, keepColorBoxOpen) {
        var self = Imgur.ShareOnImgur.getInstance();

        self._.terms = 1;
        self._.el.$terms.val(1);

        if(!keepColorBoxOpen) {
            $.colorbox.close();
        }
    },

    declineTerms : function() {
        var self = Imgur.ShareOnImgur.getInstance();

        self._.terms = 0;
        self._.el.$terms.val(0);
        $.colorbox.close();
    },

    showEmailVerification : function(callback) {
        var self = Imgur.ShareOnImgur.getInstance();

        $.colorbox.close();

        setTimeout(function(){
            var self_ = self;

            $.colorbox({
                href: self._.galleryVerifyEmailFormUrl,
                open: true,
                iframe: true,
                top: '15%',
                transition: 'none',
                scrolling: false,
                innerWidth: '520px',
                innerHeight: '256px',
                onComplete: function() {
                    self.sendVerificationEmail();
                    self_.checkEmailVerification();
                },
                onCleanup: function() {
                    if(callback) {
                        callback();
                    }
                }
            });
        }, 600);
    },

    sendVerificationEmail : function() {
        $.get('/ajax/send_email_verification');
    },

    checkEmailVerification : function(code, callback) {
        var self = this;

        if(!code) { code = '' }

        $.post('/ajax/verify_email/'+code, function(response) {
            if(response.data.verified) {
                $.colorbox.close();

                if (callback) {
                    callback()
                }
            } else {
                if(response.data.invalid && code) {
                    humanMsg.displayMsg('Verification code is invalid', 'error-msg');
                }

                setTimeout(function() { self.checkEmailVerification() }, 1500);
            }
        });
    }
});

Class.addSingleton(Imgur.ShareOnImgur);
