Namespace('Imgur.Tag.Store');

Imgur.Tag.Store.TagImage = Class.extend({
    SOURCE: {
        DEFAULT: 'default',
        GALLERY_PAGE: 'page',
        SUGGESTION: 'suggestion',
        USER: 'user'
    },

    init: function(emitter) {
        this._ = {
            emitter: emitter,
            tagsByHash: {}, // map hash -> (map tag -> tag)
            ajaxErrors: {}, // map hash -> error msg
            isLoading: false
        };
    },

    setAjaxError: function(hash, msg) {
        this._.ajaxErrors[hash] = msg;

        this._.emitter.trigger('ajax error added to post');
    },

    unsetAjaxError: function(hash) {
        delete this._.ajaxErrors[hash];

        this._.emitter.trigger('ajax error removed from post');
    },

    getAjaxError: function(hash) {
        if(this._.ajaxErrors.hasOwnProperty(hash)) {
            return this._.ajaxErrors[hash];
        } else {
            return null;
        }
    },

    getIsLoading: function() {
        return this._.isLoading;
    },

    setIsLoading: function(loading) {
        this._.isLoading = !!loading;

        this._.emitter.trigger('ajax tag loading finished');
    },

    isTagOnPost: function(hash, tag) {
        if(!this._.tagsByHash.hasOwnProperty(hash)) {
            return false;
        }

        return this._.tagsByHash[hash].hasOwnProperty(tag);
    },

    getTagsForPost: function(hash) {
        if(!this._.tagsByHash.hasOwnProperty(hash)) {
            return [];
        }

        return this._.tagsByHash[hash];
    },

    haveTagsForPost: function(hash) {
        return this._.tagsByHash.hasOwnProperty(hash);
    },

    addTagForPost: function(hash, tag_name, tag_obj) {
        if(!this._.tagsByHash.hasOwnProperty(hash)) {
            this._.tagsByHash[hash] = {};
        }

        if(this._.tagsByHash[hash].hasOwnProperty(tag_name)) {
            // merge tag data, but keep the original source
            var source = this._.tagsByHash[hash][tag_name].source;

            tag_obj = _.merge({}, this._.tagsByHash[hash][tag_name], tag_obj, {
                source: source
            });
        }

        this._.tagsByHash[hash][tag_name] = tag_obj;

        this._.emitter.trigger('tag added to post');
    },

    removeTagFromPost: function(hash, tag) {
        if(this._.tagsByHash.hasOwnProperty(hash)) {
            if(this._.tagsByHash[hash].hasOwnProperty(tag)) {
                delete this._.tagsByHash[hash][tag];
            }
        }
        
        this._.emitter.trigger('tag removed from post');
    },

    getTag: function(hash, tag_name) {
        if(!this._.tagsByHash.hasOwnProperty(hash)) {
            return null;
        }

        if(!this._.tagsByHash[hash].hasOwnProperty(tag_name)) {
            return null;
        }

        var tag = this._.tagsByHash[hash][tag_name];

        return tag;
    },

    _getTagProp: function(hash, tag, prop) {
        if(!this._.tagsByHash.hasOwnProperty(hash)) {
            return null;
        }

        if(!this._.tagsByHash[hash].hasOwnProperty(tag)) {
            return null;
        }

        var tag = this._.tagsByHash[hash][tag];

        return tag[prop];
    },

    _setTagProp: function(hash, tag, prop, value) {
        if(!this._.tagsByHash.hasOwnProperty(hash)) {
            return;
        }

        if(!this._.tagsByHash[hash].hasOwnProperty(tag)) {
            return;
        }

        var tag = this._.tagsByHash[hash][tag];

        tag[prop] = value;

        this._.emitter.trigger('image tag updated');
    },

    getTagVote: function(hash, tag) {
        return this._getTagProp(hash, tag, 'vote');
    },

    setTagVote: function(hash, tag, vote) {
        this._setTagProp(hash, tag, 'vote', vote);
    },

    setTagSource: function(hash, tag, source) {
        this._setTagProp(hash, tag, 'source', source);
    }
});
