// top level function because this is a callback for the iframe in colorbox
function checkEmailVerification(code, callback) {
    if (!code) {
        code = '';
    }

    $.post('/ajax/verify_email/' + code, function (response) {
        if (response.data.verified) {
            $.colorbox.close();

            if (callback) {
                callback();
            }
        } else {
            if (response.data.invalid && code) {
                humanMsg.displayMsg('Verification code is invalid', 'error-msg');
            }

            setTimeout(function () {
                checkEmailVerification();
            }, 1500);
        }
    });
}

