Namespace('Imgur.Tag');

Imgur.Tag.ShareTagComponent = React.createClass({
    displayName: 'Tag-ShareTagComponent',

    getInitialState: function() {
        return {
            focusDue: true,
            selectionActive: false,
            autocompleteIdx: null
        };
    },
    
    _encode: function(tag) {
        return tag.replace(new RegExp(' ', 'g'), '_');
    },

    handleTagClicked: function(tag, e) {
        e.preventDefault();
    },

    handleRemoveClicked: function(tag, e) {
        e.preventDefault();
        this.props.tagStore.removeTag(tag);
    },

    handleQueryChange: function(e) {
        this.props.suggestionStore.setQuery(e.target.value);
    },

    handleChoice: function(tag, e) {
        e.preventDefault();

        this.props.suggestionStore.setValidationMode('query');
        this.props.suggestionStore.clear();
        this.props.suggestionStore.setQuery('');

        this.props.tagStore.addTag({
            tag: tag
        });
        
        this.setState({
            selectionActive: false,
            autocompleteIdx: null
        });
    },
    
    getSelectionActive: function() {
        return this.state.selectionActive;
    },
    
    getAutocompleteIdx: function() {
        return this.state.autocompleteIdx;
    },
    
    setComponentState: function(options) {
        this.setState(options);
    },
    
    componentDidMount: function() {
        this._updateNano();
    },

    componentDidUpdate: function() {
        this._updateNano();
    },

    _updateNano: function() {
        var $nanoDiv = $(this.refs.nano),
            contentDiv = this.refs.content;

        $nanoDiv.height('inherit').height(contentDiv.scrollHeight);

        $nanoDiv.nanoScroller({
            preventPageScrolling: true,
            alwaysVisible: true
        });

        if($nanoDiv.children('.pane').is(':visible')) {
            $nanoDiv.addClass('nano-visible');
        } else {
            $nanoDiv.removeClass('nano-visible');
        }
    },
    
    stopPropagation: function(e) {
        e.stopPropagation();
    },
    
    _produceTag: function(selected, tagName, images) {
        return React.DOM.div({onClick: _.partial(this.handleChoice, tagName), className: 'tag suggested pointer' + (selected ? ' hover' : '')}, 
            React.DOM.div({className: 'name'}, tagName),
            React.DOM.div({className: 'images'}, Imgur.Util.numberFormat(images) + ' images')
        );
    },

    render: function() {
        var lastFinishedQuery   = this.props.suggestionStore.getLastFinishedQuery(), 
            query               = this.props.suggestionStore.getQuery(),
            acText              = '',
            isValidQuery        = this.props.suggestionStore.getIsValidQuery(query),
            ifNotMessage        = this.props.suggestionStore.getWhyNot(query),
            loading             = this.props.suggestionStore.getIsLoading(),
            suggestions         = this.props.suggestionStore.getSuggestions(),
            content             = [],
            defaultTags         = [],
            filteredDefaultTags = [],
            tags                = [],
            tagNames            = Object.keys(this.props.tagStore.getTags()),
            tagsLength          = 0,
            tagCountClass       = ''
            tagCountText        = '';
        
        if(!lastFinishedQuery.length) {
            content.push(React.DOM.div({className: 'empty'}, ''));
        } else {            
            _.each(suggestions, _.bind(function(stag, i) {
                var selected = (this.state.autocompleteIdx != null && i == this.state.autocompleteIdx && this.state.selectionActive);
                content.push(this._produceTag(selected, stag.tag, stag.images));
            }, this));
    
            if(!suggestions.length && isValidQuery) {
                var lastQuery = (loading ? lastFinishedQuery : query);
                content.push(React.DOM.p({className: 'empty'}, 'No tag like ', React.DOM.em(null, lastQuery), ' found. ', 
                    React.DOM.a({className: 'tag-create-from-empty-query', href: '#', onClick: _.partial(this.handleChoice, lastQuery)}, 'Make it!')));
            }
        }
        
        //Show default tags for focused empty tag input with no query
        if(content.length < 2 && tagNames.length == 0 && !suggestions.length && (!loading || !query.length)) {
            //Filter out already selected default tags
            filteredDefaultTags = _.filter(this.props.defaultTags, function(dtag){
                return tagNames.indexOf(dtag.tag) == -1;
            });

            _.each(filteredDefaultTags, _.bind(function(dtag, i) {
                if(tagNames.indexOf(dtag.tag) == -1) {
                    var selected = (this.state.autocompleteIdx != null && i == this.state.autocompleteIdx && this.state.selectionActive);
                    defaultTags.push(this._produceTag(selected, dtag.tag, dtag.images));
                }
            }, this));
        }
            
        tags = _.map(tagNames, _.bind(function(tag) {
            
            //Can't remove default gallery type tag
            if (tag == this.props.gallery_type) {
                return React.DOM.div({className: 'tag-removable'},
                    React.DOM.span(null, tag)
                );
            } else {
                return React.DOM.div({className: 'tag-removable'},
                    React.DOM.span(null, tag),
                    React.DOM.a({onClick: _.partial(this.handleRemoveClicked, tag), href: '#', className: 'remove'}, ''),
                    React.DOM.input({type: 'hidden', name: 'tag[]', value: tag})
                );
            }
        }, this));
        
        tagsLength = tags.length;
        
        if(tagsLength >= this.props.tagLimit) {
            tagCountClass = 'filled';
        } else if(tagsLength > 0) {
            tagCountClass = 'filling';
        }
        
        if(this.refs.query && $(this.refs.query).find('input').is(':focus')) {
            tagCountText = 'Press enter to add tag ';
        }

        return React.DOM.div(null,
            tags,
            React.DOM.div({className: 'tag-filter'},
                React.createElement(Imgur.Tag.AutocompleteComponent,{
                    onSubmit: this.handleChoice,
                    ref: 'query',
                    id: (tagsLength >= this.props.tagLimit) ? 'tagbox-hidden' : this.props.id,
                    type: 'text',
                    autoComplete: 'off',
                    placeholder: this.props.placeholder,
                    tabIndex: this.props.tabIndex,
                    defaultTags: filteredDefaultTags,
                    suggestionStore: this.props.suggestionStore,
                    getAutocompleteIdx: this.getAutocompleteIdx,
                    getSelectionActive: this.getSelectionActive,
                    setComponentState: this.setComponentState,
                    maybeErrorLabel: ifNotMessage
                }),
                React.DOM.div({ref: 'nano', className: 'tag-list-content nano'}, 
                    React.DOM.div({ref: 'content', className: 'tag-list-results scroll-content content'},
                        (filteredDefaultTags.length || (suggestions.length && isValidQuery)) ? React.DOM.p({}, 'TAG SUGGESTIONS') : '',
                        content,
                        defaultTags
                    )
                )
            ),
            React.DOM.p({id: 'tag-count', className: 'limit'}, tagCountText,
                React.DOM.span({className: tagCountClass}, '(' + tagsLength + '/' + this.props.tagLimit + ')')
            )
        );
    }
});
