Namespace('Imgur.Elements');

Imgur.Elements.PopupMenu = React.createClass({
    displayName: 'PopupMenu',

    componentWillUnmount: function() {
        $('body').off('click', this.handleBodyClick);
    },

    getInitialState: function() {
        return {
            opened: false
        };
    },

    handleBodyClick: function(e) {
        this.setState({
            opened: false
        });
    },

    handleMenuClick: function(e) {
        e.stopPropagation();

        if (typeof this.props.menuClick === 'function') {
            //Passes the state the component will be in after the click event finishes
            this.props.menuClick(!this.state.opened);
        }

        this.setState({
            opened: !this.state.opened
        }, _.bind(function() {
            if(this.state.opened) {
                $('body').on('click', this.handleBodyClick);
            } else {
                $('body').off('click', this.handleBodyClick);
            }
        }, this));
    },

    handleChildClick: function(e) {
        e.stopPropagation();
    },

    render: function() {
        return React.DOM.div({onClick: this.handleMenuClick, className: this.props.className + ' like-combobox-but-not ' + (this.state.opened ? ' opened' : '')},
            React.DOM.span({}, this.props.name),
            React.DOM.span({className: this.props.afterNameIcon}),
            React.DOM.div({className: 'options'},
                React.DOM.div({onClick: this.handleChildClick},
                    this.props.children
                )
            )
        );
    }
});
