Namespace('Imgur.Images');
Imgur.Images = Class.extend({
    init : function(params) {
        this._ = $.extend(true,
            {
            imagesPerPage       :    20,
            thisName            :    'app',
            ajaxURL             :    '/ajax/images',
            noPageHash          :    false,
            addToThumbsFunc     :    function() { return null },
            hiddenImagesFunc    :    function() { return null },
            noImagesFunc        :    function() { return null },
            onComplete          :    function() { return null },
            currentPage         :    1,
            totalPages          :    0,
            totalImageCount     :    0,
            numImages           :    0,
            changingPage        :    false,
            options             :    null,
            imageLimit          :    225,
            isPro               :    false,
            cache               :    [],
            stopOnPaging        :    true,
            lastScrollPos       :    0,
            ajax                :    null,

            el: {
                $navigationContainer:     $('.navigation'),
                $loader             :    $('#images-loader'),
                $totalCounter       :    $('.total-images'),
                $totalCounterSuffix :     $('.total-images-suffix'),
                $thumbsContainer    :    $('#image-thumbs'),
                pageNumbersClass    :     'page-numbers',
                prevID              :    'prev',
                nextID              :    'next'
                }
            },

            params);

        this._.el.$totalCounter.text(0);
        this._.el.$totalCounterSuffix.text('images');

        if(!this._.noPageHash) {
            this._.currentPage = this.getPage();
        }
    },

    //*********************** GENERIC FUNCTIONS ***********************//
    getHash : function() {
        return window.location.hash.substring(1);
    },

    getPage : function() {
        var page = parseInt(this.getHash());
        if(page < 1) page = 1;
        return page? page: 1;
    },

    //*********************** PAGING FUNCTIONS ***********************//

    stopPaging : function() {
        if(window.stop !== undefined) {
            window.stop();
        } else if(document.execCommand !== undefined) {
             document.execCommand('Stop', false);
        }
    },

    //*********************** NAVIGATION FUNCTIONS ***********************//

    generatePageLink : function(container, i) {
        if(i > 0 && i <= this._.totalPages) {
            var link = $('<a href="javascript:;">'+i+'</a>');
            var self = this;
            link.click(function() {
                self._.el.$thumbsContainer.parent().scrollTop(0);
                self.changePage(i);
            });

            container.append(link);

            if(i == this._.currentPage) {
                link.addClass('current-selected');
            }
        }
    },

    generateNavigationLinks : function() {
        this._.el.$navigationContainer.empty();

        if(this._.totalPages == 1) return;

        var prevPage = this._.currentPage - 1,
            nextPage = this._.currentPage + 1,
            numbers  = $('<span class="'+this._.el.pageNumbersClass+'"></span>'),
            prev     = $('<a href="javascript:;" id="'+this._.el.prevID+'">Prev</a>'),
            next     = $('<a href="javascript:;" id="'+this._.el.nextID+'">Next</a>');

        prev.click(this.prev);
        next.click(this.next);

        for(var i = this._.currentPage - 5; i < this._.currentPage; ++i) {
            this.generatePageLink(numbers, i);
        }

        for(var i = this._.currentPage; i < this._.currentPage + 6; ++i) {
            this.generatePageLink(numbers, i);
        }

        this._.el.$navigationContainer.append(numbers);

        if(this._.currentPage > 1) {
            this._.el.$navigationContainer.append(prev);
        }

        if(this._.currentPage < this._.totalPages) {
            this._.el.$navigationContainer.append(next);
        }
    },

    recalcNavigation : function() {
        var newPageCount = Math.ceil(this._.totalImageCount/this._.imagesPerPage);

        if(newPageCount < 1) {
            newPageCount = 1;
        }

        if(newPageCount != this._.totalPages) {
            this._.totalPages = newPageCount;
            this.generateNavigationLinks(this._.currentPage);
        }

        this._.el.$totalCounter.text(Imgur.Util.numberFormat(this._.totalImageCount));
        this._.el.$totalCounterSuffix.text(Imgur.Util.pluralize(this._.totalImageCount, 'image'));
    },

    handleJSON : function(data) {
        if(data != null) {
            this._.cache[$.param(this._.options)] = data;
            this._.isPro = data.isPro;

            if(data.count != this._.totalImageCount) {
                this._.totalImageCount = parseInt(data.count);
                this.recalcNavigation();
            }

            if(data.count > 0 && data.images.length > 0) {
                var self = this;

                $.each(data.images, function(i, item) {
                    self._.numImages++;
                    self._.addToThumbsFunc(item, null, true);
                });
            } else {
                this._.noImagesFunc();
            }

            this._.el.$thumbsContainer.append('<div class="clear"></div>');
        }

        this._.changingPage = false;
        this._.el.$loader.hide();

        this._.onComplete();
    },

    //*********************** PUBLIC FUNCTIONS ***********************//

    changePage : function(pageNumber, noHash, scroll) {
        if(this._.options == null) return false;

        if(this._.changingPage) {
            if(scroll) {
                return false;
            } else {
                this._.ajax.abort();
            }
        }

        if(pageNumber > this._.totalPages && this._.totalPages > 0) {
            return false;
        }

        this._.changingPage = true;
        this._.currentPage  = pageNumber;

        this._.numImages = (this._.imagesPerPage*(this._.currentPage-1));

        this._.el.$loader.show();
        this.generateNavigationLinks();

        if(!scroll) {
            this._.el.$thumbsContainer.empty();

            if(!noHash && !this._.noPageHash) {
                window.location = window.location.href.substring(0, window.location.href.indexOf('#')) + '#' + this._.currentPage;
            }
        }

        this._.options.page    = pageNumber;
        this._.options.perPage = this._.imagesPerPage;

        var key     = $.param(this._.options);
        var self     = this;

        if(this._.cache[key]) {
            this.handleJSON(this._.cache[key]);
        } else {
            if(this._.stopOnPaging && !noHash) this.stopPaging();

            this._.ajax = $.getJSON(
                this._.ajaxURL,
                this._.options,
                function(response) {
                    if(response.success) {
                        self.handleJSON(response.data);
                    }
                }
            );
        }

        return true;
    },

    bindScrollEvent : function() {
        var distance  = 80;
        var parent    = this._.el.$thumbsContainer.parent();
        var container = this._.el.$thumbsContainer;

        var self = this;

        this._.el.$thumbsContainer.parent().scroll(function() {
            if(parent.scrollTop() > container.height() - parent.height() - distance) {
                self.changePage(self._.currentPage+1, true, true);
            }

            self._.lastScrollPos = parent.scrollTop();
        })
    },

    next : function() {
        var self = Imgur.Images.getInstance();
        self._.el.$thumbsContainer.parent().scrollTop(0);
        return self.changePage(self._.currentPage+1);
    },

    prev : function() {
        var self = Imgur.Images.getInstance();
        self._.el.$thumbsContainer.parent().scrollTop(0);
        return self.changePage(self._.currentPage-1);
    },

    reload : function() {
        return this.changePage(this._.currentPage, true);
    },

    getCurrentPage : function() {
        return this._.currentPage;
    },

    setCurrentPage : function(pageNum) {
        this._.currentPage = pageNum;
    },

    setOptions : function(options) {
        this._.options = options;
    },

    getTotalPages : function() {
        return this._.totalPages;
    },

    getImageLimit : function() {
        return this._.imageLimit;
    },

    getTotalImageCount : function() {
        return this._.totalImageCount;
    },

    increaseImageCount : function() {
        this._.totalImageCount++;
    },

    clearCache : function() {
        this._.cache = [];
    },

    stopOnPaging : function(stop) {
        this._.stopOnPaging = stop;
    }
});

Class.addSingleton(Imgur.Images);
