Namespace('Imgur.Gallery.Store.Saving');

Imgur.Gallery.Store.Saving = Class.extend({
    init: function(emitter) {
        this._ = {
            posts: {}, // Map of hash -> isSaving
            saving: 0,
            emitter: emitter
        };
    },

    add: function(hash, type) {
        if (!this._.posts[hash + type]) {
            if (this._.saving == 0) {
                window.onbeforeunload = function() {
                    return 'Your updates are still saving. Are you sure you want to leave?';
                }
            }
            
            this._.posts[hash + type] = true;
            this._.saving++;
            this._.emitter.trigger('saving');
        }
    },

    remove: function(hash, type) {
        if (this._.posts[hash + type]) {
            this._.posts[hash + type] = false;
            this._.saving--;

            if (this._.saving == 0) {
                window.onbeforeunload = null;
                this._.emitter.trigger('saved');
            }
        }
    },
});