Namespace('Imgur.Tag');

Imgur.Tag.Follow = React.createClass({
    displayName: 'Tag-Follow',

    _handleUnsubscribe: function() {        
        this.props.groupModel.unsubscribe('[custom]', this.props.tag);
    },
    
    _handleSubscribe: function() {        
        this.props.groupModel.subscribe('[custom]', this.props.tag);
    },
    
   render: function() {
        if (this.props.tagFollower) {
            return React.DOM.div({className: 'btn btn-small btn-main', onClick: _.bind(this._handleUnsubscribe, this)}, 'following');
        } else {
            return React.DOM.div({className: 'btn btn-small btn-secondary', onClick: _.bind(this._handleSubscribe, this)}, 'follow');
        }
   }
});