Namespace('Imgur.Tag.Store');

Imgur.Tag.Store.TagGroup = Class.extend({
    ACTIONS: {
        ADDED: 'added',
        REMOVED: 'removed',
        ERROR: 'error'
    },

    init: function(emitter) {
        this._ = {
            emitter: emitter,
            groupsByTag         : {}, // map tag -> list of group hashes
            tagsByGroup         : {}, // map group hash -> list of tags
            pendingAddTags      : {}, // map group hash -> list of tags
            groupsEventHistory  : {}, // map tag -> group hash -> action
            failedTag           : ""
        };
    },

    pushHistory: function(tag, group_hash, action) {
        if(!this._.groupsEventHistory.hasOwnProperty(tag)) {
            this._.groupsEventHistory[tag] = {};
        }

        if(!this._.groupsEventHistory[tag].hasOwnProperty(group_hash)) {
            this._.groupsEventHistory[tag][group_hash] = [];
        }

        this._.groupsEventHistory[tag][group_hash].push({
            time: Date.now(),
            action: action
        });

        this._.emitter.trigger('new tag-group event');
    },

    getLatestTagGroupEvent: function(tag, group_hash) {
        if(!this._.groupsEventHistory.hasOwnProperty(tag) || !this._.groupsEventHistory[tag].hasOwnProperty(group_hash)) {
            return null;
        }

        var evts = this._.groupsEventHistory[tag][group_hash],
            evt = evts[evts.length - 1];

        return evt;
    },

    isTagInGroup: function(tag, group_hash) {
        if(!this._.groupsByTag.hasOwnProperty(tag)) {
            return false;
        }
        
        return this._.groupsByTag[tag].indexOf(group_hash) !== -1 || (this._.pendingAddTags.hasOwnProperty(group_hash) && this._.pendingAddTags[group_hash].indexOf(tag) !== -1);
    },

    getTagsForGroup: function(group_hash) {
        if(!this._.tagsByGroup.hasOwnProperty(group_hash)) {
            return [];
        }

        if(this._.pendingAddTags.hasOwnProperty(group_hash)) {
            return this._.tagsByGroup[group_hash].concat(this._.pendingAddTags[group_hash]);
        }

        return this._.tagsByGroup[group_hash];
    },

    getGroupHashesForTag: function(tag) {
        if(!this._.groupsByTag.hasOwnProperty(tag)) {
            return [];
        }

        return this._.groupsByTag[tag];
    },

    addTagToGroup: function(tag, group_hash) {
        if(!this._.groupsByTag.hasOwnProperty(tag)) {
            this._.groupsByTag[tag] = [];
        }

        if(!this._.tagsByGroup.hasOwnProperty(group_hash)) {
            this._.tagsByGroup[group_hash] = [];
        }

        this._.groupsByTag[tag].push(group_hash);
        this._.tagsByGroup[group_hash].push(tag);

        this._.emitter.trigger('tag added to group');
    },

    removeTagFromGroup: function(tag, group_hash) {
        if(this._.groupsByTag.hasOwnProperty(tag)) {
            this._.groupsByTag[tag] = _.filter(this._.groupsByTag[tag], function(cur_hash) {
                return (cur_hash != group_hash);
            });
        }

        if(this._.tagsByGroup.hasOwnProperty(group_hash)) {
            this._.tagsByGroup[group_hash] = _.filter(this._.tagsByGroup[group_hash], function(cur_tag) {
                return (cur_tag != tag);
            });
        }

        this._.emitter.trigger('tag removed from group');
    },
    
    addPendingTag: function(tag, group_hash) {
        if(this._.pendingAddTags[group_hash] && this._.pendingAddTags[group_hash].length) {
            this._.pendingAddTags[group_hash].push(tag);
        } else {
            this._.pendingAddTags[group_hash] = [tag];
        }

        this._.emitter.trigger('pending tag added to group');
    },
    
    removePendingTag: function(tag, group_hash) {
        var index = this._.pendingAddTags[group_hash].indexOf(tag);

        if(index > -1) {
            this._.pendingAddTags[group_hash].splice(index, 1);
            this._.emitter.trigger('pending tag removed from group');
        }
    },
    
    isPending: function(tag, group_hash) {
        if(this._.pendingAddTags.hasOwnProperty(group_hash)) {
            return _.contains(this._.pendingAddTags[group_hash], tag);
        }
        return false;
    },
    
    getFailedTag: function() {
        return this._.failedTag;
    },
    
    setFailedTag: function (tag) {
        this._.failedTag = tag;
    },
    
    getTags: function(){
        return this._.groupsByTag;
    }
});
