Namespace('Imgur.ShareButtons');

Imgur.ShareButtons = Imgur.extend({
    init: function(params) {
        this._ = $.extend({
            resetUrl         : false,
            shareButtonClass : 'addthis_button',
            addthisTimeout   : null,
            shareData: {
                title: null,
                url: null,
                jafoMeta: {}
            },
            globalShareData: null
        }, params);
        this._super(this._);
        
        this.bindButtons();
    },

    elements : {
        $social: $(".social")
    },

    getAddthisConfig: function() {
        return {
            pubid: 'imgur',
            username: 'imgur',
            ui_click: true,
            ui_header_color: '#181817',
            ui_header_background: '#ddddd1',
            ui_offset_top: '0px',
            ui_open_windows: true,
            data_ga_property: 'UA-6671908-2',
            data_ga_tracker: window._gaq,
            data_track_clickback : false,
            data_ga_social : true,
            services_compact: 'stumbleupon,google_plusone_share,pinterest_share,tumblr,email,gmail',
            services_exclude: 'facebook,reddit',
            share: {
                url: this._.shareData.url,
                title: this.getShareTitle()
            }
        };
    },

    initAddthis: function(openMenu) {
        if(typeof window.addthis_config === 'undefined') {
            window.addthis_config = this.getAddthisConfig();
    
            $.getScript(location.protocol+'//s7.addthis.com/js/300/addthis_widget.js#pubid=imgur&domready=1', _.bind(function() {
                this.setupAddthis();
                
                if(openMenu) {
                    var openFunc = function(e) { $('.addthis_button').click(); }
                    
                    window.addthis.addEventListener('addthis.ready', openFunc);
                    this._.addthisTimeout = setTimeout(openFunc, 400);
                }
            }, this), true);
        }
    },

    setShareData: function(data, options) {
        options = _.merge({
            global: false,
        }, options);

        if (options.global) {
            this._.globalShareData = data;
        }

        this._setShareData(data);
    },

    resetShareData: function() {
        if (this._.globalShareData) {
            this._setShareData(this._.globalShareData)
        }

        if (!this._.shareData.url) {
            this._.shareData.url = this._.protocol + '//' + this._.host + this._.url;
        }
    },

    _setShareData: function(data) {
        this._setJafoMeta(data.jafoMeta);
        this._setShareTitle(data.title);
        this._setUrl(data.url);
    },

    _setJafoMeta: function(value) {
        this._.shareData.jafoMeta = value;
    },

    _setShareTitle: function(title) {
        if (!title) {
            title = document.title;
        }
        title = title.replace(/\s*\- (?:Album on )?Imgur$/, '');
        this._.shareData.title = title;
        if('addthis' in window && 'update' in window.addthis) {
            window.addthis.update('share', 'title', title);
        }
    },

    _setUrl: function(url) {
        this._.shareData.url = url;
        
        if('addthis' in window && 'update' in window.addthis) {
            window.addthis.update('share', 'url', url);
        }
    },

    bindButtons: function() {
        this._.shareData.url = (this._.shareData.url) ? this._.shareData.url : this._.protocol + '//' + this._.host + this._.url;
        this.elements.$social.unbind('click');
        this.elements.$social.on('click', 'li span[data-jshook]', _.bind(function(e) {
            var service;

            // Sharing a comment changes the share data. 
            // Post share buttons should reset to the post share data.
            this.resetShareData();

            switch (e.currentTarget.getAttribute('data-jshook')) {
                case 'twitter_custom':
                    this.shareTwitter();
                    break;
                case 'facebook_custom':
                    this.shareFacebook();
                    break;
                case 'reddit_custom':
                    this.shareReddit();
                    break;
                case 'digg_custom':
                    this.shareDigg();
                    break;
                case 'tumblr_custom':
                    this.shareTumblr();
                    break;
                case 'stumbleupon_custom':
                    this.shareStumbleupon();
                    break;
                case 'googleplus_custom':
                    this.shareGoogleplus();
                    break;
                case 'pinterest_custom':
                    this.sharePinterest();
                    break;
                case 'st_email_custom':
                    this.shareEmail();
                    break;
                case 'addthis_custom':
                    this.shareAddthis();
                    break;
            }
        }, this));
        
        $(document.body).on('hover', '.addthis_button', _.bind(function() {
            this.initAddthis();
        }, this));
    },

    bindAddthisButton: function() {
        try {
            window.addthis.button('.'+this._.shareButtonClass);
        } catch(e) {}
    },

    setupAddthis: function() {
        try {
            window.addthis.ost = 0;
            
            window.addthis.addEventListener('addthis.menu.open', _.bind(function(e) {
                this.addthisOpen(e);
            }, this));
            
            window.addthis.addEventListener('addthis.menu.share', _.bind(function(e) {
                // Handle AddThis's internal share and share.track'd custom shares
                this.logSharePostAddthis(e.data.service);
            }, this));

            window.addthis.ready();
            clearTimeout(this._.addthisTimeout);
        } catch (e) {
            this._.addthisTimeout = setTimeout(_.bind(this.setupAddthis, this), 100);
        }
    },

    addthisOpen: function(e) {
        if(!e.data.share) { e.data.share = {}; }
        e.data.share.url = this._.shareData.url;
    },

    logShare: function(service) {
        if (this._.resetUrl) { this.resetShareData(); }
        try {
            addthis_share.url = this._.shareData.url;
            window.addthis.share.track(service, 0, addthis_share, window.addthis_config);
        } catch (e) {
            this.logSharePostAddthis(service);
        }
    },

    logSharePostAddthis: function(service) {
        var url = this._.shareData.url;

        switch (service) {
            case 'google_plusone_share':
                service = 'googleplus';
                break;
            default:
                // Remove _share suffix, specifically from pinterest
                service = service.replace(/_share$/, '');
                break;
        }

        if(this._.shareData.jafoMeta.hash) {
            $.post('//'+this._.host+'/share/share', {hash: this._.shareData.jafoMeta.hash});
        }
        var url = this._.shareData.url;

        Imgur.Util.jafoLog({
            event: 'share',
            meta: _.merge({
                shareTo: service
           }, this._.shareData.jafoMeta)
        });

        try {
            Imgur.Util.getGoogleTracker();
            if (typeof __ga === 'function') {
                __ga('send', 'social', service, 'share', url);
            }
        } catch(e) { }
    },

    getShareTitle: function() {
        if (!this._.shareData.title) {
            this._setShareTitle();
        }

        return this._.shareData.title;
    },
    
    shareTwitter: function() {
        this.logShare('twitter');
        Imgur.Util.newWindow('http://twitter.com/share?url=' + encodeURIComponent(this._.shareData.url) + '&text=' + encodeURIComponent(this.getShareTitle()) + '&via=imgur', 550, 425);
    },

    shareFacebook: function() {
        this.logShare('facebook');
        Imgur.Util.newWindow('http://www.facebook.com/sharer.php?u=' + encodeURIComponent(this._.shareData.url), 755, 425);
    },

    shareReddit: function() {
        this.logShare('reddit');
        Imgur.Util.newWindow('http://reddit.com/submit?url=' + encodeURIComponent(this._.shareData.url) + '&title=' + encodeURIComponent(this.getShareTitle()));
    },

    shareDigg: function() {
        this.logShare('digg');
        Imgur.Util.newWindow('http://digg.com/submit?phase=2&url=' + encodeURIComponent(this._.shareData.url), 1060, 655)
    },

    shareTumblr: function() {
        this.logShare('tumblr');
        var image = $('link[rel="image_src"]').attr('href');

        if(image) {
            var url = 'http://www.tumblr.com/share/photo?source='+encodeURIComponent(image)+'&caption='+encodeURIComponent(this.getShareTitle())+'&click_thru='+encodeURIComponent(this._.shareData.url);
        } else {
            var url = 'http://www.tumblr.com/share/link?url='+encodeURIComponent(this._.shareData.url)+'&name='+encodeURIComponent(this.getShareTitle())+'&description='+encodeURIComponent($('meta[name="description"]').attr('content'));
        }

        Imgur.Util.newWindow(url, 450, 430);
    },

    shareStumbleupon: function() {
        this.logShare('stumbleupon');
        Imgur.Util.newWindow('http://www.stumbleupon.com/submit?url=' + encodeURIComponent(this._.shareData.url), 1060, 500);
    },
    
    shareGoogleplus: function() {
        this.logShare('googleplus');
        Imgur.Util.newWindow('https://plus.google.com/share?url=' + encodeURIComponent(this._.shareData.url), 520, 500);
    },

    sharePinterest: function () {
        this.logShare('pinterest');
        var image = $('link[rel="image_src"]').attr('href');

        Imgur.Util.newWindow('http://pinterest.com/pin/create/button/?url=' + encodeURIComponent(this._.shareData.url) + '&description=' + encodeURIComponent(this.getShareTitle()) + '&media=' + image, 800, 340);
    },

    shareEmail: function() {
        // Do nothing, AddThis triggers everything
    },

    shareAddthis: function() {
        this.initAddthis(true);
    }

});

Class.addSingleton(Imgur.ShareButtons);
