Namespace('Imgur.Comment');

Imgur.CommentTree = React.createClass({
    statics: {
        EXPAND_MODE_FORCE_COLLAPSED: -1,
        EXPAND_MODE_DEFAULT_COLLAPSED: 0,
        EXPAND_MODE_FORCE_EXPANDED: 1
    },

    displayName: 'CommentTree',

    mixins: [React.addons.PureRenderMixin],

    propTypes: {
        comment: React.PropTypes.object.isRequired,
        image: React.PropTypes.object.isRequired,
        expandMode: React.PropTypes.number.isRequired,
        expandedFromPermalink: React.PropTypes.bool.isRequired,
        groupingBadReplies: React.PropTypes.bool.isRequired,
        contextids: React.PropTypes.object.isRequired,
        childmap: React.PropTypes.object.isRequired,
        votemap: React.PropTypes.object.isRequired,
        level: React.PropTypes.number,
        maxLevel: React.PropTypes.number,
        votable: React.PropTypes.bool
    },

    contextTypes: {
        onContextChange: React.PropTypes.func.isRequired
    },

    getInitialState: function() {
        return {
            removing: false,
            expanded: this.props.expandMode === Imgur.CommentTree.EXPAND_MODE_FORCE_EXPANDED || this.props.expandedFromPermalink
        };
    },

    getDefaultProps: function() {
        return {
            level: 0,
            maxLevel: 7,
            votable: true
        };
    },

    componentWillReceiveProps: function(nextProps) {
        if (this.props.expandMode != nextProps.expandMode || nextProps.expandedFromPermalink) {
            if(this.state.expanded && nextProps.expandMode <= Imgur.CommentTree.EXPAND_MODE_FORCE_COLLAPSED) {
                this.setState({
                    expanded: false
                });
            } else if(!this.state.expanded && nextProps.expandMode === Imgur.CommentTree.EXPAND_MODE_FORCE_EXPANDED || nextProps.expandedFromPermalink) {
                this.setState({
                    expanded: true
                });
            }
        }
    },

    handleCommentClick: function(e) {
        var $target = $(e.target);

        if(!$target.is('a, button') && !$target.closest('.comment-create-reply').length && $target.closest('.usertext, .comment-reply-count').length) {
            var have_children = this.props.comment.id in this.props.childmap;

            var metadata = {
                commentId: this.props.comment.id,
                hash: this.props.comment.hash,
                isAlbum: this.props.comment.on_album,
                nsfw: this.props.image.nsfw,
                parentId: this.props.comment.parent_id,
                location: $(e.target).closest('.comment-reply-count').length ? 'collapseLink' : 'commentBox'
            };

            var metadata = {
                commentId: this.props.comment.id,
                hash: this.props.comment.hash,
                isAlbum: this.props.comment.on_album,
                nsfw: this.props.image.nsfw,
                parentId: this.props.comment.parent_id
            };

            e.stopPropagation();

            this.setState({
                removing: this.state.expanded
            }, _.bind(function() {
                this.setState({
                    removing: false,
                    expanded: !this.state.expanded
                });
            }, this));

            if(this.state.expanded === false && have_children === true){
                Imgur.Util.jafoLog({
                        event: 'commentExpandInvoked',
                        meta: metadata
                });
            } else if(this.state.expanded === true && have_children === true){
                Imgur.Util.jafoLog({
                    event: 'commentCollapseInvoked',
                    meta: metadata
                });
            }
        }
    },
    
    handleSubmit: function() {
        this.setState({
            expanded: true
        });
    },

    handleLoadMoreClicked: function(e) {
        this.context.onContextChange(e, this.props.comment.id, 0);
    },

    render: function() {
        var subtree;

        var have_children = this.props.comment.id in this.props.childmap;
        var replies = this.props.childmap[this.props.comment.id] &&
            this.props.childmap[this.props.comment.id].length || 0;

        if(this.props.level < this.props.maxLevel) {
            if(this.state.expanded) {
                subtree = React.createElement(Imgur.CommentList,{
                    image: this.props.image, 
                    contextids: this.props.contextids,
                    childmap: this.props.childmap,
                    votemap: this.props.votemap,
                    expandMode: this.props.expandMode,
                    expandedFromPermalink: this.props.expandedFromPermalink,
                    groupingBadReplies: this.props.groupingBadReplies,
                    level: this.props.level + 1, 
                    parent_id: this.props.comment.id,
                    votable: this.props.votable,
                    removing: this.state.removing,
                    touchEnabled: this.props.touchEnabled,
                });
            }
        } else if (have_children) {
            subtree = React.DOM.a({className: 'load-more', href: 'javascript:void(0)', onClick: this.handleLoadMoreClicked}, "load more");
        }

        var className = classNames({
            'comment': true,
            'expanded': this.state.expanded
        });

        return React.DOM.div({onKeyDown: this.handleKeyDown, 'data-level': this.props.level, onClick: this.handleCommentClick, className: className},
            React.createElement(Imgur.Comment,{
                childmap: this.props.childmap,
                contextids: this.props.contextids,
                votemap: this.props.votemap,
                image: this.props.image,
                comment: this.props.comment,
                votable: this.props.votable,
                onSubmit: this.handleSubmit,
                expanded: this.state.expanded,
                haveChildren: have_children && this.props.level < this.props.maxLevel && replies > 0,
                level: this.props.level,
                collapsing: this.state.removing,
                touchEnabled: this.props.touchEnabled,
            }),
            React.createElement(React.addons.CSSTransitionGroup,{
                key: 'just-collapsed-' + this.props.image.hash,
                transitionName: "collapsetree",
                transitionEnter: false,
                transitionLeaveTimeout: 250
            },
                subtree
            )
        );
    }
});
