Namespace('Imgur.Tag.Store');

Imgur.Tag.Store.OutsideActiveTag = Class.extend({
    init: function(emitter) {
        this._ = {
            emitter: emitter,
            activeTag: null
        };
    },

    getIsActive: function() {
        return (null != this._.activeTag);
    },

    getActiveTag: function() {
        return this._.activeTag;
    },

    getActiveTagEncoded: function() {
        return this._.activeTag.replace(new RegExp(' ', 'g'), '_');
    },

    setActiveTag: function(tag_name, fromPopState) {
        this._.activeTag = tag_name;
        this._.emitter.trigger('new outside active tag', !!fromPopState);
    },

    deactivate: function(fromPopState) {
        this._.activeTag = null;
        this._.emitter.trigger('new outside active tag', !!fromPopState);
    }
});
