Namespace('Imgur.Tag');

Imgur.Tag.SuggestModelNoProvider = Imgur.Tag.Model.extend({
    init: function(ajax, autoSuggestUrl) {
        this._super(ajax, null);
        this._.autoSuggestUrl = autoSuggestUrl;
    },
    
    fetch: function(query, cb) {
        if(query) {
            this._super(query, this._.autoSuggestUrl + '/tags/_suggest', cb, JSON.stringify({
                'tags' : {
                    'text' : query,
                    'completion' : {
                        'field' : 'name_suggest',
                        'size': 10,
                        "fuzzy" : {
                            "edit_distance" : 1
                        }
                    }
                }
            }));
        } else {
            cb('', []);
        }
    },

    responseWasSuccessful: function(response) {
        return response._shards.successful > 0;
    },

    _handleResponseSuccess: function(queryContext, response) {
        var tags = [];

        var query = queryContext;

        _.each(response.tags[0].options, _.bind(function(tag_obj, key) {
            tags.push({
                tag: tag_obj.text,
                subscribers: tag_obj.payload.subscribed,
                images: tag_obj.payload.images
            });
        }, this));

        return tags;
    },

    _handleError: _.noop
});
