Imgur.LinkGenerator = Class.extend({
    init: function() {
        this._ = {
            item: null,
            thumbsize: null,
            threshold: null
        };
    },

    setItem: function(item) {
        this._.item = item;
        return this;
    },

    setThumbSize: function(thumbsize) {
        this._.thumbsize = thumbsize;
        return this;
    },

    setThreshold: function(threshold) {
        this._.threshold = threshold;
        return this;
    },

    reset: function() {
        this._.item = null;
        this._.thumbsize = '';

        return this;
    },

    imageLink: function(isAlbum) {
        return 'http://' + Imgur.Environment.host + '/' + (isAlbum ? 'a/' : '') + this._.item.hash;
    },

    directishLink: function() {
        var ext = (this._.thumbsize == '' && this._.item.ext == '.gif' && this._.item.animated) ? '.gifv' : this._.item.ext;
        return 'http:' + Imgur.Environment.cdnUrl + '/' + this._.item.hash + this._.thumbsize + ext;
    },

    directLink: function() {
        return 'http:' + Imgur.Environment.cdnUrl + '/' + this._.item.hash + this._.thumbsize + this._.item.ext;
    },
    
    htmlImage: function() {
        if(this._.thumbsize == '' && this._.item.ext == '.gif' && this._.item.animated && this._.item.size > this._.threshold) {
            return '<iframe class="imgur-embed" width="100%" height="' + this._.item.height + '" frameborder="0" src="' + this.directishLink() + '#embed"></iframe>';
        }

        return '<a href="'+this.imageLink()+'"><img src="'+this.directLink()+'" title="source: imgur.com" /></a>';
    },

    markdown: function() {
        var title;
        var ext = (this._.thumbsize == '' && this._.item.ext == '.gif' && this._.item.animated) ? '.gifv' : this._.item.ext;

        if(this._.item.title) {
            title = this._.item.title;
        } else {
            title = 'Imgur';
        }

        return '[' + title + '](http:' + Imgur.Environment.cdnUrl + '/' + this._.item.hash + this._.thumbsize + ext + ')';
    },

    bbcode: function() {
        return '[img]http:' + Imgur.Environment.cdnUrl + '/' + this._.item.hash + this._.thumbsize + this._.item.ext + '[/img]';
    },

    linkedBbcode: function() {
        if(this._.thumbsize == '' && this._.item.ext == '.gif' && this._.item.animated && this._.item.size > this._.threshold) {
            return '[url=http://' + Imgur.Environment.host + '/' + this._.item.hash + ']http:' + Imgur.Environment.cdnUrl + '/' + this._.item.hash + this._.thumbsize + '.gifv[/url]';
        }

        return '[url=http://' + Imgur.Environment.host + '/' + this._.item.hash + '][img]http:' + Imgur.Environment.cdnUrl + '/' + this._.item.hash + this._.thumbsize + this._.item.ext + '[/img][/url]';
    },

    deletion: function() {
        return 'https://' + Imgur.Environment.host + '/delete/' + this._.item.deletehash;
    },

    embed: function() {
        var source = (Imgur.Environment.host === 'imgur.com' ? 's.imgur.com/min' : Imgur.Environment.host + '/include/minified') + '/embed.js',
            title  = this._.item.title;

        if(!title) {
            title = 'View post on imgur.com';
        }

        var prefix = this._.item.is_album ? 'a/' : '';

        return '<blockquote class="imgur-embed-pub" lang="en" data-id="' + prefix + this._.item.hash + '"><a href="//' +
            Imgur.Environment.host + '/' + prefix + this._.item.hash + '">' + _.escape(title) + '</a></blockquote><script async src="//' + source + '" charset="utf-8"></script>';
    }
});
