Namespace('Imgur.Tag');

Imgur.Tag.Sentence = Class.extend({
    init: function($before, $middle, groupStore) {
        this._ = {
            groupStore: groupStore,
            el: {
                $before: $before,
                $middle: $middle
            }
        };
    },

    get$Before: function() {
        return this._.el.$before;
    },

    get$Middle: function() {
        return this._.el.$middle;
    },

    change: function(mode) {
        if('hot' == mode) {
            this._.el.$before.text('The');
        } else if('gallery-other' == mode || 'usersub' == mode || 'subreddit' == mode) {
            this._.el.$before.text('');
        } else if('tag' == mode) {
            this._.el.$before.text('Images tagged with');
        } else if('gallery' == mode) {
            this._.el.$before.text('Your');
        }

        var middle_text = ', sorted by';

        if('hot' == mode) {
            middle_text = 'images on the Internet' + middle_text;
        } else if('usersub' == mode || 'subreddit' == mode) {
            middle_text = 'images' + middle_text;
        } else if('gallery' == mode || 'gallery-other' == mode) {
            middle_text = ' gallery' + middle_text;
        }

        this._.el.$middle.text(middle_text);
    }
});
