Namespace('Imgur.Gallery');

Imgur.Gallery.MainOutside = Imgur.Gallery.Outside.extend({
    init: function(outsideActiveTagStore, emitter, params, history) {
        this._super(emitter, params, history);
        this._.outsideActiveTagStore = outsideActiveTagStore;
    },

    handleTagChanged: function(fromPopState) {
        var isActive = this._.outsideActiveTagStore.getIsActive();

        if(isActive) {
            this._handleTagSelect(fromPopState);
        } else {
            this._handleTagDeselect(fromPopState);
        }
    },

    _handleTagDeselect: function() {
        this._.overRideUrl = null;
        this._.history.addWindowState('initial', 'tagDeselect', this.getSectionUrl());
        this.refetchPage();
    },

    _handleTagSelect: function(fromPopState) {
        var tagName = this._.outsideActiveTagStore.getActiveTag(),
            tagNameUrlEncoded = encodeURIComponent(this._.outsideActiveTagStore.getActiveTagEncoded());

        this._.overRideUrl = '/t/'+tagNameUrlEncoded;

        if(this._.sort == 'rising') {
            this._.sort = 'viral';
        }

        if(!fromPopState) {
            if(window.history.replaceState && !window.history.state) {
                window.history.replaceState('initial', 'initial', window.location.href);
            }

            this._.history.addWindowState('tag', 'Imgur - '+tagName, this._.overRideUrl);
        }

        this._updatePageOnTagChange(this._.overRideUrl);
    },

    _updatePageOnTagChange: function(url) {
        var $sort    = $('#sort');
        var $section = $('#section');
        var $rising  = $sort.find('.item[value="rising"]');
        var $top     = $sort.find('.item[value="top"]');
        
        $section.find('.selected').removeClass('selected');

        if($rising.hasClass('selected')) {
            var activeEl = $sort.find('.item[value="'+this._.sort+'"]').addClass('selected');
            $sort.find('.selection').html(activeEl.text());
        }

        $rising.hide();
        $('.sort-options').hide();

        this.refetchPage();
    },

    refetchPage: function() {
        if(this._.vars.loading) {
            return;
        }

        if(this._.vars.scrolledPage > 0) {
            this._.vars.scrolledImageSet = -1;
        }
        
        this._.vars.scrolledPage = 0;

        Imgur.InfiniteScroll.getInstance().stopInfiniteScroll();
        $(this._.el.outside.pageDivider).remove();
        this.fadeInLoader();
        
        this.fetchImages(0, 0, _.bind(function() {
            if(!this._.vars.loading) {
                Imgur.InfiniteScroll.getInstance().continueInfiniteScroll();
                this.initOutsideInfScroll();
            }
        }, this));
    },

});
