Namespace('Imgur.Tag');

Imgur.Tag.Comparator = Class.extend({
    compare: function(left, right) {
        var leftName = left.getName(),
            rightName = right.getName();

        if(rightName == leftName) {
            return 0;
        }

        var result = [leftName, rightName];
        result.sort();

        if(result[0] == leftName) {
            return -1;
        } else {
            return 1;
        }
    }
});
