Namespace('Imgur.Caption.Reply');

Imgur.Caption.Reply = React.createClass({
    displayName: 'Reply',

    propTypes: {
        image: React.PropTypes.object.isRequired,
        parentId: React.PropTypes.number,
        parentAuthorId: React.PropTypes.number,
        autofocus: React.PropTypes.bool,
        onSuccess: React.PropTypes.func,
        closeReply: React.PropTypes.func
    },

    contextTypes: {
        msid: React.PropTypes.string.isRequired,
        onSubmit: React.PropTypes.func.isRequired
    },

    getDefaultProps: function() {
        return {
            maxLength: 140,
            autofocus: false
        };
    },

    getInitialState: function() {
        return {
            text: '',
            error: '',
            showingLoader: false
        };
    },

    componentDidMount: function() {
        $(this.refs.textarea).autoResize({animate: false, extraSpace: 0});

        if (this.props.autofocus) {
            this.refs.textarea.focus();
        }
    },

    getValue: function() {
        return $.trim(this.state.text);
    },

    handleSubmit: function(e) {
        e.preventDefault();
        e.stopPropagation();
        this._submit();
    },

    handleError: function(xhr) {
        var response = _.isObject(xhr.responseJSON) ? xhr.responseJSON : xhr;

        if(response && 'data' in response && 'error' in response.data && 'message' in response.data.error) {
            this.setState({
                error: response.data.error.message
            });
        } else {
            this.setState({
                error: 'An unknown error occurred.'
            });
        }
    },

    _submit: function() {
        if (this.state.text.length === 0 || this.state.text.length > this.props.maxLength) {
            return;
        }

        var postData = {
            parent_id: this.props.parentId,
            parent_author: this.props.parentAuthorId,
            caption: this.state.text,
            sid: this.context.msid,
            gallery: Imgur.Gallery.getInstance().getGalleryMetadata()
        };

        if(!Imgur.Environment.signed) {
            Imgur.getInstance().signin('commentAttempt');

            // When the controller sees this, it will 
            // redirect to the #id URL when the user logs back in
            postData.redirect = true; 
        }

        this.setState({
            showingLoader: true
        });

        var jqx = $.ajax({
            url: '/gallery/action/caption/' + this.props.image.hash,
            type: 'POST',
            data: postData
        });
        
        jqx.always(_.bind(function() {
            this.setState({
                showingLoader: false
            });
        }, this));

        Imgur.Util.jafoLog({
            event: 'commentSubmissionButton',
            meta: {
                parentId: this.props.parentId || null,
                hash: this.props.image.hash
            }
        });
                
        if(Imgur.Environment.signed) {
            jqx.done(_.bind(function(response) {
                if(response.success) {
                    response.data.caption.just_inserted = true;

                    this.props.onSuccess();
                    Imgur.Caption.getInstance().mergeCaptions(response.data.caption.hash, [response.data.caption]);

                    var vote = {caption_id: response.data.caption.id, vote: 'up'};
                    Imgur.Caption.getInstance().mergeVotes(response.data.caption.hash, [vote]);

                    this.context.onSubmit(response.data.caption);
                } else {
                    this.handleError(response);
                }
            }, this));

            jqx.fail(_.bind(this.handleError, this));
        }
    },

    handleTextChanged: function(e) {
        this.setState({
            text: e.target.value
        });
    },

    handleClick: function(e) {
        // Stop clicks from propagating up to the parent, because click triggers an expand / collapse
        e.stopPropagation();
        this.refs.textarea.focus();
    },

    handleKeyDown: function(e) {
        if(13 === e.keyCode) { // Enter key
            e.preventDefault();
            this._submit();
        } else if(27 === e.keyCode) { // Escape key
            this.refs['textarea'].blur();
        }
    },

    render: function() {
        var counter = this.props.maxLength - this.state.text.length,
            cx = classNames;

        var formCls = cx({
            'caption-create': true,
            'loading': this.state.showingLoader,
            'counter-exceeded': counter < 0
        });

        return React.DOM.form({
            className: formCls, 
            onClick: this.handleClick, 
            onSubmit: this.handleSubmit
        },
            React.DOM.span({className: 'icon-x right pointer', onClick: this.props.onClose}),
            React.DOM.textarea({
                ref: 'textarea',
                onKeyDown: this.handleKeyDown,
                onKeyUp: this.handleKeyUp,
                onChange: this.handleTextChanged,
                onFocus: this.handleFocus,
                placeholder: 'Write a comment',
                value: this.state.text
            }),
            (this.state.error.length > 0 ? React.DOM.div({className: 'error'}, this.state.error) : ''),
            React.DOM.div({className: 'summary'},
                React.DOM.input({value: 'Post', type: 'submit', id: 'submit-comment', className: 'right btn btn-main', disabled: ((counter == this.props.maxLength || counter < 0) ? 'disabled' : '')}),
                React.DOM.a({className: 'rules left', href: '/rules', target: '_blank'}, 'remember the community rules'),
                React.DOM.div({className: 'counter right'}, counter)
            )
        );
    }
});
