Namespace('Imgur.Element');

Imgur.Element.OutsideSortedListEmptyable = Imgur.Element.SortedListEmptyable.extend({
    hasEmptyMessage: function() {
        return this._.el.$selectorContainer.hasClass('noGroups');
    },

    hideEmptyMessage: function() {
        if(this.hasEmptyMessage()) {
            this._.el.$selectorContainer.removeClass('noGroups');
        }
    },

    showEmptyMessage: function() {
        this._replaceEmptyMessage();

        if(!this.hasEmptyMessage()) {
            this._.el.$selectorContainer.addClass('noGroups');
        }
    }
});
