Namespace('Imgur.Tag');

/**
 * Common base class for gallery-image-tags and normal and suggested tags
 */
Imgur.Tag.Tag = Class.extend({
    init: function($container) {
        this._ = {
            el: {
                $container: $container,
                $name: Object.getPrototypeOf(this).get$NameFrom$Container($container)
            }
        };
    },

    insertBefore: function(tag) {
        this._.el.$container.before(tag.get$Container());
    },

    remove: function() {
        this._.el.$container.remove();
    },

    getName: function() {
        return this._.el.$name.text().toLowerCase();
    },

    get$Container: function() {
        return this._.el.$container;
    },

    get$Name: function() {
        return this._.el.$name;
    },

    get$NameFrom$Container: function($container) {
        return $container.children('.name'); // name is also the sub-group-line name class so we can't find()
    },

    getNameFrom$Container: function($container) {
        return $container.children('.name').text();
    },

    equals: function(other) {
        return (other instanceof Imgur.Tag.Tag) &&
               (other.getName() == this.getName());
    },

    addSelection: function() {
        this.get$Container().addClass('selected');
    },

    removeSelection: function() {
        this.get$Container().removeClass('selected');
    },    
});
