        return React.createElement(
            'div',
            { className: 'modal-backdrop', onClick: this.unmount },
            React.createElement(
                'form',
                { onSubmit: this.handleSubmit, onClick: this.stopPropagation, ref: 'form' },
                React.createElement(
                    'div',
                    { className: 'modal bad-ads-modal' },
                    React.createElement(
                        'div',
                        { className: 'bad-ads-modal-header' },
                        React.createElement(
                            'h2',
                            { className: 'headline' },
                            'Report Ad'
                        ),
                        React.createElement('div', { className: 'icon-x', onClick: this.unmount })
                    ),
                    React.createElement(
                        'div',
                        { className: 'bad-ads-modal-body' },
                        React.createElement(
                            'p',
                            null,
                            'See an ad that may violate our ',
                            React.createElement(
                                'a',
                                { href: 'https://help.imgur.com/hc/en-us/articles/205107685-Bad-ads' },
                                'advertising guidelines'
                            ),
                            '? It\u2019s easy to report it using this form.'
                        ),
                        React.createElement(
                            'p',
                            null,
                            'Please indicate the location of the ad:'
                        ),
                        React.createElement('div', { className: 'bad-ads-layouts' }),
                        React.createElement(
                            'radiogroup',
                            null,
                            React.createElement(
                                'label',
                                { className: 'radio' },
                                React.createElement('input', { type: 'radio', name: 'slotname', value: 'top_banner' }),
                                'Upper Left'
                            ),
                            React.createElement(
                                'label',
                                { className: 'radio' },
                                React.createElement('input', { type: 'radio', name: 'slotname', value: 'under_sidebar' }),
                                'Middle Right'
                            ),
                            React.createElement(
                                'label',
                                { className: 'radio' },
                                React.createElement('input', { type: 'radio', name: 'slotname', value: 'pop_sky' }),
                                'Lower Right'
                            )
                        ),
                        React.createElement('textarea', { className: 'bad-ads-comments', ref: 'comments', placeholder: 'Please provide us with additional context for your submission' }),
                        React.createElement('input', { type: 'submit', value: 'Report', className: 'btn-action btn-large right' })
                    )
                )
            )
        );
