Namespace('Imgur.Album');

Imgur.Album = Class.extend({
    init : function(params) {
        params = params || {};

        this._ = $.extend(true, {
            id            : null,
            album         : null,
            images        : null,
            cover         : null,
            layout        : null,
            cdnUrl        : null,
            preloadCount  : 4,
            operation     : null,
            mobile        : false,
            initialImage  : true,

            el: {
                links: {
                    createAlbum             : $('.album-create-link'),
                    changeSettings          : $('.album-settings-link'),
                    nonAuthorChangeSettings : $('.album-layout-link'),
                    changeImages            : $('.album-images-link.unpublished'),
                    changeCover             : $('.album-cover-link'),
                    deleteAlbum             : $('.album-delete-link'),
                    embedAlbum              : $('.album-embed-link'),
                    downloadAlbum           : $('.album-download-link'),
                    hideAlbum               : $('.album-hide-link')
                },

                embedAlbumForm: Imgur.Utilities.Embed.elEmbedAlbumForm(),

                colorboxEmbed : Imgur.Utilities.Embed.elColorboxEmbed(),

                downloadAlbumForm: {
                    id         : '#download-album-form',
                    emailInput : $('#download-album-form #email'),
                    form       : $('#download-album-form #download-form'),
                    message    : $('#message'),
                    save       : $('#download-album-form #save'),
                    loader     : $('#download-loader')
                },

                createAlbumForm: {
                    id               : '#create-album-form',
                    form             : $('#create-album-form form'),
                    header           : $('#create-album-form h1'),
                    titleInput       : $('#album-title'),
                    descriptionInput : $('#album-description'),
                    layoutInput      : $('#album-layout input[type=radio]'),
                    privacyInput     : $('#album-privacy input[type=radio]'),
                    loader           : $('#create-album-loader'),
                    cover            : $('#album-cover'),
                    hiddenIcon       : $('.privacy-icon.hidden'),
                    secretIcon       : $('.privacy-icon.secret'),
                    exlanationPublic : $('.privacy-explanation.public'),
                    exlanationHidden : $('.privacy-explanation.hidden'),
                    exlanationSecret : $('.privacy-explanation.secret'),
                    privacyTitle     : $('.privacy-title'),
                    errorbox         : $('#create-album-form .error'),
                    submitButton     : $('#create-album-form input[type="submit"]')
                },

                changeLayout: {
                    layoutButton : $('.userSessionLayout'),
                    layoutForm   : $('#userLayoutForm')
                },

                albumImagesForm: {
                    id              : '#album-images',
                    form            : $('#album-images form'),
                    selectHeader    : $('#album-images-select-header'),
                    coverHeader     : $('#album-images-cover-header'),
                    menuFirstItemId : '#album-images .mode.start',
                    imagesMenu      : $('#images-menu'),
                    mainColumn      : $('#main-column')
                },

                deleteAlbumForm: {
                    id     : '#delete-album-form',
                    yes    : $('#delete-album-yes'),
                    no     : $('#delete-album-no'),
                    loader : $('#delete-album-form .small-loader')
                },

                hideAlbumForm: {
                    id     : '#hide-album-form',
                    yes    : $('#hide-album-yes'),
                    no     : $('#hide-album-no'),
                    loader : $('#hide-album-form .small-loader')
                },

                layout: {
                    global: {
                        title                   : $('#title'),
                        description             : $('#description'),
                        container               : $('#image-container'),
                        mainImage               : null,
                        thumbsCarousel          : $('#thumbs-top, #thumbs-bottom'),
                        thumbsCarouselContainer : $('.thumbs-carousel'),
                        thumbsCarouselNext      : $('.thumbs-carousel .carousel-button.next'),
                        thumbsCarouselPrev      : $('.thumbs-carousel .carousel-button.prev'),
                        thumbsCarouselTop       : $('#thumbs-top'),
                        thumbsCarouselBottom    : $('#thumbs-bottom'),
                        nextImage               : $('#next'),
                        prevImage               : $('#prev'),
                        imageHoverIconClass     : '.image-hover',
                        actionOptionsClass      : '.action-options',
                        actionOptionsItems      : $('.image-hover .options .item')
                    },

                    blog: {
                        leftPanel : $('#content .main'),
                        sidebar : $('#scrollpanel')
                    },

                    embed: {
                        offsite   : $('#offsite')
                    }
                },

                subdomain: {
                    container         : $('.left .panel'),
                    albumClass        : '.album',
                    editButton        : $('.edit-button'),
                    editOptionsClass  : '.edit-options',
                    editOptionsItems  : $('.options .item')
                }
            },

            layouts: {
                blog        : 'b'
            },

            operations: {
                embed        : 'embed'
            },

            messages: {
                createAlbum           : 'Create an album',
                albumSettings         : 'Change album settings',
                switchLayout          : 'Switch album layout',
                updateSettings        : 'Album settings have been updated',
                updateImages          : 'Album images have been updated',
                updateCover           : 'Album cover has been updated',
                deleteAlbum           : 'Album has been deleted',
                hideAlbum             : 'Album has been hidden',
                privateGalleryMessage : 'This album is in the gallery, please remove it before making it secret.',
                privateGalleryError   : 'A gallery album\'s privacy cannot be set to secret.'
            },

            vars: {
                currentImageIndex       : 0,
                ajaxURL                 : '/ajaxalbums/',
                albumImages             : null,
                interval                : null,
                loadingViewport         : false,
                bodyHeight              : 0,
                thumbsSpeed             : 400,
                embedClipboard          : null,
                scrolling               : false,
                creatingAlbum           : false,
                scrolledPage            : 0,
                bindings                : {
                    create : false,
                    images : false,
                    del    : false,
                    thumbs : false,
                    main   : false
                },
                currentTheme            : 'dark',
                embedAlbumFormLoaded    : false,
                newEmbedAlbumFormLoaded : false,
                needCaptchaToProceed    : false
            },
            themes: {
                dark: {
                    background: '292929',
                    text      : 'ffffff',
                    link      : '4e76c9'
                },
                light: {
                    background: 'f2f2f2',
                    text      : '1a1a1a',
                    link      : '4e76c9'
                },
                custom: {
                    background: '292929',
                    text      : 'ffffff',
                    link      : '4e76c9'
                }
            },
        }, params);

        this._ = $.extend(this._ , Imgur.getInstance().getSettings());
        this._.el.colorboxEmbed.embedCodeInput.click($(this).selectAll);
    },

    findImageIndex : function(hash) {
        for(var i = 0; i < this._.images.count; ++i) {
            if(this._.images.images[i].hash == hash) {
                return i;
            }
        }

        return 0;
    },

    getStartingIndex : function() {
        var hash = Imgur.getInstance().getPageHash();

        if(hash) {
            if(isNaN(hash)) {
                return this.findImageIndex(hash);
            } else {
                return parseInt(hash);
            }
        }

        return 0;
    },

    loadImage : function(img) {
        if(!img) return;
        if(!img.hasClass('unloaded')) return;

        img.attr('src', img.attr('data-src'));
        img.removeClass('unloaded');

        var loadImage = new Image();
        loadImage.src = img.attr('src');

        var self = this;

        loadImage.onload = function() {
            var src  = $(this).attr('src');
            var hash = src.substring(src.lastIndexOf('/')+1, src.lastIndexOf('.'));
        };

        // If the thumb fails to load then load the original
        loadImage.onerror = function() {
            var src      = $(this).attr('src');
            var hash_len = src.substring(src.lastIndexOf('/')+1, src.lastIndexOf('.')).length;

            // If the image is a thumbnail
            if(hash_len === 8 || hash_len === 6) {
                var url = src.substring(0, src.lastIndexOf('.') - 1);

                if(url) {
                    url += '.jpg';
                    this.src = url;
                    $(img).attr('src', url);

                    if($(img).parent().hasClass('zoom')) {
                        $(img).parent().attr('href', this.src);
                    }
                }
            }
        }
    },

    initAlbumImages : function(changeCover, showMessage, callback, skipImageSetup) {
        var self = this;

        if(this._.images == null) {
            $.getJSON(
                '/ajax/images',
                {
                album    : this._.id,
                sort    : 0,
                order    : 1,
                page    : 1,
                perPage    : 999999
                },
                function(response) {
                    var data = response.data;
                    self._.images             = {};
                    self._.images['images']   = data.images;
                    self._.images['count']    = data.count;

                    if(skipImageSetup) {
                        if(typeof callback == 'function') {
                            callback();
                        }
                    } else {
                        self.setupAlbumImages(changeCover, showMessage, callback);
                    }

                    self._.images = null;
                }
            );
        } else {
            if(skipImageSetup) {
                if(typeof callback == 'function') {
                    callback();
                }
            } else {
                self.setupAlbumImages(changeCover, showMessage, callback);
            }
        }
    },

    setupAlbumImages : function(changeCover, showMessage, callback) {
        var images = [];

        if(changeCover) {
            images[this._.cover] = true;

            this._.el.albumImagesForm.coverHeader.show();
            this._.el.albumImagesForm.selectHeader.hide();
        } else {
            for(i = 0; i < this._.images.count; ++i) {
                images[this._.images.images[i].hash] = true;
            }

            this._.el.albumImagesForm.coverHeader.hide();
            this._.el.albumImagesForm.selectHeader.show();
        }

        var self = this;

        // If the album is empty when we enter this colorbox to add/remove images from it, always show non-album images.
        var albumImageMode = 1;
        if( (this._.images.count && this._.images.count>0) ||
            (this._.images.images && this._.images.images.length && this._.images.images.length>0)
        ) {
            albumImageMode = this._.id;
        }

        var albumImagesSettings =
            {
            host               : this._.host,
            album              : this._.id,
            images             : images,
            cdnUrl             : this._.cdnUrl,
            totalCounter       : $('<div/>'),
            totalCounterSuffix : $('<div/>'),
            imagesPerPage      : 64,
            changeCover        : changeCover,
            mode               : albumImageMode,

            onSubmitComplete   : function(response) {
                $.colorbox.close();

                if(showMessage) {
                    if(changeCover) {
                        humanMsg.displayMsg(self._.messages.updateCover);
                    } else {
                        humanMsg.displayMsg(self._.messages.updateImages);
                    }
                }

                var self_ = self;

                if(changeCover) {
                    $(document).bind(
                        'cbox_closed',
                        function() {
                            $(document).unbind('cbox_closed');
                            self_._.cover = response.data[0][0];

                            $('#album-'+self._.id).attr('data-cover', response.data[0][0]+'b.jpg')
                                                  .find('.cover img').attr('src', self_._.cdnUrl+'/'+response.data[0][0]+'s.jpg');

                            self_.changeSettings();
                        }
                    );
                } else {
                    if(typeof callback == 'function') {
                        callback(response.data);
                    } else if(location.pathname != '/' > 0) {
                        location.reload();
                    }
                }
            }
        };

        this._.vars.albumImages = Imgur.Album.Images.getInstance();
        this._.vars.albumImages.init(albumImagesSettings);
    },

    bindchangeImages : function() {
        var self = this;

        this._.vars.bindings.images = true;

        var albumID = self._.id;

        this._.el.albumImagesForm.form.submit(function() {
            self._.vars.albumImages._.album = albumID;
            self._.vars.albumImages.submit();
            return false;
        });
    },

    // ***************** DELETE ALBUM FUNCTIONS *****************

    bindDeleteAlbum : function(callback) {
        this._.vars.bindings.del = true;

        this._.el.deleteAlbumForm.no.click(function() {
            $.colorbox.close();
        });

        var self = this;

        this._.el.deleteAlbumForm.yes.click(function() {
            self._.el.deleteAlbumForm.yes.disable();
            self._.el.deleteAlbumForm.loader.show();

            var self_ = self;

            $.post(self_._.vars.ajaxURL+'delete/'+self_._.id, {msid: Imgur.Environment.msid}, function(data) {
                self_._.el.deleteAlbumForm.loader.hide();
                self_._.el.deleteAlbumForm.yes.enable();
                $.colorbox.close();

                humanMsg.displayMsg(self_._.messages.deleteAlbum);

                if(typeof callback == 'function') {
                    callback(data);
                } else {
                    if(location.pathname == '/') {
                        $('#album-'+self_._.id).fadeOut();
                    }

                    if(location.pathname != '/') {

                        var self__ = self_;

                        setTimeout(function() {
                            if(self__._.signed) {
                                window.location = location.protocol + '//'+ Imgur.getInstance()._.auth.url + '.' + self__._.host;

                            } else {
                                window.location = '/';
                            }
                        }, 1000);
                    }
                }

            }).error(function() {
                self._.el.deleteAlbumForm.yes.enable();
                self._.el.deleteAlbumForm.loader.hide();
            });
        });
    },

    bindHideAlbum : function(callback) {
        this._.vars.bindings.del = true;

        this._.el.hideAlbumForm.no.click(function() {
            $.colorbox.close();
        });

        var self = this;

        this._.el.hideAlbumForm.yes.click(function() {
            self._.el.hideAlbumForm.yes.disable();
            self._.el.hideAlbumForm.loader.show();

            $.post(self._.vars.ajaxURL+'hide/'+self._.id, {msid: Imgur.Environment.msid}, function(data) {
                self._.el.hideAlbumForm.loader.hide();
                self._.el.hideAlbumForm.yes.enable();
                $.colorbox.close();

                humanMsg.displayMsg(self._.messages.hideAlbum);

                if(typeof callback == 'function') {
                    callback(data);
                } else {
                    if(location.pathname == '/') {
                        $('#album-'+self._.id).fadeOut();
                    }

                    if(location.pathname != '/') {
                        setTimeout(function() {
                            if(self._.signed) {
                                window.location = location.protocol + '//'+ Imgur.getInstance()._.auth.url + '.' + self._.host;

                            } else {
                                window.location = '/';
                            }
                        }, 1000);
                    }
                }

            }).error(function() {
                self._.el.hideAlbumForm.yes.enable();
                self._.el.hideAlbumForm.loader.hide();
            });
        });
    },



    // ***************** CREATE ALBUM FUNCTIONS *****************

    bindCreateAlbum : function(callback) {
        this._.vars.bindings.create = true;

        var self = this;

        this._.el.createAlbumForm.form.submit(function() {
            self.createAlbumSubmitHandler(callback);
            return false;
        });

        // when editing the create album form,
        // mark fields as changed for form submissions
        // for non-new albums (edits), so we don't have to
        // send every form value, and so we can track which
        // fields are actually changed on the backend
        this._.el.createAlbumForm.form.change(function(e) {
            $(e.target).attr('data-changed', 'true');
        });

        this._.el.createAlbumForm.privacyInput.change(function() {
            self.hideAlbumPrivacy();
            self.showAlbumPrivacy();
        });

        this._.el.createAlbumForm.privacyTitle.tipsy({
            html: false,
            opacity: 1,
            gravity: 'w'
        });
    },

    initCreateAlbum : function() {
        this._.vars.creatingAlbum = true;

        $(this._.el.createAlbumForm.id).addClass('create');
        this._.el.createAlbumForm.header.text(this._.messages.createAlbum);
        this._.el.createAlbumForm.titleInput.val('');
        this._.el.createAlbumForm.descriptionInput.val('');
        this._.el.createAlbumForm.layoutInput.removeAttr('checked').filter(':first').attr('checked', 'checked');
        this._.el.createAlbumForm.cover.removeAttr('src');

        if(this._.signed && this._.auth) {
            this._.el.createAlbumForm.privacyInput.removeAttr('checked').filter('[value='+this._.auth.album_privacy+']').attr('checked', 'checked');
        } else {
            this._.el.createAlbumForm.privacyInput.removeAttr('checked').filter(':first').attr('checked', 'checked');
        }

        this._.el.createAlbumForm.titleInput.showPlaceholder();
        this._.el.createAlbumForm.descriptionInput.showPlaceholder();
    },

    initChangeSettings : function(callingElement) {
        // Make sure we know how album change settings is called, so we can get relevant info (e.g. album id)
        var album = this._.id? $('#album-'+this._.id) : $(callingElement).parents('[data-privacy]');
        if(!this._.id) {
            this._.id = $(callingElement).parents('[data-privacy]').attr('id').split('-')[1];
        }

        this._.vars.creatingAlbum = false;

        if(album.length) {
            // Set the form data
            if(!this._.signed) {
                $(this._.el.createAlbumForm.id).addClass('create');
            } else {
                $(this._.el.createAlbumForm.id).removeClass('create');
            }
            if(this._.el.createAlbumForm.titleInput.length > 0) {
                this._.el.createAlbumForm.header.text(this._.messages.albumSettings);
            } else {
                this._.el.createAlbumForm.header.text(this._.messages.switchLayout);
            }
            this._.el.createAlbumForm.titleInput.val(album.attr('data-title'));
            this._.el.createAlbumForm.descriptionInput.val(album.attr('data-description'));
            this._.el.createAlbumForm.privacyInput.removeAttr('checked').filter('[value="'+album.attr('data-privacy')+'"]').attr('checked', 'checked');

            var cover = album.attr('data-cover');

            if(cover) {
                var coverUrl = this._.cdnUrl+'/'+album.attr('data-cover');
                // If it's a bare link, get the big thumb to use as the cover.
                if(Imgur.Util.basename(coverUrl).indexOf('.')==-1) {
                    coverUrl += 'b.jpg';
                }

                this._.el.createAlbumForm.cover.attr('src', coverUrl);
            }

            // Adjust the title placeholder
            if(this._.el.createAlbumForm.titleInput.val()) {
                this._.el.createAlbumForm.titleInput.removeClass('placeholder');
            } else {
                this._.el.createAlbumForm.titleInput.showPlaceholder();
            }

            // Adjust the description placeholder
            if(this._.el.createAlbumForm.descriptionInput.val()) {
                this._.el.createAlbumForm.descriptionInput.removeClass('placeholder');
            } else {
                this._.el.createAlbumForm.descriptionInput.showPlaceholder();
            }
        }
    },

    showAlbumPrivacy : function() {
        this.hideAlbumPrivacy();

        switch(this._.el.createAlbumForm.privacyInput.filter(':checked').val()) {
            case "0":
                this._.el.createAlbumForm.exlanationPublic.show();
                break;

            case "1":
                this._.el.createAlbumForm.hiddenIcon.show();
                this._.el.createAlbumForm.exlanationHidden.show();
                break;

            case "2":
                this._.el.createAlbumForm.secretIcon.show();
                this._.el.createAlbumForm.exlanationSecret.show();
                break;
        }

        this.resizeCreateAlbumForm();
    },

    hideAlbumPrivacy : function() {
        this._.el.createAlbumForm.hiddenIcon.hide();
        this._.el.createAlbumForm.secretIcon.hide();

        this._.el.createAlbumForm.exlanationPublic.hide();
        this._.el.createAlbumForm.exlanationHidden.hide();
        this._.el.createAlbumForm.exlanationSecret.hide();
    },

    resizeCreateAlbumForm : function() {
        var form = $(this._.el.createAlbumForm.id);

        $.colorbox.resize({
            innerWidth: form.width()+20,
            innerHeight: form.height()+20
        });
    },

    createAlbumSubmitHandler : function(callback) {
        var self = Imgur.Album.getInstance();

        if(!this._.el.createAlbumForm.errorbox.is(':hidden')) {
            this._.el.createAlbumForm.errorbox.hide();
            this.resizeCreateAlbumForm();
        }

        if( this._.el.createAlbumForm.titleInput.length > 0 &&
            this._.el.createAlbumForm.titleInput.val().toLowerCase() == 'album title (optional)') {
            this._.el.createAlbumForm.titleInput.val('');
        }

        if( this._.el.createAlbumForm.descriptionInput.length > 0 &&
            this._.el.createAlbumForm.descriptionInput.val().toLowerCase() == 'album description (optional)') {
            this._.el.createAlbumForm.descriptionInput.val('');
        }

        if (this._.vars.needCaptchaToProceed) {
            var formData = self._.el.createAlbumForm.form.serializeObject();
            $.ajax({
                url: '//' + Imgur.Environment.host + '/clearprotection',
                method: 'POST',
                data: {
                    recaptcha_challenge_field: formData.recaptcha_challenge_field,
                    recaptcha_response_field: formData.recaptcha_response_field
                }
            }).success(function(response) {
                if(response.data.error) {
                    humanMsg.displayMsg("Captcha invalid: " + response.data.error);
                    Recaptcha.reload();
                } else {
                    self._.vars.needCaptchaToProceed = false;
                    self.createAlbumSubmitHandler(callback);
                }
            }).error(function(response) {
                humanMsg.displayMsg("An error occurred when trying to submit the Captcha.");
                Recaptcha.reload();
            });

            // Submission will continue asynchronously if the captcha succeeds
            return;
        }

        this._.el.createAlbumForm.submitButton.disable();
        this._.el.createAlbumForm.loader.show();

        var url1     = this._.vars.creatingAlbum? 'create': 'settings/'+this._.id;
        var formData = this._.el.createAlbumForm.form.serialize();

        if(this._.el.createAlbumForm.titleInput.length > 0) {

            var self = this;

            $.post(this._.vars.ajaxURL + url1, formData, function(data) {
                if(typeof(data) !== 'object') {
                    data = $.parseJSON(data);
                }
                data = data.data;

                self._.el.createAlbumForm.submitButton.enable();
                self._.el.createAlbumForm.loader.hide();

                // Editing an existing one
                if(!self._.vars.creatingAlbum) {
                    if(data.id) {
                        $.colorbox.close();

                        humanMsg.displayMsg(self._.messages.updateSettings);

                        var album = $('#album-'+data.id);

                        album.attr('data-description', data.description)
                             .attr('data-privacy', data.privacy)
                             .attr('data-title', data.title);

                        if(typeof callback == 'function') {
                            callback(data);
                        } else {
                            location.reload();
                        }

                    // There was an error
                    } else {
                        var errorstr = '';

                        for(key in errors) {
                            if(errorstr != '') errorstr += "\n";

                            errorstr += errors[key];
                        }

                        self._.el.createAlbumForm.errorbox.text(errorstr).show();
                        self.resizeCreateAlbumForm();
                    }

                // Creating a new one
                } else {
                    $.colorbox.close();

                    if(typeof callback == 'function') {
                        callback(data);
                    } else {
                        if(location.pathname == '/') {

                            var self_ = self;

                            $(document).bind('cbox_closed', function() {
                                $(document).unbind('cbox_closed');

                                self_._.id     = data.id;
                                self_._.images = [];

                                self_.changeImages(false, false);

                                $(document).bind('cbox_closed', function() {
                                    setTimeout(location.reload, 1000);
                                });
                            });
                        } else {
                            window.location = '/a/'+data.id;
                        }
                    }
                }
            }).error(function(response) {
                self._.el.createAlbumForm.submitButton.enable();
                self._.el.createAlbumForm.loader.hide();

                response = $.parseJSON(response.responseText);

                if(response.data.error == self._.messages.privateGalleryError) {
                    humanMsg.displayMsg(self._.messages.privateGalleryMessage);
                } else if(response.status == 429) {
                    self._.vars.needCaptchaToProceed = true;

                    if (typeof Recaptcha === 'undefined') {
                        $.getScript('//www.google.com/recaptcha/api/js/recaptcha_ajax.js', function() {
                            Recaptcha.create(Imgur.Environment.recaptcha_key,
                                'albumCreateCaptcha',
                                {
                                    theme: 'clean',
                                    callback: function() {
                                        humanMsg.displayMsg(response.data.error.message);
                                        $.colorbox.resize();
                                    }
                                }
                            );
                        });
                    } else {
                        Recaptcha.reload();
                    }
                }
            });
        } else {
            window.location = window.location.protocol + '//' + Imgur.Environment.host + '/a/' + this._.id + '/layout/' + this._.el.changeLayout.layoutForm.find(':radio:checked').attr('id');

            callback? callback.stopPropagation() : void(0);

            return false;
        }

        return false;
    },

    openCreateAlbumColorbox : function(callback, clean_up_id) {
        var self = this;

        $.colorbox({
            href        : this._.el.createAlbumForm.id,
            open        : true,
            top            : '15%',
            inline        : true,
            transition    : 'none',
            preloading    : false,
            scrolling    : false,
            onComplete: function() {
                if(!self._.vars.bindings.create) {
                    self.bindCreateAlbum(callback);
                }

                self.showAlbumPrivacy();
                self._.el.createAlbumForm.titleInput.focus();
            },
            onCleanup: function() {
                $('.tipsy').remove();

                if(clean_up_id) {
                    self._.id = null;
                }
            }
        });
    },

    // ***************** CAROUSEL FUNCTIONS *****************

    carouselItemFirstIn : function(element, carousel, prev) {
        if(element.index() == 0) {
            prev.addClass('disabled');
        } else {
            prev.removeClass('disabled');
        }
    },

    carouselItemLastIn : function(element, carousel, next) {
        var length = carousel.jcarousel('items').length;

        if(element.index() == length-1) {
            next.addClass('disabled');
        } else {
            next.removeClass('disabled');
        }
    },

    // ***************** THUMB CAROUSEL FUNCTIONS *****************

    shrinkThumbsCarousel : function() {
        this._.el.layout.global.thumbsCarouselBottom.remove();
        this._.el.layout.global.thumbsCarouselContainer.addClass('shrunk');
    },

    getThumbsCarouselScroll : function() {
        var operations = this._.operations,
            layouts    = this._.layouts;

        if(this._.operation == operations.embed) {
            var width     = $('.embed').parent().width();
            var thumbSize = 82;

            return Math.floor(width/thumbSize/2);
        }

        switch(this._.album.album_layout) {
            case layouts.blog:
                return 6;

            case layouts.horizontal:
                return 5;
        }

        return 0;
    },

    thumbsCarouselScroll : function(i, animate) {
        var operations = this._.operations,
            layouts    = this._.layouts;

        try {
            if((!this._.operation && this._.album.album_layout == layouts.horizontal) || this._.operation == operations.embed) {
                var scroll = this.getThumbsCarouselScroll();

                if(i >= scroll && i < this._.images.count - scroll) {
                    this._.el.layout.global.thumbsCarousel.jcarousel('scroll', i-scroll, animate);
                } else {
                    if(i < scroll) {
                        this._.el.layout.global.thumbsCarousel.jcarousel('scroll', 0, animate);
                    } else {
                        this._.el.layout.global.thumbsCarousel.jcarousel('scroll', this._.images.count-1, animate);
                    }
                }
            }
        } catch(e) {}
    },

    thumbsCarouselBind : function() {
        var operations = this._.operations,
            layouts    = this._.layouts,
            self       = this;

        this._.el.layout.global.thumbsCarouselNext.click(function() {
            var scroll = self.getThumbsCarouselScroll();

            if(self._.operation != operations.embed) {
                if(self._.album.album_layout == layouts.blog) {
                    scroll = scroll/2;
                }
            }

            self._.el.layout.global.thumbsCarousel.jcarousel('scroll', '+='+scroll);
            return false;
        });

        this._.el.layout.global.thumbsCarouselPrev.click(function() {
            var scroll = self.getThumbsCarouselScroll();

            if(self._.operation != operations.embed) {
                if(self._.album.album_layout == layouts.blog) {
                    scroll = scroll/2;
                }
            }

            self._.el.layout.global.thumbsCarousel.jcarousel('scroll', '-='+scroll);
            return false;
        });

        this._.el.layout.global.thumbsCarousel.find('img').click(function() {
            var i     = parseInt($(this).attr('data-index'));
            var image = self._.images.images[i];

            if((!self._.operation && self._.album.album_layout == layouts.horizontal) || self._.operation == operations.embed) {
                self.scrollMainImage(i);
            } else if(self._.album.album_layout == layouts.blog) {
                Imgur.getInstance().setPageHash(image.hash);
            }
        });

        $('.thumb-title-embed').tipsy({
            gravity    : 's',
            opacity    : 0.95,
            html    : true,
            live    : true
        });

        var shrink = this._.album.album_layout == layouts.blog ? this.getThumbsCarouselScroll()/2 : this.getThumbsCarouselScroll();

        if(this._.images.count <= shrink) {
            this.shrinkThumbsCarousel();
        }
    },

    thumbsCarouselItemVisibleIn : function(carousel) {
        Imgur.Album.getInstance().loadImage($(this).find('img'));
    },

    initThumbsCarousel : function() {
        var self = this;

        this._.el.layout.global.thumbsCarousel.find('li')
            .bind('jcarouselitemvisiblein', self.thumbsCarouselItemVisibleIn);

        this._.el.layout.global.thumbsCarouselTop.find('li')
            .bind('jcarouselitemfirstin', function(carousel) {
                self.carouselItemFirstIn($(this), self._.el.layout.global.thumbsCarouselTop, self._.el.layout.global.thumbsCarouselPrev);
            })
            .bind('jcarouselitemlastin', function(carousel) {
                self.carouselItemLastIn($(this), self._.el.layout.global.thumbsCarouselTop, self._.el.layout.global.thumbsCarouselNext);
            });

        this._.el.layout.global.thumbsCarousel
            .jcarousel({
                animation: this._.vars.thumbsSpeed
            });

        this.thumbsCarouselBind();
    },

    // ***************** MAIN CAROUSEL FUNCTIONS *****************

    getImageSrc : function(i) {
        if(i >= this._.images.count) i = this._.images.count-1;
        if(i < 0) i = 0;

        var image = this._.images.images[i];

        return this._.cdnUrl+'/'+image.hash+image.ext;
    },

    scrollMainImage : function(i) {
        if(this._.images.count == 0) return;
        if(i >= this._.images.count) i = this._.images.count-1;
        if(i < 0) i = 0;

        var src   = this.getImageSrc(i),
            image = this._.images.images[i];

        this._.vars.currentImageIndex = i;

        var $container = $("#image-container-inner"),
            thumbsize  = '';

        //scale embed album image based on largest dimension of album container
        if(this._.operation == this._.operations.embed && !image.animated) {
            var height = this._.el.layout.global.container.height(),
                width = this._.el.layout.global.container.width(),
                largestDimension = (height > width) ? height : width;

            if (largestDimension <= 160) {
                thumbsize = 't';
            } else if (largestDimension <= 320) {
                thumbsize = 'm';
            } else if (largestDimension <= 640) {
                thumbsize = 'l';
            } else if (largestDimension <= 1024) {
                thumbsize = 'h';
            }
        }

        this._.el.layout.global.mainImage = ReactDOM.render(React.createElement(Imgur.ImageViewer,{
            item: image,
            thumbsize: thumbsize,
            discarding_referrer: !this._.initialImage
        }), $container.get(0));

        var zoomed = $('.jquery-image-zoom');

        if(zoomed.length) {
            zoomed.click();
        }

        this.mainCarouselItemVisible();
        Imgur.getInstance().setPageHash(i);
        this._.initialImage = false;
    },

    mainCarouselSetup : function() {
        var self        = this,
            operations = this._.operations,
            layouts    = this._.layouts;

        // Description tipsy
        this._.el.layout.global.description.tipsy({
            gravity: $.fn.tipsy.autoNS,
            opacity: 0.95,
            offset: -10,
            html: true,
            delayOut: 1200,
            live: true
        });

        // Next button
        this._.el.layout.global.nextImage.click(function() {
            self.scrollMainImage(self._.vars.currentImageIndex+1);
            $('.' + self._.el.layout.global.actionButtonOpenClass).removeClass('opened');
            return false;
        });

        // Prev button
        this._.el.layout.global.prevImage.click(function() {
            self.scrollMainImage(self._.vars.currentImageIndex-1);
            $('.' + self._.el.layout.global.actionButtonOpenClass).removeClass('opened');
            return false;
        });

        // Bind key up
        $(document).keydown(function(e) {
            if(!e.ctrlKey && !e.altKey && !e.metaKey) {
                if(e.keyCode == 37) {
                    self.scrollMainImage(self._.vars.currentImageIndex-1);
                } else if(e.keyCode == 39) {
                    self.scrollMainImage(self._.vars.currentImageIndex+1);
                }

                return true;
            }

            return true;
        });

        // Bind scroll wheel on embed albums
        if(this._.operation == operations.embed && this._.el.layout.embed.offsite.length == 0) {
            this._.el.layout.global.container.parent().mousewheel(function(event, delta) {
                // Fix mousewheel's delta from zeroing on scroll.
                delta = delta? detla : event.originalEvent.wheelDeltaY || event.originalEvent.detail;

                if(delta > 0) {
                    self.scrollMainImage(self._.vars.currentImageIndex-1);
                } else {
                    self.scrollMainImage(self._.vars.currentImageIndex+1);
                }

                event.preventDefault();
                event.stopPropagation();

                return false;
            });
        }

        var index = self.getStartingIndex();

        if(this._.album.album_layout != 'g' || !isNaN(parseInt(window.location.hash.substr(1)))) {
            self.scrollMainImage(index);
        }
    },

    mainCarouselItemVisible : function() {
        var index      = this._.vars.currentImageIndex,
            hash       = this._.images.images[index],
            layouts    = this._.layouts,
            ext        = hash.prefer_video ? '.gifv' : hash.ext;

        // Remove any previous tipsies
        $('.tipsy').remove();

        // Display the title
        if(hash.title) {
            this._.el.layout.global.title.text(hash.title).show();
        } else {
            this._.el.layout.global.title.hide();
        }

        // Display the description
        if(hash.description) {
            var sanitizedDescription = Imgur.Util.htmlEntity(hash.description);
            this._.el.layout.global.description.html(Imgur.Util.linkify(sanitizedDescription, true, true)).show();
        } else {
            this._.el.layout.global.description.hide();
        }

        // Clear previous tipsy descriptions
        this._.el.layout.global.description.attr('title','').attr('original-title','');

        // Set the correct height
        if(this._.operation == this._.operations.embed) {
            Imgur.Utilities.Embed.setupEmbedScreen.call(this);
        } else if($(document).height() > this._.vars.bodyHeight) {
            this._.vars.bodyHeight = $(document).height();
            $('body').css('height', this._.vars.bodyHeight);
        }

        // Scroll the thumbnails
        this.thumbsCarouselScroll(index, false);

        // Select the correct thumb
        this._.el.layout.global.thumbsCarousel.find('img').removeClass('selected');
        $('#thumb-'+hash.hash).addClass('selected');

        // Setup the hover icon
        $(this._.el.layout.global.imageHoverIconClass).find('.album-view-link').html('<a href="/a/'+this._.id+'" target="_blank">View album on Imgur</a>');
        $(this._.el.layout.global.imageHoverIconClass).find('.album-view-image-link').html('<a href="'+this._.cdnUrl+'/'+hash.hash+ext+'" target="_blank">View full resolution</a>');
        $(this._.el.layout.global.imageHoverIconClass).find('.download').html('<a href="'+'//'+Imgur.Environment.host + '/download/'+hash.hash+(hash.title? '/'+encodeURIComponent(hash.title): '')+'">Download full resolution</a>');
        // Disable/Enable the next button
        if(index == this._.images.count-1) {
            this._.el.layout.global.nextImage.addClass('disabled');
        } else {
            this._.el.layout.global.nextImage.removeClass('disabled');
        }

        // Disable/Enable the prev button
        if(index == 0) {
            this._.el.layout.global.prevImage.addClass('disabled');
        } else {
            this._.el.layout.global.prevImage.removeClass('disabled');
        }

        // Preload the next images
        for(var p = index-1; p < index + this._.preloadCount && p <= this._.images.count; ++p) {
            if('undefined' !== typeof this._.images.images[p]) {
                if (!this._.images.images[p].animated || '.gif' !== this._.images.images[p].ext) {
                    // don't preload animated GIFs because they get video tags now
                    $.preloadImages(this.getImageSrc(p));
                }
            }
        }
    },

    initMainCarousel : function() {
        this.mainCarouselSetup();
    },

    // ***************** INIT FUNCTIONS *****************

    chooseLogoColor : function(hexcolor) {
        if (hexcolor) {
            var r = parseInt(hexcolor.substr(0,2),16);
            var g = parseInt(hexcolor.substr(2,2),16);
            var b = parseInt(hexcolor.substr(4,2),16);
            var yiq = ((r*299)+(g*587)+(b*114))/1000;
            return (yiq >= 128) ? true : false;
        }
        return false;
    },

    initDownloadForm : function() {
        if(Imgur.Environment.auth.email) {
            this._.el.downloadAlbumForm.emailInput.val(Imgur.Environment.auth.email);
            this._.el.downloadAlbumForm.emailInput.removeClass('placeholder');
        }

        if(this._.el.downloadAlbumForm.form.length > 0) {
            this._.el.downloadAlbumForm.form.validate({
                onkeyup: false,
                focusCleanup: true,

                errorPlacement: _.bind(function(error) {
                    this._.el.downloadAlbumForm.message.html(error);
                }, this),

                rules: {
                    email: {
                        required: true,
                        email: true
                    }
                },

                messages: {
                    email: 'Enter a valid email address.'
                }
            });
        }

        this._.el.downloadAlbumForm.form.unbind('submit');
        this._.el.downloadAlbumForm.form.submit(_.bind(function() {
            if(this._.el.downloadAlbumForm.form.valid()) {
                $.post(
                    this._.vars.ajaxURL+'zip/'+this._.id,
                    this._.el.downloadAlbumForm.save.attr('name')+'=true&email='+this._.el.downloadAlbumForm.emailInput.val(),
                    _.bind(function(response) {
                        if(response.data == 'success') {
                            this._.el.downloadAlbumForm.message.html('<strong><div class="green">Done!</div></strong>');
                            this._.el.downloadAlbumForm.message.show();
                            setTimeout($.colorbox.close, 900);
                            setTimeout(this._.el.downloadAlbumForm.message.hide, 1000);

                        } else {
                            this._.el.downloadAlbumForm.message.html('<div class="error">Oops! Something went wrong.</div>');
                            this._.el.downloadAlbumForm.message.show();

                        }
                    }, this)
                );
            }
        }, this));
    },

    // ***************** SUBDOMAIN FUNCTIONS *****************

    albumSortStop : function() {
        var self = Imgur.Album.getInstance();

        if(self._.el.subdomain.container.find(self._.el.subdomain.albumClass).length > 0) {
            var albums = self._.el.subdomain.container.sortable('toArray');
            var order  = [];

            for(var i = 0, count = albums.length; i < count; ++i) {
                order[i] = albums[i].substr(albums[i].indexOf('-')+1);
            }

            $.post(self._.vars.ajaxURL+'rearrangeAlbums', 'order='+order+'&msid=' + Imgur.Environment.msid);
        };
    },

    toggleEditButton : function(button) {
        button.toggleClass('opened');
        button.siblings(this._.el.subdomain.editOptionsClass).toggle();
    },

    editButtonClickHandler : function(e) {
        $('body').unbind('click');

        var self = Imgur.Album.getInstance();

        if(/album-/.test($(e.target).parent().attr('id'))) {
            self.setAlbumID( $(e.target).parent().attr('id').replace(/album-/,'') );
        }

        $('body').click(function(e) {

            var self_ = self;

            $('body').find('.opened').each(function() {
                self_.toggleEditButton($(this));
            });
        });

        self.toggleEditButton($(this));

        if($(e.target).closest('.gallery-submit-link').length == 0) {
            e.stopPropagation();
            e.preventDefault();
            return false;
        }

        return true;
    },

    // ***************** PUBLIC FUNCTIONS *****************
    downloadAlbum : function() {
        var self = Imgur.Album.getInstance();

        self._.el.downloadAlbumForm.loader.show();

        $.post(self._.vars.ajaxURL+'zipexists/'+self._.id,
            function(response){
                if(response == 'available') {
                    window.location = '//'+Imgur.getInstance()._.host+'/a/'+self._.id+'/zip';
                    self._.el.downloadAlbumForm.loader.hide();
                } else {
                    var self_ = self;

                    $.colorbox({
                        open: true,
                        href: self._.el.downloadAlbumForm.id,
                        transition: 'none',
                        top: '15%',
                        inline: true,
                        onComplete: function() {
                            self_.initDownloadForm();
                            self_._.el.downloadAlbumForm.emailInput.focus();
                            self_._.el.downloadAlbumForm.loader.hide();
                        }
                    });
                }
            }
        );
    },

    createAlbum : function(callback) {
        var self = Imgur.Album.getInstance();
        self.initCreateAlbum();
        self.openCreateAlbumColorbox(callback);
    },

    changeSettings : function(callback, callingElement) {
        var self = Imgur.Album.getInstance();
        var clean_up_id = this._.id === null;
        self.initChangeSettings(callingElement);
        self.openCreateAlbumColorbox(callback, clean_up_id);
    },

    changeImages : function(changeCover, showMessage, callback) {
        if (Imgur.Environment.published) return false;

        var self = this;

        $.colorbox({
            href: this._.el.albumImagesForm.id,
            open: true,
            inline: true,
            transition: 'none',
            scrolling: false,
            onComplete: function() {
                if(!self._.vars.bindings.images) {
                    self.bindchangeImages();
                }

                if(changeCover) {
                    self._.el.albumImagesForm.imagesMenu.hide();
                    $.colorbox.resize({
                        innerWidth:  self._.el.albumImagesForm.mainColumn.width()+20,
                        innerHeight: self._.el.albumImagesForm.form.height()+20
                    });
                    self._.el.albumImagesForm.coverHeader.width( self._.el.albumImagesForm.mainColumn.width()+20);
                } else {
                    self._.el.albumImagesForm.imagesMenu.show();
                }

                self.initAlbumImages(changeCover, showMessage, callback);
            },
            onCleanup: function() {
                self._.vars.albumImages.destroy();
                self._.vars.albumImages = null;
            }
        });
    },

    deleteAlbum : function(callback) {
        var self = Imgur.Album.getInstance();

        $.colorbox({
            href: self._.el.deleteAlbumForm.id,
            open: true,
            inline: true,
            top: '15%',
            transition: 'none',
            scrolling: false,
            onComplete: function() {
                if(!self._.vars.bindings.del) {
                    self.bindDeleteAlbum(callback);
                }
            }
        });
    },

    hideAlbum : function(callback) {
        var self = Imgur.Album.getInstance();

        $.colorbox({
            href: self._.el.hideAlbumForm.id,
            open: true,
            inline: true,
            top: '15%',
            transition: 'none',
            scrolling: false,
            onComplete: function() {
                if(!self._.vars.bindings.del) {
                    self.bindHideAlbum(callback);
                }
            }
        });
    },

    generalInit : function() {
        var self       = this,
            operations = this._.operations,
            layouts    = this._.layouts;

        if(this._.operation === operations.embed) {
            Imgur.Utilities.Embed.initEmbed.call(this);
        }

        $('.thumb-title').tipsy({
            gravity: 'n',
            opacity: 0.95,
            offset: -10,
            html: true
        });

        this._.el.links.createAlbum.unbind('click');
        this._.el.links.changeSettings.unbind('click');
        this._.el.links.deleteAlbum.unbind('click');
        this._.el.links.hideAlbum.click(this.hideAlbum);
        this._.el.links.changeImages.unbind('click');
        this._.el.links.changeCover.unbind('click');
        this._.el.links.downloadAlbum.unbind('click');

        this._.el.links.createAlbum.click(this.createAlbum);
        this._.el.links.changeSettings.click(function(){ self.changeSettings(undefined, this); });
        this._.el.links.deleteAlbum.click(this.deleteAlbum);
        this._.el.links.changeImages.click(function() { self.changeImages(false, true); });
        this._.el.links.changeCover.click(function() { self.changeImages(true, true); });
        this._.el.links.embedAlbum.click(_.bind(Imgur.Utilities.Embed.embedModal, this));
        this._.el.links.downloadAlbum.click(this.downloadAlbum);


        $(".album-images-link.published").tipsy({
            gravity: "w"
        });

        this._.vars.bodyHeight = $('body').height();

        this._.el.embedAlbumForm.embedCodeInput.click($(this).selectAll);

        if(Imgur.Fullscreen) {
            Imgur.Fullscreen.getInstance();
        }
    },

    profileInit : function() {
        this._.el.subdomain.editButton.unbind('click');
        this._.el.subdomain.editButton.click(this.editButtonClickHandler);

        this._.el.subdomain.container.sortable({
            stop: this.albumSortStop,
            items: '.album'
        });

        var self = this;

        this._.el.subdomain.editOptionsItems.unbind('click');
        this._.el.subdomain.editOptionsItems.click(function() {
            var id = $(this).parent().parent().parent().attr('id');
            self._.id = id.substr(id.indexOf('-')+1);
        });

        this.generalInit();
    },

    setAlbumID : function(id) {
        this._.id = id;
    }

});

Class.addSingleton(Imgur.Album);
