Namespace('Imgur.Element');

Imgur.Element.SortedList = Class.extend({
    init: function($container, comparator, initialList) {
        this._ = {
            el: {
                $container: $container
            },
            comparator: comparator,
            list: initialList || []
        };
    },

    getComparator: function() {
        return this._.comparator;
    },

    getList: function() {
        return this._.list;
    },

    get$Container: function() {
        return this._.el.$container;
    },

    /**
     * Insert the tag in sorted position
     */
    add: function(el) {
        var inserted = false;

        for(var existing_index = 0; existing_index < this._.list.length; existing_index++) {
            var existing_element = this._.list[existing_index];

            var comparison_result = this._.comparator(el, existing_element);

            if(comparison_result <= 0) {
                existing_element.get$Container().before(el.get$Container());
                this._.list.splice(existing_index, 0, el);
                inserted = true;
                break;
            }
        }

        if(!inserted) {
            this.append(el);
        }
    },

    prepend: function(el) {
        if(this._.list.length) {
            this._.list[0].insertBefore(el);
        } else {
            this._.el.$container.append(el.get$Container());
        }
        this._.list.unshift(el);
    },

    append: function(el) {
        this._.el.$container.append(el.get$Container());
        this._.list.push(el);
    },

    remove: function(el) {
        this.removeElementPredicate(function(cur_el) {
            return cur_el.equals(el);
        });
    },

    /**
     * Remove the tags for which the predicate evaluates to true
     */
    removeElementPredicate: function(predicate) {
        this._.list = _.filter(this._.list, function(cur_el) {
            var result = predicate(cur_el);

            if(result) {
                cur_el.remove();
            }

            return !result;
        });
    },

    clear: function() {
        for(var i = 0; i < this._.list.length; i++) {
            this._.list[i].remove();
        }

        this._.list = [];
    },

    extend: function(list) {
        _.each(list, _.bind(this.add, this));
    },

    length: function() {
        return this._.list.length;
    },

    contains: function(el) {
        return _.some(this._.list, function(other) {
            return el.equals(other);
        });
    },

    containsBy: function(predicate) {
        return _.some(this._.list, predicate);
    },

    moveToTop: function(el) {
        el.remove();
        this.remove(el);

        this.prepend(el);
    },

    findFirstBy: function(predicate) {
        var els = _.filter(this._.list, predicate);
        return (els.length ? els[0] : null);
    }
});
