Namespace('Imgur.Tag.Store');

Imgur.Tag.Store.Suggestion = Class.extend({
    init: function(emitter, tagValidator) {
        this._ = {
            tagValidator: tagValidator,
            emitter: emitter,
            suggestions: [],
            query: '',
            lastFinishedQuery: '',
            validationMode: 'query',
            loading: false
        };
    },

    getIsLoading: function() {
        return this._.loading;
    },
    
    setIsLoading: function(isloading) {
        this._.loading = isloading;

        this._.emitter.trigger('new suggestions');
    },

    setValidationMode: function(mode) {
        this._.validationMode = mode;

        this._.emitter.trigger('new suggest validation mode');
    },

    getIsValidQuery: function(query) {
        return this._.tagValidator.isValidTag(this._.validationMode, query);
    },

    getWhyNot: function(query) {
        return this._.tagValidator.whyNot(this._.validationMode, query);
    },

    getQuery: function() {
        return this._.query.toLowerCase();
    },

    getLastFinishedQuery: function() {
        return this._.lastFinishedQuery;
    },

    setLastFinishedQuery: function(query) {
        this._.lastFinishedQuery = query;

        this._.emitter.trigger('new finished query');
    },

    setQuery: function(value) {
        this._.query = value;

        this._.emitter.trigger('new suggest query');
    },

    getSuggestions: function() {
        return this._.suggestions;
    },

    setSuggestions: function(suggestions) {
        this._.suggestions = suggestions;

        this._.emitter.trigger('new suggestions');
    },

    addSuggestion: function(suggest) {
        this._.suggestions.push(suggest);

        this._.emitter.trigger('new suggestions');
    },

    clear: function() {
        this._.suggestions = [];

        this._.emitter.trigger('new suggestions');
    }
});
