Namespace('Imgur.Tag.Validator');

Imgur.Tag.GroupValidator = Class.extend({
    GROUP_REGEX: /^[a-zA-Z0-9&\-\s]*$/,

    init: function(min_length, max_length) {
        this._ = {
            maxLength: max_length,
            minLength: min_length
        };
    },

    isValidGroup: function(cause, query) {
        return this._validateGroup(cause, query).success;
    },

    whyNot: function(cause, query) {
        return this._validateGroup(cause, query).message;
    },

    _validateGroup: function(cause, query) {
        var validationResult = {
            success: true,
            message: null
        };

        query = query.trim();
        
        if(query.match(Object.getPrototypeOf(this).GROUP_REGEX) == null) {
            validationResult.message = 'Galleries can only contain alphanumeric and others (&-).';
            validationResult.success = false;
        }

        if(cause == 'submit' && query.length < this._.minLength) {
            validationResult.message = 'Gallery is too short. Galleries can be between ' + this._.minLength + ' and ' + this._.maxLength + ' characters.';
            validationResult.success = false;
        } else if(query.length > this._.maxLength) {
            validationResult.message = 'Gallery is too long. Galleries can be between ' + this._.minLength + ' and ' + this._.maxLength + ' characters.';
            validationResult.success = false;
        }

        return validationResult;
    }
});