Namespace('Imgur.TitleDesc');
Imgur.TitleDesc = Imgur.extend({
    init : function(params) {
        this._ = $.extend(true, {
            'el'         : {
                'titleDescriptionForm'  : {
                    'id'                : '#title-description',
                    '$form'             : $('#title-description form'),
                    '$titleInput'       : $('#title-input'),
                    '$descriptionInput' : $('#description-input'),
                    '$loader'           : $('#title-description-loader')
                }
            }
        }, params);

        this._ = this._super(this._);
        return this._;
    },

    bindTitleDescriptionSubmit : function(callback, fillTitleDesc) {
        if (fillTitleDesc){
            this._.el.titleDescriptionForm.$titleInput.val(Imgur.Environment.hashes[0].title);
            var textArea = document.createElement('textarea'); // description is sent html encoded.
            textArea.innerHTML = Imgur.Environment.hashes[0].description;
            this._.el.titleDescriptionForm.$descriptionInput.val(textArea.value);
        }

        var self = this;

        this._.el.titleDescriptionForm.$form.submit(function() {
            return self.titleDescriptionSubmitHandler(callback)
        });
    },

    titleDescriptionSubmitHandler : function(callback) {
        var title = this._.el.titleDescriptionForm.$titleInput.val() != this._.el.titleDescriptionForm.$titleInput.attr('title')?
                    this._.el.titleDescriptionForm.$titleInput.val(): '';

        var desc = this._.el.titleDescriptionForm.$descriptionInput.val() != this._.el.titleDescriptionForm.$descriptionInput.attr('title')?
                   this._.el.titleDescriptionForm.$descriptionInput.val(): '';

        this._.el.titleDescriptionForm.$loader.show();
        this._.hash = Imgur.getInstance()._.hash;

        var self    = this;
        var deletehash = this._.hash.deletehash || Imgur.Environment.hashes[0].deletehash;
        $.post('/ajax/titledesc/' + deletehash, {
            title: title,
            description:  desc
        }, function(data) {
            $.colorbox.close();
            humanMsg.displayMsg(self._.messages.titledesc);
            self._.el.titleDescriptionForm.$loader.hide();

            if (callback){
                callback({title: title, description: desc});
            } else {
                window.setTimeout(function(){
                    location.reload();
                }, 1000);
            }
        });

        return false;
    },

    addTitleDescription : function() {
        var self = this;

		$.colorbox({
			href: self._.el.titleDescriptionForm.id,
			open: true,
			inline: true,
			top: '15%',
			transition: 'none',
			scrolling: false,
			onComplete: function() {
				self._.el.titleDescriptionForm.$titleInput.focus();
			}
		});
    },

    getElements : function() {
        return this._.el;
    }
});

Class.addSingleton(Imgur.TitleDesc);
