Namespace('Imgur.Tag');

Imgur.Tag.MainGroupSelector = Imgur.Tag.GroupSelector.extend({
    init: function(emitter, section, sort, inSearch, $container, sentence, outsideActiveTagStore) {
        this._super(emitter, section, sort, inSearch, $container);

        this._ = _.merge(this._ || {}, {
            emitter: emitter,
            section: section,
            sort: sort,
            inSearch: inSearch,
            sentence: sentence,
            outsideActiveTagStore: outsideActiveTagStore,
            el: {
                $container: $container,
                $selectionName: $container.find('#section .selection .name')
            }
        });
    },

    get$Container: function() {
        return this._.el.$container;
    }
});
