Namespace('Imgur.Album.Images');
Imgur.Album.Images = Class.extend({
    init : function(params) {
        this._ = $.extend(true,
            {
            host                    : 'imgur.com',
            cdnUrl                  : '//i.imgur.com',
            postURL                 : '/ajaxalbums/images',
            imagesPerPage           : 25,
            album                   : null,
            images                  : null,
            changeCover             : false,
            albumTitle              : null,
            mode                    : null,
            onSubmitComplete        : function() {},
            sort                    : 0,
            order                   : 1,
            selected                : [],
            offsetSelectionInterval : null,

            imgurImages             : null,

            operationQueue          : {},

            el:
                {
                $thumbsContainer    : $('#album-images #image-thumbs'),
                thumbsId            : '#album-images #image-thumbs',
                messagesClass       : 'thumbs-message',
                $loader             : $('#album-images #images-loader'),
                imageDivClass       : 'thumb',
                $modes              : $('#album-images #images-menu .mode'),
                $operationLoader    : $('#album-images #small-loader'),
                imageSelectedClass  : 'selected',
                $submitButton       : $('#album-images #save')
                },

            messages:
                {
                    noImages        : '<span class="thumbs-message">No images were found.</span>'
                }

            },
            params);

        this._.operationQueue.hashes = this._.images == null? []: this._.images;
        this._.operationQueue.count  = this._.images == null? 0: Object.size(this._.images);

        this.loadImages();
        this.loadPageElements();
    },

    getPageOptions : function() {
        if(isNaN(this._.album)) {
            this._.sort  = 3;
            this._.order = 0;
        } else {
            this._.sort  = 0;
            this._.order = 1;
        }

        return {
            sort  :  this._.sort,
            order : this._.order,
            album : this._.album
        };
    },

    noImagesFound : function() {
        var self = Imgur.Album.Images.getInstance();
        self._.el.$thumbsContainer.html(self._.messages.noImages);
    },

    addImageToThumbs : function(item, nocache, append) {
        var self = Imgur.Album.Images.getInstance();

        if($(self._.el.thumbsId + ' #'+item.hash).length > 0) return;

        var match = item.ext.match(/\?(.*)/);
        var sext  = 's.jpg'+(match != null? (match[0]): '');

        var href  = self._.cdnUrl+'/'+item.hash+(item.ext == '.gif'? item.ext: 'h'+item.ext),
            div   = $('<div class="'+self._.el.imageDivClass+'" id="'+item.hash+'" ext="'+item.ext+'"></div>'),
            sel   = $('<div class="selection"></div>');
            img   = $('<img src="'+self._.cdnUrl+'/'+item.hash+sext+'" />');

        $(div).append(img);
        $(div).append(sel);

        if(self._.operationQueue.hashes[item.hash]) {
            div.addClass(self._.el.imageSelectedClass);
        }

        $('.'+self._.el.messagesClass).remove();

        if(append) {
            $(div).appendTo(self._.el.$thumbsContainer);
        } else {
            $(div).prependTo(self._.el.$thumbsContainer).hide().fadeIn('slow');
        }
    },

    queueOperation : function(image) {
        var hash = image.attr('id');

        // If we're changing the cover
        if(this._.changeCover) {
            this._.operationQueue.count     = 1;
            this._.operationQueue.hashes[0] = hash;

            this._.el.$thumbsContainer.find('.'+this._.el.imageDivClass).removeClass(this._.el.imageSelectedClass);
            image.addClass(this._.el.imageSelectedClass);

        // If we're selecting multiple images
        } else {
            // Dequeue
            if(this._.operationQueue.hashes[hash]) {
                this._.operationQueue.count--;
                this._.operationQueue.hashes[hash] = null;
                image.removeClass(this._.el.imageSelectedClass);

            //Queue
            }  else {
                this._.operationQueue.count++;
                this._.operationQueue.hashes[hash] = true;
                image.addClass(this._.el.imageSelectedClass);
            }
        }
    },

    isInQueue : function(hash) {
        return this._.operationQueue.hashes[hash] == true;
    },

    startOperation : function() {
        if(this._.operationQueue.count == 0) return null;

        this._.images = [];

        if(this._.changeCover) {
            this._.images.push(this._.operationQueue.hashes[0]);
        } else {
            for(var i in this._.operationQueue.hashes) {
                if(this._.operationQueue.hashes[i] != null) {
                    this._.images.push(i);
                }
            }
        }

        return this._.images;
    },

    toggleOperationsLoader : function() {
        this._.el.$operationLoader.toggle();
    },

    bindOperationClick : function(element) {
        var self = this;

        element.unbind('click');

        element.click(function() {
            self.queueOperation($(this).parent());
            return false;
        });
    },

    offsetSelection : function() {
        var self = Imgur.Album.Images.getInstance();
        var offset    = 5;
        var parent    = self._.el.$thumbsContainer.parent();
        var scrollPos = parent.scrollTop();
        var pos       = parent.selectable();

        if(pos.y < 0) {
            parent.scrollTop(parent.scrollTop()-offset);

            if(parent.scrollTop() < scrollPos) {
                parent.selectable("recalc", -offset);
            }
        } else if(pos.y > parent.height()) {
            parent.scrollTop(parent.scrollTop()+offset);

            if(parent.scrollTop() > scrollPos) {
                parent.selectable("recalc", offset)
            }
        }
    },

    makeSelectable : function() {
        var self = Imgur.Album.Images.getInstance();

        self._.el.$thumbsContainer.unbind('mousedown');

        self._.el.$thumbsContainer.parent().selectable(
            {
            filter   : '.thumb',

            selected: function(event, ui) {
                self._.selected.push($(ui.selected));
            },

            stop: function(event, ui) {
                var found  = 0;
                var adding = false;
                var count  = self._.selected.length;

                for(var i in self._.selected) {
                    if(self.isInQueue($(self._.selected[i]).attr('id'))) {
                        found++;

                        if(i == count-1 && found < count) {
                            adding = true;
                        }
                    } else if(found) {
                        adding = true;
                        break;
                    }
                };

                for(var i in self._.selected) {
                    var selected = $(self._.selected[i]);

                    if(adding) {
                        if(!self.isInQueue(selected.attr('id'))) {
                            self.queueOperation(selected);
                        }
                    } else {
                        self.queueOperation(selected);
                    }
                };

                self._.selected = [];
            }
        });

        self._.el.$thumbsContainer.bind('mousedown', function() {
            clearInterval(self._.offsetSelectionInterval);
            self._.offsetSelectionInterval = setInterval(self.offsetSelection, 50);
        });

        $(document).bind('mouseup', function() {
            clearInterval(self._.offsetSelectionInterval);
        });
    },

    submit : function() {
        this._.images = this.startOperation();

        this._.el.$submitButton.disable();
        this.toggleOperationsLoader();

        var self = this;

        $.post(
            this._.postURL+'/'+this._.album, 'hashes='+this._.images+'&change_cover='+this._.changeCover,
            function(response) {
                if(response.success) {
                    self._.el.$submitButton.enable();
                    self.toggleOperationsLoader();
                    self._.onSubmitComplete(response);
                } else if(response.data.error.code == 4344) {
                    self._.el.$submitButton.enable();
                    self.toggleOperationsLoader();
                    humanMsg.displayMsg('You are trying to add too many images to this album');
                } else if(response.data.error.code == 403) {
                    self._.onSubmitComplete(response);
                    humanMsg.displayMsg('Cannot edit album images while album is in the Gallery');
                } else {
                    humanMsg.displayMsg('Unknown error occurred');
                }
            }
        ).error(function(response) {
            self._.el.$submitButton.enable();
            self.toggleOperationsLoader();
            var json = JSON.parse(response.responseText);
            humanMsg.displayMsg(json.data.error);
        });

        return false;
    },

    loadImages : function() {
        if(this._.album == null || typeof this._.album == 'undefined') return;

        var imgurImages = Imgur.Images.getInstance();
        imgurImages.init(
            {
            imagesPerPage   : this._.imagesPerPage,
            addToThumbsFunc : this.addImageToThumbs,
            noImagesFunc    : this.noImagesFound,
            onComplete      : this.makeSelectable,
            loader          : this._.el.$loader,
            noPageHash      : true
            }
        );

        imgurImages.setOptions(this.getPageOptions());

        this.next = function() {
            imgurImages.next();
        };

        this.prev = function() {
            imgurImages.prev();
        };

        this.changePage = function(page) {
            imgurImages.changePage(page);
        };

        imgurImages.bindScrollEvent();
        imgurImages.reload();
        this._.imgurImages = imgurImages;
    },

    loadPageElements : function() {
        if(!this._.album) return;

        this._.el.$modes.filter(':first').attr('data-value', this._.album);
        this._.el.$modes.removeClass('selected');

        if(!this._.mode) {
            this._.el.$modes.siblings('.start').addClass('selected');
        } else {
            this._.el.$modes.siblings('div[data-value='+this._.mode+']').addClass('selected');
            // If we initially have a mode, use it
            this._.album = this._.mode;
        }

        // Init image selection pool with current settings
        this._.imgurImages.setCurrentPage(1);
        this._.imgurImages.setOptions(this.getPageOptions());
        this._.imgurImages.reload();
        this._.el.$modes.click(this.modeClickHandler);
    },

    modeClickHandler : function() {
        var self = Imgur.Album.Images.getInstance();
        self._.el.$modes.removeClass('selected');
        $(this).addClass('selected');

        var albumMode = $(this).attr('data-value');
        self._.album = albumMode;

        self._.imgurImages.setCurrentPage(1);
        self._.imgurImages.setOptions(self.getPageOptions());
        self._.imgurImages.reload();
    },

    destroy : function() {
        this._.el.$thumbsContainer.empty();
        this._.el.$modes.unbind('click');
        this._.el.$modes.removeClass('selected');
        this._.el.$loader.show();
    }
});

Class.addSingleton(Imgur.Album.Images);
