Namespace('Imgur');

Imgur.GalleryPost = React.createClass({
    displayName: 'GalleryPost',

    getInterestingEvents: function() {
        return [
            'select'
        ];
    },

    render: function() {
        var img = this.props.currentImageStore.getCurrentImage();
        var title = img.title;
        var matureClass = img.nsfw ? 'mature-indicator' : '';

        if(title == 'untitled') {
            title = '';
        }

        if(img.isAd) {
            return React.DOM.div(null,
                this.props.promotedTag(),
                React.DOM.h1({
                    id: 'image-title',
                    className: matureClass
                }, title)
            );
        }

        return React.DOM.div(null,
            React.DOM.h1({
                id: 'image-title',
                className: matureClass
            }, title)
        );
    }
});
