Namespace('Imgur.Comment');

Imgur.Comment = React.createClass({
    displayName: 'Comment',

    _isMounted: true,

    propTypes: {
        childmap: React.PropTypes.object.isRequired,
        contextids: React.PropTypes.object.isRequired,
        image: React.PropTypes.object.isRequired,
        comment: React.PropTypes.object.isRequired,
        votable: React.PropTypes.bool.isRequired,
        votemap: React.PropTypes.object,
        onSubmit: React.PropTypes.func,
        expanded: React.PropTypes.bool,
        haveChildren: React.PropTypes.bool,
        touchEnabled: React.PropTypes.bool.isRequired,
    },

    contextTypes: {
        msid: React.PropTypes.string.isRequired,
        onContextChange: React.PropTypes.func.isRequired,
        linkifier: React.PropTypes.object.isRequired,
        onVoteChange: React.PropTypes.func
    },

    getInitialState: function() {
        return {
            showingReply: false,
            reported: false
        };
    },
    
    componentWillUnmount: function() {
        this._isMounted = false;
    },

    handleReplyClicked: function() {
        Imgur.Util.jafoLog({
            event: 'newComment',
            meta: {
                parentId: this.props.comment.parent_id,
                hash: this.props.comment.hash,
                state: 'initiated'
            }
        });

        this.setState({
            showingReply: true
        });
    },

    handleReplySuccess: function() {
        this.setState({
            showingReply: false
        });

        if(this.props.onSubmit) {
            this.props.onSubmit();
        }
    },

    handlePermalinkClick: function(e) {
        e.stopPropagation();

        this.context.onContextChange(e, this.props.comment.id, 0);

        Imgur.Util.jafoLog({
            event: 'permalinkInvoked',
            meta: {
                type: 'comment',
                commentId: this.props.comment.id,
                hash: this.props.comment.hash,
                isAlbum: this.props.comment.on_album,
                nsfw: this.props.image.nsfw,
                parentId: this.props.comment.parent_id
            }
        });

    },

    handleContextClick: function(e) {
        e.stopPropagation();

        this.context.onContextChange(e, this.props.comment.id, 1);

        Imgur.Util.jafoLog({
            event: 'contextInvoked',
            meta: {
                type: 'comment',
                commentId: this.props.comment.id,
                hash: this.props.comment.hash,
                isAlbum: this.props.comment.on_album,
                nsfw: this.props.image.nsfw,
                parentId: this.props.comment.parent_id
            }
        });



    },

    handleShareClick: function(e) {

        Imgur.Util.jafoLog({
            event: 'shareInvoked',
            meta: {
                type: 'comment',
                commentId: this.props.comment.id,
                hash: this.props.comment.hash,
                isAlbum: this.props.comment.on_album,
                nsfw: this.props.image.nsfw,
                parentId: this.props.comment.parent_id
            }
        });



        var url = window.location.protocol + '//' + Imgur.Environment.host + Imgur.Environment.baseURL + '/' + this.props.comment.hash + '/comment/' + this.props.comment.id;
        var title = this.props.comment.author + ' comments on \'' + this.props.image.title + '\' - Imgur';

        var share = Imgur.ShareButtons.getInstance();
        share.setShareData({
            jafoMeta: _.extend(Imgur.Gallery.getInstance().getGalleryMetadata(), {
                type: 'comment',
                hash: this.props.comment.hash, 
                isAlbum: this.props.comment.on_album,
                inGallery: true,
                commentId: this.props.comment.id,
                parentId: this.props.comment.parent_id
            }),
            title: title,
            url: url
        });
    },

    handleMobileIconClick: function(e) {
        Imgur.Util.jafoLog({
            event: 'viaAppIcon',
            meta: {
                type: 'comment',
                commentId: this.props.comment.id,
                platform: this.props.comment.platform
            }
        });
    },

    handleDeleteClick: function(e) {
        var msg = 'Are you sure you want to delete this comment?';

        if(!confirm(msg)) {
            return;
        }

        $.ajax({
            'url'    : '/gallery/action/delete_caption/' + this.props.comment.id,
            'type'   : 'POST',
            'data'   : { 'sid' : this.context.msid }
        }).fail(_.bind(function() {
            Imgur.Caption.getInstance().undeleteCaption(this.props.comment.hash, this.props.comment.id);
            
            if (this._isMounted) {
                this.forceUpdate();
            }            
        }, this));

        Imgur.Caption.getInstance().deleteCaption(this.props.comment.hash, this.props.comment.id);

        this.forceUpdate();
    },

    handleReportClick: function(e) {
        var metadata = {
            type: 'comment',
            commentId: this.props.comment.id,
            hash: this.props.comment.hash,
            isAlbum: this.props.comment.on_album,
            nsfw: this.props.image.nsfw
        };

        Imgur.Util.jafoLog({
            event: 'reportInvoked',
            meta: metadata
        });

        if(!Imgur.Environment.auth.id) {
            Imgur.getInstance().signin('reportAttempt');
        } else {
            $.colorbox({
                html: '<div id="report-container"></div>',
                open: true,
                top: '10%',
                transition: 'none',
                scrolling: false,
                fixed: true,
                width: Imgur.Elements.Report.dimensions.width,
                height: Imgur.Elements.Report.dimensions.height,
                className: 'noBorder',
                close: '',
                onComplete: _.bind(function() {
                    var ReactDOM = window.ReactDOM || window.React;
                    ReactDOM.render(React.createElement(Imgur.Elements.Report,{
                        type: 'comment',
                        url: '/gallery/action/report_caption/' + this.props.comment.id,
                        metadata: metadata,
                        cb: _.bind(function() {
                            this.setState({
                                reported: true
                            });
                        }, this)
                    }), $('#report-container').get(0));
                }, this),
                onClosed: _.bind(function() {
                    if (!Imgur.Gallery.getInstance()._.preventReportCloseAnalytics) {
                        Imgur.Util.jafoLog({
                            event: 'reportCancel',
                            meta: _.extend({
                                method: 'modal'
                            }, metadata)
                        });
                    }

                    Imgur.Gallery.getInstance().resetReportCloseAnalytics();
                }, this)
            });
        }
    },

    handleUpvoteClick: function() {
        var current_vote = this.props.votemap[this.props.comment.id];

        if(current_vote === 'up') {
            this._vote('veto');
        } else {
            this._vote('up');
        }
    },

    handleDownvoteClick: function() {
        var current_vote = this.props.votemap[this.props.comment.id];

        if(current_vote === 'down') {
            this._vote('veto');
        } else {
            this._vote('down');
        }
    },

    _vote: function(direction) {
        if(!Imgur.Environment.signed) { 
            Imgur.getInstance().signin({ invokedRegisterSigninBy: 'voteAttempt', type: 'comment' }); 
        }
        var location = Imgur.InsideNav && Imgur.InsideNav.getInstance().getHash() ? 'inside' : 'outside';

        Imgur.Caption.getInstance().setVote(this.props.comment.hash, this.props.comment.id, direction);
        this.context.onVoteChange(this.props.comment, direction);

        $.ajax({
            url: '/gallery/action/vote/' + this.props.comment.id,
            type : 'POST',
            data : {
                'vote': direction,
                'sid' : this.context.msid,
                'type': 'caption',
                'location': location,
                'gallery': Imgur.Gallery.getInstance().getGalleryMetadata()
            },
        });

        var jafoData = {
            event: 'vote' + (this.props.image.readonly ? 'ReadOnly' : ''),
            meta: _.extend(Imgur.Gallery.getInstance().getGalleryMetadata(), {
                'type': 'comment',
                'vote': direction,
                'location': location,
                'commentId': this.props.comment.id,
                'hash': this.props.comment.hash,
                'parentId': this.props.comment.parent_id
            })
        };

        Imgur.Util.jafoLog(jafoData);
    },

    _nthIfy: function(n) {
        if(n === 1) {
            return '1st';
        } else if(n === 2) {
            return '2nd';
        } else if(n === 3) {
            return '3rd';
        } else {
            return String(n) + 'th';
        }
    },

    handleUserProfileClick: function() {
        Imgur.Util.jafoLog({
            event: 'userProfileNav',
            meta: {
                accountId: this.props.comment.author_id,
                method: 'usernameComment'
            }
        });
    },

    handleMenuClick: function(opening) {
        if (!opening) {
            return;
        }

        Imgur.Util.jafoLog({
            event: 'moreMenuInvoked',
            meta: {
                commentId: this.props.comment.id,
                hash: this.props.comment.hash,
                isAlbum: this.props.comment.on_album,
                nsfw: this.props.image.nsfw,
                parentId: this.props.comment.parent_id
            }
        });
    },

    closeReply: function() {
        this.setState({
            showingReply: false
        });
    },

    render: function() {
        var op = '', replies = 0;

        var expand_replies;

        if(this.props.comment.id in this.props.childmap && this.props.haveChildren) {
            replies = this.props.childmap[this.props.comment.id].length;

            if (replies > 0) {
                var icon = React.DOM.span({key: 'reply-vertical', className: 'comment-reply-icon ' + (this.props.expanded ? '' : ' comment-reply-icon--vertical')});
                var text = this.props.expanded ? ' Collapse' : ' ' + replies + (replies > 1 ? ' replies' : ' reply');

                expand_replies = React.DOM.span({className: 'comment-reply-count'}, React.DOM.span({className: 'comment-reply-icon'}), icon, React.DOM.span({className: 'comment-reply-count-text'}, text));
            }
        }

        var upvoted = this.props.votemap[this.props.comment.id] == 'up';
        var downvoted = this.props.votemap[this.props.comment.id] == 'down';

        var upvoteClasses = classNames({
            'comment-vote': true,
            'icon-upvote-fill': true,
            'green': upvoted
        });

        var downvoteClasses = classNames({
            'comment-vote': true,
            'icon-downvote-fill': true,
            'red': downvoted
        });

        var actions = React.DOM.div({className: 'comment-votes' + (this.props.touchEnabled ? ' touch' : '')},
            (!this.props.comment.deleted ? React.DOM.div({className: 'comment-votes-wrapper'},
                this.props.votable ? React.createElement(Imgur.Elements.PressButton, {onPress: this.handleUpvoteClick, className: upvoteClasses}) : '',
                this.props.votable ? React.createElement(Imgur.Elements.PressButton, {onPress: this.handleDownvoteClick, className: downvoteClasses}) : ''
            ) : '')
        );
        
        var best_comment_of_the_day = this.props.comment.best_comment ? 
            React.DOM.span({className: 'caption-awards right title'}, this._nthIfy(this.props.comment.best_comment)) :
            null;

        var className = classNames({
            'caption': true,
            'pointer': this.props.haveChildren,
            'caption--haschildren': this.props.haveChildren,
            'caption-deleted': this.props.comment.deleted,
            'caption-deleted--noreply': this.props.comment.just_deleted && !this.props.haveChildren,
            'unvotable': !this.props.votable,
            'contexted': this.props.comment.id in this.props.contextids
        });
        var userTextClasses = classNames({
            'usertext': true,
            'cf': true,
            'comment-upvoted': upvoted,
            'comment-downvoted': downvoted
        });

        var body = this.context.linkifier.grab(this.props.comment.caption);

        var point_inflection = this.props.comment.visible_points === 1 ? ' pt' : ' pts';

        var via = null;
        var mobile_platform_strings = {
            'iphone': 'iPhone',
            'android': 'Android',
        };
        var mobile_platforms = Object.keys(mobile_platform_strings);

        if (mobile_platforms.indexOf(this.props.comment.platform) > -1) {
            via = React.DOM.span({className: 'via'},
                ' via ',
                React.DOM.a({href: '/apps', target: '_blank', onClick: this.handleMobileIconClick},
                    mobile_platform_strings[this.props.comment.platform],
                    React.DOM.span({className: 'icon icon-mobile'}, null)
                )
            );
        }

        return React.DOM.div({className: className, 'data-id': this.props.comment.id},
            React.DOM.div({className: userTextClasses},
                best_comment_of_the_day,
                React.DOM.div({className: 'author'},
                    React.DOM.a({className: 'comment-username', href: '/user/' + this.props.comment.author, title: this.props.comment.author, onClick: this.handleUserProfileClick}, this.props.comment.author),
                    (this.props.image.account_id && this.props.image.account_id === this.props.comment.author_id && !this.props.comment.deleted) ? React.DOM.span({className: 'comment-op'}, 'OP') : '',
                    via,
                    React.DOM.span({className: this.props.comment.deleted ? '' : 'comment-meta-spacer'}, Imgur.Util.numberFormat(this.props.comment.visible_points) + point_inflection),
                    React.createElement(Imgur.Elements.Timestamp, {classNames: 'comment-meta-spacer', shouldUpdate: false, timestamp: this.props.comment.datetime}),
                    React.createElement(Imgur.Elements.PopupMenu,{className: 'icon-ellipses caption-toolbar edit-button comment-meta-spacer', menuClick: this.handleMenuClick},
                        React.DOM.div({onClick: this.handleShareClick, className: 'addthis_button item share-caption-link'}, 'share'),
                        ((this.state.reported || $.cookie('c'+this.props.comment.id) == 1) ?
                                React.DOM.div({className: 'item report-caption-link red'}, 'reported') :
                                React.DOM.div({onClick: this.handleReportClick, className: 'item report-caption-link'}, 'report')
                        ),
                        React.DOM.a({onClick: this.handlePermalinkClick, className: 'item permalink-caption-link', href: '/gallery/' + this.props.comment.hash + '/comment/' + this.props.comment.id}, 'permalink'),
                        (this.props.comment.parent_id != null ?
                                React.DOM.a({onClick: this.handleContextClick, className: 'item context-caption-link', href: '/gallery/' + this.props.comment.hash + '/comment/' + this.props.comment.id + '/1'}, 'context')
                                : ''
                        ),
                        (Imgur.Environment.signed && (Number(this.props.comment.author_id) === Imgur.Environment.auth.id || Imgur.Environment.isGalleryAdmin) ?
                                React.DOM.a({onClick: this.handleDeleteClick, className: 'item delete-caption-link red', href: 'javascript:void(0)'}, 'delete')
                                : ''
                        )
                    )
                ),
                body,
                this.props.comment.deleted ? '' : React.DOM.div({className: 'comment-create-reply', onClick: this.handleReplyClicked},
                    React.DOM.div({className: 'comment-create-reply-wrapper'},
                        React.DOM.div({className: 'icon-reply'}),
                        React.DOM.div({}, 'Reply')
                    )
                )
            ),
            (this.state.showingReply || !this.props.haveChildren || this.props.expanded) ? '' : React.DOM.div({className: 'caption-gradient-wrapper'}, React.DOM.div({className: 'caption-gradient'})),
            actions,
            expand_replies,
            (this.state.showingReply ?
                React.createElement(Imgur.Caption.Reply, {
                    autofocus: true,
                    ref: 'reply',
                    image: this.props.image,
                    parentId: this.props.comment.id, 
                    parentAuthorId: Number(this.props.comment.author_id),
                    onSuccess: this.handleReplySuccess,
                    onClose: this.closeReply
                })
                : ''
            )
        );
    }
});
