Namespace('Imgur.Tag');

Imgur.Tag.GroupSelector = Class.extend({
    init: function(emitter, section, sort, inSearch, $container, activeGroup) {
        this._ = _.merge(this._ || {}, {
            emitter: emitter,
            section: section,
            sort: sort,
            inSearch: inSearch,
            activeGroup: activeGroup,
            el: {
                $selection: $container.find('#section .selection')
            }
        });
    },

    changeSelectionToTag: function(tag) {
        this._.el.$sectionName.html(tag);
    },

    getActiveGroupId: function() {
        return this._.activeGroup.getId();
    },  

    getActiveGroup: function() {
        return this._.activeGroup;
    },

    getActiveGroupName: function() {
        return this._.activeGroup.getName();
    },

    getActiveTag: function() {
        return this._.tag;
    },

    getActiveTagName: function() {
        return this._.tag.getName();
    },

    setActiveGroupName: function(name) {
        this._.activeGroup.setName(name);
        this._.el.$selection.text(name).trim();
    },

    findTagInActiveGroup: function(tagName) {
        return this._.activeGroup.findFirstTagBy(function(cur_tag) {
            return cur_tag.getName() === tagName;
        });
    }
});

