Namespace('Imgur.Tag');

Imgur.Tag.SuggestedTag = Imgur.Tag.Tag.extend({
    VOTE_UP: 'up',

    init: function($dom, group_id) {
        this._super($dom);

        this._.groupId = group_id;
        this._.el.$addBtn = $dom;
        this._.el.$subscribers = $dom.find('.subscribers');
    },

    getGroupId: function() {
        return this._.groupId;
    },

    get$Add: function() {
        return this._.el.$addBtn;
    },

    getSubscribers: function() {
        return Number(this._.el.$subscribers.attr('data-subscribers'));
    },

    get$ExternalLink: function() {
        return this._.el.$container.find('.external-link');
    }
});
