Namespace('Imgur.Utilities.Embed');

Imgur.Utilities.Embed = {
    elColorboxEmbed : function() {
        return {
            id                 : '#embed-post',
            form               : $('#new-embed-post'),
            checkbox           : $('#data-context'),
            embedCodeInput     : $('#new-embed-post #embedcode'),
            clipboardLink      : $('#new-embed-post #embed-clipboard-link'),
            clipboardContainer : $('#new-embed-post .clipboard-container'),
            preview            : $('#embed-preview'),
            oldEmbed           : $('#old-embed-view')
        };
    },

    elEmbedAlbumForm : function() {
        return {
            id                 : '#embed-album-form',
            form               : $('#embed-album-form'),
            embedCodeInput     : $('#embed-album-form #embedcode'),
            embedCodePreview   : $('#embed-album-form #embedcodePreview a'),
            clipboardContainer : $('#embed-album-form .clipboard-container'),
            newEmbed           : $('#new-embed-view'),
            hashRegex          : new RegExp('/a/(.*?)/')

        }
    },

    embedModal : function() {
        Imgur.Utilities.Embed.setupEmbedImage.call(this);
        this._.el.colorboxEmbed.form.removeClass('nodisplay');
        this._.el.embedAlbumForm.form.addClass('nodisplay');
        this._.el.colorboxEmbed.oldEmbed.addClass('nodisplay');
        this._.el.colorboxEmbed.checkbox.change(_.partial(Imgur.Utilities.Embed.onEmbedCheckboxClick, this));


        $.colorbox({
            href: this._.el.colorboxEmbed.id,
            open: true,
            width: '580px',
            inline: true,
            top: '15%',
            className: 'embed-post-colorbox',
            transition: 'none',
            scrolling: false,
            onComplete: _.bind(Imgur.Utilities.Embed.bindEmbedImage, this)
        });
    },

    getPostMetadata : function() {
        var options = {
            event: 'openEmbedModal'
        };

        if(this.imgurInsideNav) {
            var hash = this.imgurInsideNav.getHash();

            if(hash) {
                var item = this.imgurInsideNav._.sideGallery.getImage(hash);

                //Image not in sidegallery because it came from non-gallery initial page load
                if (!item) {
                    item = this._.image;
                }

                this._.album = item.is_album ? item : {};
                Imgur.Util.jafoLog(_.extend(options, {
                    meta: {
                        inGallery: (_.isNumber(item.comment_count) ? true : false),
                        hash: hash,
                        isAlbum: !!item.is_album,
                        nsfw: !!item.nsfw,
                        control: 'postOptions'
                    }

                }));

                return {
                    id: (item.is_album ? 'a/' : '') + hash,
                    title: item.is_album ? item.title : item.description
                };
            }
        }
        Imgur.Util.jafoLog(_.extend(options, {
            meta: {
                inGallery: false,
                hash: (this._.album.id || this._.id),
                isAlbum: true,
                nsfw: false,
                control: 'albumOptions'
            }

        }));
        return {
            id: 'a/' + (this._.album.id || this._.id),
            title: this._.album.title
        };
    },

    getSnippet: function(hash, title, showContext, logAnalytics){
        var host    = Imgur.Environment.host;
        var source  = (host === 'imgur.com' ? 's.imgur.com/min' : host + '/include/minified') + '/embed.js';
        var context = showContext === false ? ' data-context="' + showContext + '"': '';
        var analytics = logAnalytics ? '': ' data-analytics="false"';

        return '<blockquote class="imgur-embed-pub" lang="en" data-id="' + hash + '"' + context + analytics + '><a href="//' +
            host + '/' + hash + '">' + _.escape(title) + '</a></blockquote><script async src="//' + source + '" charset="utf-8"></script>';
    },

    bindEmbedImage : function(showContext) {
        this._.album = this._.album || {};
        var postData = Imgur.Utilities.Embed.getPostMetadata.call(this);
        if(!postData.title) {
            postData.title = 'View post on imgur.com';
        }

        var snippet = Imgur.Utilities.Embed.getSnippet(postData.id, postData.title, showContext, true);

        this._.el.colorboxEmbed.preview.html(Imgur.Utilities.Embed.getSnippet(postData.id, postData.title, showContext, false));
        this._.el.colorboxEmbed.embedCodeInput.val(snippet);
        flashElement(this._.el.colorboxEmbed.embedCodeInput);
        Imgur.Utilities.Embed.initEmbedClipboard.call(this, this._.el.colorboxEmbed.clipboardContainer);
        if (!this._.vars.newEmbedAlbumFormLoaded) {
            this._.el.colorboxEmbed.embedCodeInput.focus();
            _.defer(_.bind(this._.el.colorboxEmbed.embedCodeInput.selectAll, this));
            try {
                this._.vars.embedClipboard.glue('new-embedcode-clipboard', 'new-embedcode-container');
                this._.vars.embedClipboard.addEventListener('onMouseUp', _.bind(function() {
                    this._.vars.embedClipboard.setText(this._.el.colorboxEmbed.embedCodeInput.val());
                }, this));
                this._.vars.newEmbedAlbumFormLoaded = true;
            } catch(e) {}
        }

        $.colorbox.resize();
    },

    viewOldEmbed : function() {
        var hash;
        this._.el.colorboxEmbed.form.addClass('nodisplay');
        this._.el.embedAlbumForm.form.removeClass('nodisplay');
        if(this._.baseURL === '/gallery') {
            hash = this.imgurInsideNav.getHash();
        } else {
            hash = (this._.album.id || this._.id);
        }
        Imgur.Utilities.Embed.setEmbedUrl.call(this, '//' + this._.host + '/a/' + hash + '/embed');

        this._.el.embedAlbumForm.newEmbed.click(_.bind(function() {
            this._.el.colorboxEmbed.form.removeClass('nodisplay');
            this._.el.embedAlbumForm.form.addClass('nodisplay');
            Imgur.Utilities.Embed.initEmbedClipboard.call(this, this._.el.colorboxEmbed.clipboardContainer);
            $.colorbox.resize();
        }, this));

        Imgur.Utilities.Embed.initEmbedClipboard.call(this, this._.el.embedAlbumForm.clipboardContainer);
        flashElement(this._.el.embedAlbumForm.embedCodeInput);

        Imgur.Util.jafoLog({
            event: 'oldEmbed',
            meta: {
                hash: this._.id
            }
        });

        Imgur.Utilities.Embed.setEmbedCode.call(this);

        if (!this._.vars.embedAlbumFormLoaded) {
            this._.el.embedAlbumForm.embedCodeInput.focus();
            setTimeout(_.bind(function(){this._.el.embedAlbumForm.embedCodeInput.selectAll()}, this), 10);
            try {
                this._.vars.embedClipboard.glue('embedcode-clipboard', 'embedcode-container');
                this._.vars.embedClipboard.addEventListener('onMouseUp', _.bind(function() {
                    this._.vars.embedClipboard.setText(this._.el.embedAlbumForm.embedCodeInput.val());
                }, this));
                this._.vars.embedAlbumFormLoaded = true;
            } catch(e) {}
        }

        $.colorbox.resize();
    },

    setupEmbedImage : function() {
        var eventMethod    = window.addEventListener ? 'addEventListener' : 'attachEvent';
        var receiveMessage = window[eventMethod];
        var messageEvent   = eventMethod == 'attachEvent' ? 'onmessage' : 'message';

        // Custom post message event handler
        receiveMessage(messageEvent, _.bind(function(e) {
            var eventData = JSON.parse(e.data);
            if (eventData.message === 'resize_imgur') {
                if(this._.album.id) {
                    this._.el.colorboxEmbed.oldEmbed.removeClass('nodisplay');
                    this._.el.colorboxEmbed.oldEmbed.click(_.bind(Imgur.Utilities.Embed.viewOldEmbed, this));
                }
                this._.el.colorboxEmbed.preview.css('height', eventData.height + 'px');
                $('.imgur-embed-iframe-pub').height = eventData.height + 'px';
                if($('.cboxIframe').length === 0) { //makes sure we aren't resizing the old embed preview
                    $.colorbox.resize();
                }
            }
        }, this), false);
    },

    onEmbedCheckboxClick : function(view) {
        if($(this).is(':checked')) {
            Imgur.Utilities.Embed.bindEmbedImage.call(view, false);
        } else {
            Imgur.Utilities.Embed.bindEmbedImage.call(view);
        }
    },

    initEmbed : function() {
        Imgur.Utilities.Embed.setupEmbedCss.call(this);
        //Don't need thumbs carousel if thumbs are hidden
        if (this._.el.layout.global.thumbsCarouselContainer.length) {
            this.initThumbsCarousel();
        }
        this.initMainCarousel();
        Imgur.Utilities.Embed.setupEmbedScreen.call(this);

        $(window).resize(_.bind(function() {
            Imgur.Utilities.Embed.setupEmbedCss.call(this);
            Imgur.Utilities.Embed.setupEmbedScreen.call(this);
        }, this));
    },

    setupEmbedCss : function() {
        $('head').append('<style>.main-carousel ul li { width: '+$('.embed').parent().width()+'px; }</style>');
        this._.vars.bodyHeight = $('.embed').parent().height();


        var cssParams = Imgur.Util.getQueryParams(window.location.search);
        var $dom = $(_.templates['album.embedStyles']({
            background : cssParams.background,
            text       : cssParams.text,
            link       : cssParams.link
        }));

        if(this.chooseLogoColor(cssParams.background)) {
            this._.el.layout.embed.offsite.addClass('light');
        }

        $dom.appendTo('head');
    },

    setupEmbedScreen : function() {
        var width       = $('.embed').parent().width(),
            height      = $('.embed').parent().height(),
            tHeight     = 1,
            thumbs      = true;

        //Check if thumbs exist before adding bottom padding
        if (this._.el.layout.global.thumbsCarouselContainer.length) {
            tHeight += this._.el.layout.global.thumbsCarousel.height()+18;
        } else {
            thumbs = false;
            this._.el.layout.global.description.css('maxHeight', 121);
        }

        if(this._.el.layout.global.title.is(':visible')) {
            tHeight += $('#title').height() + 10;
        }

        this._.el.layout.global.container.css({
            lineHeight : (height - tHeight) + 'px',
            height     : this._.vars.bodyHeight - tHeight
        });
        
        $('.embed #thumbs-top').css({
            width: Math.min(79*this._.images.count, width-65)
        });

        $('.embed .thumbs-carousel').css({
            width: Math.min(79*this._.images.count+45, width-20)
        });

        var imageHeight = $('.embed .image').height();

        //Position the description dialog depending on if there are thumbnails or not
        this._.el.layout.global.description.css({
            top : imageHeight - (thumbs ? 48 : Math.min(121, this._.el.layout.global.description.innerHeight()))
        });
    },

    setEmbedUrlAlbumId : function(albumId) {
        Imgur.Utilities.Embed.setEmbedUrl.call(this, this._.embedUrl.replace(this._.el.embedAlbumForm.hashRegex, '/a/' + albumId + '/'));
    },

    setEmbedUrl : function(embedUrl) {
        this._.embedUrl = embedUrl;
    },

    setEmbedCode : function() {
        this._.el.embedAlbumForm.embedCodeInput.val('<iframe class="imgur-album" width="100%" height="550" frameborder="0" src="' + this._.embedUrl + '"></iframe>');
        this._.el.embedAlbumForm.embedCodePreview.attr('href', this._.embedUrl);
    },

    // ***************** EMBED CLIPBOARD FUNCTIONS *****************

    clipboardClick : function(e) {
        flashElement($(e).siblings('input'));

        $(e).attr('title', 'copied');
        $(e).tipsy('show');
        $(e).attr('original-title', 'copy');
    },

    initEmbedClipboard : function(clipboardIconElement) {
        this._.vars.embedClipboard = new ZeroClipboard.Client();

        this._.vars.embedClipboard.addEventListener('onMouseUp', _.partial(Imgur.Utilities.Embed.clipboardClick, clipboardIconElement));

        clipboardIconElement.tipsy({
            gravity: 'e',
            opacity: 1,
            html: true
        });
    }
};
