Namespace('Imgur.Tag.Controller');

Imgur.Tag.Controller.OutsideComponent = Class.extend({
    init: function(suggestionModel, suggestionStore) {
        this._ = {
            suggestionModel: suggestionModel, 
            suggestionStore: suggestionStore
        };
    },

    handleChangedQuery: function() {
        var query = this._.suggestionStore.getQuery();

        this._.suggestionStore.setIsLoading(true);
        this._.suggestionModel.cancel();
        this._.suggestionModel.fetch(query, _.bind(this._handleSuggestResponse, this), _.bind(this._handleSuggestError, this, query));
    },

    _handleSuggestResponse: function(query, tags) {
        this._.suggestionStore.setSuggestions(tags);
        this._.suggestionStore.setIsLoading(false);
        this._.suggestionStore.setLastFinishedQuery(query);
    },

    _handleSuggestError: function(query) {
        this._.suggestionStore.setLastFinishedQuery(query);
        this._.suggestionStore.setIsLoading(false);
    }
});
