Namespace('Imgur.Element');

Imgur.Element.SortedListEmptyable = Imgur.Element.SortedList.extend({
    init: function($container, $empty, comparator, initialList, emptyMessageProvider) {
        this._super($container, comparator, initialList);
        this._.el.$empty = $empty;
        this._.emptyMessageProvider = emptyMessageProvider || function() { return $empty; };
    },

    setEmptyMessageProvider: function(provider) {
        this._.emptyMessageProvider = provider;
    },

    getEmptyMessageProvider: function() {
        return this._.emptyMessageProvider;
    },

    _replaceEmptyMessage: function() {
        var $empty = $(this._.emptyMessageProvider());
        this._.el.$empty.replaceWith($empty);
        this._.el.$empty = $empty;
    },

    add: function(el) {
        this._super(el);
        this.hideEmptyMessage();
    },

    prepend: function(el) {
        this.hideEmptyMessage();
        this._super(el);
    },

    append: function(el) {
        this.hideEmptyMessage();
        this._super(el);
    },

    remove: function(el) {
        this._super(el);

        if(this._.list.length == 0) {
            this.showEmptyMessage();
        }
    },

    removeElementPredicate: function(predicate) {
        this._super(predicate);

        if(this._.list.length == 0) {
            this.showEmptyMessage();
        }
    },

    clear: function() {
        this._super();
        this.showEmptyMessage();
    },

    hasEmptyMessage: function() {
        return !this._.el.$empty.hasClass('nodisplay');
    },

    hideEmptyMessage: function() {
        if(this.hasEmptyMessage()) {
            this._.el.$empty.addClass('nodisplay');
        }
    },

    showEmptyMessage: function() {
        this._replaceEmptyMessage();

        if(!this.hasEmptyMessage()) {
            this._.el.$empty.removeClass('nodisplay');
        }
    }
});
