Namespace('Imgur');

Imgur.InsideCommentsArea = React.createClass({
    displayName: 'InsideCommentArea',

    propTypes: {
        image: React.PropTypes.object.isRequired,
        loading: React.PropTypes.bool.isRequired,
        defaultExpandMode: React.PropTypes.number.isRequired,
        expandedFromPermalink: React.PropTypes.bool.isRequired,
        childmap: React.PropTypes.object.isRequired,
        commentSort: React.PropTypes.string.isRequired,
        onCommentSortChange: React.PropTypes.func.isRequired,
        contextId: React.PropTypes.number,
        contextLevels: React.PropTypes.number,
        permalink: React.PropTypes.object
    },

    contextTypes: {
        onContextChange: React.PropTypes.func.isRequired
    },

    mixins: [React.addons.PureRenderMixin],

    handleClickPermalinkViewRestBanner: function(e) {
        e.preventDefault();
        this.context.onContextChange(e, undefined, undefined);
    },

    getInitialState: function() {
        return {
            commentReplyNumber: 0,
            expandMode: this.props.defaultExpandMode,
            expandedFromPermalink: this.props.expandedFromPermalink,
            forceCollapsed: false,
            touchEnabled: !!this.touchEnabled(),
        };
    },

    // from http://stackoverflow.com/a/4819886
    touchEnabled: function() {
        return 'ontouchstart' in window        // works on most browsers
            || navigator.maxTouchPoints;       // works on IE10/11 and Surface
    },

    componentDidMount: function() {
        var $parentNode = $(ReactDOM.findDOMNode(this).parentNode);

        if(!$parentNode.hasClass('comments-initialized')) {
            $parentNode.addClass('comments-initialized');
        }

        $(document).on('keyup', this.handleDocumentKeyUp);
    },

    componentWillUnmount: function() {
        var $parentNode = $(ReactDOM.findDOMNode(this).parentNode);
        $(document).off('keyup', this.handleDocumentKeyUp);
    },

    componentWillReceiveProps: function(nextProps) {
        if(this.props.contextId !== nextProps.contextId && nextProps.contextId) {
            // When transitioning to a permalink, the comments have already been rendered, so they already
            // have a default collapsed state. Force them to open.
            this.setState({
                expandedFromPermalink: true
            });
        } else {
            this.setState({
                expandedFromPermalink: false
            })
        }

        if(this.props.image.hash !== nextProps.image.hash || this.props.image.is_album !== nextProps.image.is_album) {
            this.setState({
                showingReply: true
            });
        }
    },

    handleClickToggleForceExpanded: function(e) {
        if(this.state.expandMode !== Imgur.CommentTree.EXPAND_MODE_FORCE_EXPANDED) {
            this.setState({
                expandMode: Imgur.CommentTree.EXPAND_MODE_FORCE_EXPANDED
            })
            Imgur.Util.jafoLog({
                event: 'expandCollapseAllInvoked',
                meta: {
                    state: 'expand',
                    hash: this.props.image.hash
                }
            });
        } else {
            this.setState({
                expandMode: Imgur.CommentTree.EXPAND_MODE_FORCE_COLLAPSED - Math.abs(this.state.expandMode)
            })
            Imgur.Util.jafoLog({
                event: 'expandCollapseAllInvoked',
                meta: {
                    state: 'collapse',
                    hash: this.props.image.hash
                }
            });
        }
    },

    handleCommentSuccess: function() {
        // Increment the number so that we flush the state (text, errors, etc.)

        this.setState({
            commentReplyNumber: this.state.commentReplyNumber + 1
        });
    },

    handleDocumentKeyUp: function(e) {
        if(!e.ctrlKey && !e.altKey && !e.metaKey && !$(e.target).is('input,textarea')) {
            var meta = {
                commentId: null,
                hash: this.props.image.hash,
                isAlbum: this.props.image.isAlbum,
                nsfw: this.props.image.nsfw,
                parentId: null,
                location: 'hotkey'
            }

            if(e.keyCode === 219) { // '[' key
                if(this.state.expandMode === Imgur.CommentTree.EXPAND_MODE_FORCE_EXPANDED) {
                    Imgur.Util.jafoLog({
                        event: 'commentCollapseInvoked',
                        meta: meta
                    });

                    this.setState({
                        expandMode: Imgur.CommentTree.EXPAND_MODE_FORCE_COLLAPSED
                    });
                } else {
                    Imgur.Util.jafoLog({
                        event: 'commentExpandInvoked',
                        meta: meta
                    });

                    this.setState({
                        expandMode: Imgur.CommentTree.EXPAND_MODE_FORCE_EXPANDED
                    });
                }
            } else if(e.keyCode === 221) { // ']' key
                // Toggle force collapsed
                //
                // If the mode was, before they pressed one of the buttons, already FORCE_COLLAPSED e.g. they had pressed ] once prior,
                // then there will be no change in state as the mode is already FORCE_COLLAPSED, so the Pure Render Mixin will decide not to
                // re-render the tree, and moreover, even if we called forceUpdate, since the toggling of the internal CommentTree state occurs
                // in the componentWillReceiveProps lifecycle method, the comment won't get collapsed because the expandMode prop did not change (from FORCE_COLLAPSED to FORCE_COLLAPSED).
                //
                // Instead, we define EXPAND_MODE_FORCE_COLLAPSED as any value less than or equal to -1, and each time we force a collapse, we decrement the expandMode.
                // That way, the first time this code is called the expandMode will be -1, the next time -2, and so on, so that each key press both passes the Pure Render Mixin's test
                // and also triggers componentWillReceiveProps

                Imgur.Util.jafoLog({
                    event: 'commentCollapseInvoked',
                    meta: meta
                });

                this.setState({
                    expandMode: Imgur.CommentTree.EXPAND_MODE_FORCE_COLLAPSED - Math.abs(this.state.expandMode)
                });
            }
        }
    },

    render: function() {
        var children = [];
        var expanded = this.state.expandMode === Imgur.CommentTree.EXPAND_MODE_FORCE_EXPANDED;

        children.push(React.createElement(Imgur.Caption.Reply,{
            key: 'reply:' + String(this.state.commentReplyNumber) + ':' + this.props.image.hash,
            image: this.props.image,
            parentId: null,
            parentAuthorId: null,
            autofocus: false,
            onBlur: _.noop,
            onSuccess: this.handleCommentSuccess
        }));

        children.push(React.DOM.div({key: 'controls', className: 'comments-info'},
            React.DOM.div({className: 'left'},
                React.DOM.div({className: 'inline'},
                    React.DOM.span({className: 'comments-count'},
                        (this.props.loading ? 'LOADING' : Imgur.Caption.getInstance().countComments(this.props.image.hash))
                    ),
                    ' COMMENTS'
                )
            ),
            React.createElement(Imgur.Elements.PopupMenu,{className: 'comments-sort right', name: this.props.commentSort, afterNameIcon: 'icon-arrow-down'},
                    React.DOM.div({className: 'item' + (this.props.commentSort == 'best' ? ' selected' : ''), onClick: _.partial(this.props.onCommentSortChange, 'best')}, 'best'),
                    React.DOM.div({className: 'item' + (this.props.commentSort == 'new' ? ' selected' : ''), onClick: _.partial(this.props.onCommentSortChange, 'new')}, 'new'),
                    React.DOM.div({className: 'item' + (this.props.commentSort == 'top' ? ' selected' : ''), onClick: _.partial(this.props.onCommentSortChange, 'top')}, 'top')
            ),
            React.DOM.span({className: (expanded ? 'icon-collapse-all' : 'icon-expand') + ' comments-expand right pointer', onClick: this.handleClickToggleForceExpanded, href: 'javascript:void(0)'},
                (expanded ? 'Collapse All' : 'Expand All')
            ),
            React.DOM.div({className: 'clear'})
        ));


        if(!this.props.loading) {
            if(this.props.contextId) {
                children.push(React.DOM.div({key: 'permalink-banner', className: 'reply-header'},
                    React.DOM.span(null, 'You are viewing a single comment'),
                    React.DOM.p(null,
                        React.DOM.a({className: 'load-more', href: '/gallery/' + this.props.image.hash, onClick: this.handleClickPermalinkViewRestBanner}, 'view the rest of the comments')
                    )
                ));

                var contextids = {};
                var root_comment = Imgur.Caption.getInstance().getCaptionById(this.props.image.hash, this.props.contextId);
                var permalink_comment = Imgur.Caption.getInstance().getCaptionById(this.props.image.hash, this.props.permalink.commentId);

                if(Imgur.Environment.signed && Imgur.Environment.auth.id == permalink_comment.author_id) {
                    var filtered_captions = Imgur.Caption.getInstance().getCaptionIdsByFilter(this.props.image.hash, _.bind(function(caption) {
                        var unixTimeCreated = moment.utc(caption.datetime, 'YYYY-MM-DD HH:mm:ss').unix();
                        var isDescendentComment = (permalink_comment.id == caption.parent_id);
                        var isAuthorComment = (Imgur.Environment.auth.id == caption.author_id);
                        var isUnread = (unixTimeCreated > this.props.timeLastRead);
                        return isUnread && isDescendentComment && !isAuthorComment;
                    }, this));

                    _.each(filtered_captions, function(cap_id) {
                        contextids[cap_id] = true;
                    });
                }
                children.push(React.createElement(Imgur.CommentTree,{
                    contextids: contextids,
                    childmap: this.props.childmap,
                    votemap: this.props.votemap,
                    image: this.props.image,
                    key: 'tree:' + this.props.image.hash,
                    expandMode: this.state.expandMode,
                    expandedFromPermalink: this.state.expandedFromPermalink,
                    groupingBadReplies: false,
                    comment: root_comment,
                    votable: true,
                    touchEnabled: this.state.touchEnabled,
                }));
            } else {
                // for recommendations experiment (expT4377)
                var bucket = Imgur.Experiments.getUserBucket('expT4377');
                var variation = bucket && bucket.variation;
                Imgur.Experiments.isActive('expT4377');

                children.push(React.createElement(Imgur.CommentList,{
                    contextids: {}, // ignore for now
                    childmap: this.props.childmap,
                    votemap: this.props.votemap,
                    image: this.props.image,
                    key: 'list:' + this.props.image.hash,
                    expandMode: this.state.expandMode,
                    expandedFromPermalink: this.state.expandedFromPermalink,
                    level: 0,
                    parent_id: null,
                    groupingBadReplies: true,
                    votable: true,
                    touchEnabled: this.state.touchEnabled,
                    // for recommendations experiment (expT4377)
                    inRecommendationsExperiment: variation != 'control' && Imgur.Experiments.isActive('expT4377')
                }));
            }
        }

        return React.DOM.div({id: 'comments', className: (!this.props.loading ? 'comments-loaded' : '')},
            React.DOM.div({id: 'captions', className: 'root-caption-container'},
                children
            )
        );
    }
});
