(function(){
    function PromotedPost(emitter) {
        this.emitter = emitter;
        this.parts = {
            companion: null,
            page: null,
            thumb: null,
            data: {}
        };
        this.pixels = {};
    };

    PromotedPost.prototype = {
        addPixel: function(purpose, url) {
            if('object' !== typeof this.pixels[purpose]) {
                this.pixels[purpose] = [];
            }

            this.pixels[purpose].push(url);
        },

        addCompanionPlacement: function(data) {
            this.parts.companion = _.extend({}, data);
        },

        addPagePlacement: function(data) {
            this.parts.page = _.extend({}, data);
        },

        addThumbPlacement: function(data) {
            this.parts.thumb = _.extend({}, data);
        },

        getHash: function() {
            return Imgur.Util.dive(this.parts, 'data.hash');
        },

        setData: function(data) {
            this.parts.data = _.extend(this.parts.data, data);
            this.parts.data.starting_score = 0;
        }
    };

    // alias it out
    Imgur.Promoted.Post = PromotedPost;
})();